import _merge from 'lodash.merge'

import setPageLoadType from '../../data/pageLoadType'

import type { ITrackerContent, IPushPayload, ITrackerExtendDataPayload } from '../../types/plugin'

export default function trackerDataMerge(trackerBuilder: ITrackerContent['builder'], data: ITrackerExtendDataPayload, extendData: ITrackerExtendDataPayload, payload: IPushPayload) {
  // 业务自定义埋点数据 payload 与全局 extendData merge 处理
  data = _merge({}, extendData, payload)
  // 业务埋点 event 字段中 pageLoadType 字段赋值
  data = setPageLoadType(data, trackerBuilder)
  return data
}
