/* eslint camelcase: "warn" */

import type {
  INeedPbToPaveData, IFrontPb, IXrayData, IBaseData,
} from '../types/helps/pbToPave'

const needPbToPaveMetric = ['BrowserResourceError']

export function needPbToPave(data: INeedPbToPaveData) {
  if (data?.browserApmTracker) {
    const metric = data?.browserApmTracker?.value
    const metricKeys = Object.keys(metric)
    for (let i = 0; i < metricKeys?.length; i++) {
      const metricData = metric[metricKeys[i]]
      const { type: metricType } = metricData
      if (needPbToPaveMetric.includes(metricType)) {
        return true
      }
    }
  }
  return false
}

function initialToLower(word: string) {
  return word.charAt(0).toLowerCase() + word.slice(1)
}

function transformPb(metric: {
  [attr: string]: {
    type: string
    value: {
      [attr: string]: unknown
    }
  }
}, extra: IBaseData) {
  const data: IXrayData[] = []
  Object.keys(metric).forEach(key => {
    const metricData = metric[key]
    if (metricData) {
      const { type: metricType, value: metricValue } = metricData
      if (Array.isArray(metricValue)) {
        metricValue?.forEach((item: { [attr: string]: {
          [props: string]: string | number | object
        } }) => {
          data.push({
            ...extra,
            measurement_name: initialToLower(metricType),
            measurement_data: item,
          })
        })
      } else {
        data.push({
          ...extra,
          measurement_name: initialToLower(metricType),
          measurement_data: metricValue,
        })
      }
    }
  })
  return data
}

export default function pbToPave(data: IFrontPb) {
  let xrayData: IXrayData[] = []
  const {
    app, mob, dvc, network, browser, user,
  } = data
  const {
    artifactName, artifactVersion, appVersion, platform, buildId,
  } = app?.value || {}
  const { value: userValue } = user || {}
  const { value: mobValue } = mob || {}
  const { value: dvcValue } = dvc || {}
  const { value: networkValue } = network || {}
  const { value: browserValue } = browser || {}

  const baseData = {
    clientTime: Date.now(),
    context_package: buildId || '',
    context_appVersion: appVersion,
    context_artifactName: artifactName,
    context_artifactVersion: artifactVersion,
    context_userId: userValue?.userId || '',
    context_deviceId: dvcValue?.dvceId || '',
    context_route: browserValue?.route || '',
    context_matchedPath: browserValue?.matchedPath || '',
    context_deviceModel: mobValue?.dvceModel || '',
    context_osVersion: mobValue?.osVersion || '',
    context_userAgent: browserValue?.userAgent || '',
    context_networkType: networkValue?.networkType?.value || '',
    context_nameTracker: 'wapT',
    context_platform: platform?.value,
  }

  // 旧点位
  if (data.browserApmTracker) {
    xrayData = transformPb(data.browserApmTracker.value, baseData)
  } else if (data.frontApmTracker) {
    // 自定义点位
    const { value: frontValue } = data.frontApmTracker
    xrayData = transformPb(frontValue, baseData)
  }

  return xrayData
}
