/**
 * 弹框
 */
export function showToast(message, config = {}) {
  const spec = {
    argsT: T.string.isRequired,
  }

  checkArgs(message, spec.argsT, 'showToast')

  if (message === undefined || message === null) {
    throw new BridgeError('ERR_ARGS_NULL', 'message should not be undefined or null')
  }

  return adapter('toast', { message, ...config })
}

export function showAlert(data) {
  const bridgeName = 'showalertV2'
  const spec = {
    argsT: T.shape({
      title: T.string,
      desc: T.string,
      actions: T.arrayOf(T.shape({
        name: T.string,
        callback: T.func,
      })),
    }),
  }

  checkArgs(data, spec.argsT, 'showAlert')

  // BUGFIX: Android use 'des' as 'desc'
  const cloneData = { ...data, des: data.desc || '' }
  const formattedActions = []
  const ACTION_STYPE_MAP_IOS = {
    default: 0,
    cancel: 1,
    destructive: 2,
  }

  cloneData.actions.forEach(action => {
    const callbackName = callbacks.add(bridgeName, action.callback)

    formattedActions.push({
      name: action.name,
      type: ACTION_STYPE_MAP_IOS[action.style], // iOS only
      script: `window.${callbackName} && window.${callbackName}`, // native 执行时候会往后面再添加一个 ‘()’
    })
  })
  cloneData.actions = formattedActions

  return adapter(bridgeName, cloneData)
}

export function showActionSheet(data) {
  const spec = {
    argsT: T.shape({
      title: T.string,
      desc: T.string,
      actions: T.arrayOf(T.shape({
        name: T.string,
        value: T.string.isRequired,
      })),
    }),
    resT: T.shape({
      result: resultType,
      value: T.string,
    }),
  }

  return adapter('showActionSheet', data, spec)
}

