export function setStatusBarTextColor(color) {
  const colorMap = {
    white: 0,
    black: 1,
  }

  const newColor = colorMap[color]

  if (commonChecks.isIOS) {
    return adapter('setStatusBarStyleIOS', newColor)
  }

  return adapter('setStatusBarTextColor', newColor)
}

