export function shareContent(data) {
  const spec = {
    argsT: T.shape({
      type: T.string.isRequired,
      contentType: T.oneOf(['link', 'image', 'text']),
      title: T.string,
      content: T.string,
      linkurl: urlType,
      imageurl: urlType,
      base64string: T.string,
      extension: T.shape({
        miniprogram: T.shape({
          title: T.string,
          desc: T.string,
          webpageurl: urlType,
          path: T.string,
          thumb: T.string,
          username: T.string,
        }),
        friend: T.oneOf([T.string, {
          source: T.string,
          type: T.oneOf([
            'general',
            'goodsDetail',
            'center',
            'topic',
            'event',
          ]),
          title: T.string,
          content: T.string,
          cover: T.string,
          image: T.string,
          link: T.string,
          btn: T.string,
          avatar: T.string,
          brandName: T.string,
          id: T.string,
          desc: T.string,
          price: T.number,
        }]),
      }),
    }),
    resT: T.shape({
      result: resultType,
    }),
  }

  const newData = { ...data }

  if (newData.type && !canShareMp) {
    newData.type = data.type.replace(/WXMiniProgram/, 'WeixinSession')
  }

  if (newData.image && !newData.imageurl) {
    warnDeprecated('args.image', 'args.imageurl')

    newData.imageurl = newData.image
  }

  // handle friend data
  const friend = arrangeExtensionFriend(newData.extension?.friend)

  if (friend) {
    newData.extension.friend = friend
  }

  return adapter('shareContentV2', newData, spec)
}

