export async function openXhsSystemSettings() {
  try {
    const res = await invoke('openXhsSystemSettings')
    const { result } = res || {}
    if (result === 0) {
      return res
    }
    return adapter('openXhsSystemSettings')
  } catch (error) {
    return adapter('openXhsSystemSettings')
  }
}

