import { inject } from 'vue'  // eslint-disable-line
import AuthStores, { defaultAuthStateGenerator } from './stores'
import permissionModule from './permission'
import serviceModule from './service'
import localStorageModule from './localStorage'
import sidModule from './sid'
import internalSSOModule from './internalSSO'
import apiFallbackModule from './apiFallback'
import redpassModule from './redpass'
import handlePostLogin from './sid/handlePostLogin'

const LAUNCHER_USE_KEY = 'LAUNCHER_USE_auth'

const needImplement = name => {
  const notify = () => {
    throw new Error(`${name} is not specific`)
  }

  notify.needImplement = true // a flag so that sub modules can know the service is not implemented
  return notify
}

const Auth = {
  name: 'auth',
  install(app, Launcher, options = {}) {
    // Server side not implement auth module yet
    if (!Launcher.isBrowser) {
      return
    }

    let scheme

    if (typeof options.scheme === 'string') {
      scheme = {
        type: options.scheme,
      }
    } else if (typeof options.scheme === 'object') {
      scheme = options.scheme
    }

    const authServices = {
      login: needImplement('login'),
      logout: needImplement('logout'),
      fetchUserInfo: needImplement('fetchUserInfo'),

      getAuthToken: needImplement('getAuthToken'),
      setAuthToken: needImplement('setAuthToken'),
      resetAuthToken: needImplement('resetAuthToken'),
    }

    // Register globalProperties.$auth
    app.vm.config.globalProperties.$auth = {
      services: authServices,
    }

    app.vm.provide(LAUNCHER_USE_KEY, app.vm.config.globalProperties.$auth)

    app.auth = app.vm.config.globalProperties.$auth

    redpassModule.call(app, Launcher, options)

    // If use localStorage
    if (scheme && scheme.type === 'localStorage') {
      // which will mix getAuthToken, setAuthToken, resetAuthToken into $auth context,
      //   and add noAuth method to http module
      localStorageModule.call(app, Launcher, options, scheme)
    }

    // If use sid
    if (scheme && scheme.type === 'sid') {
      sidModule.call(app, Launcher, options, scheme)
    }

    if (scheme && scheme.type === 'internalSSO') {
      if (!scheme.subsystemAlias) {
        throw new Error('[Launcher auth plugin Exception] `subsystemAlias` field is necessary when `schema.type` is `internalSSO`')
      }
      internalSSOModule.call(app, Launcher, options, scheme)
    }

    if (options.apiFallback) {
      // Install apiFallback submodule,
      // which will add a interceptors to handle if api response 401 or 403 status
      apiFallbackModule.call(app, Launcher, options, options.apiFallback)
    }

    // 后置登录逻辑，用户显示指定才开启
    if (options.handlePostLogin) {
      handlePostLogin(options.handlePostLogin, authServices.checkLoginWithAction)
    }
  },
  create(app, Launcher, options) {
    // Server side do not implement auth services yet
    if (!process.env.BROWSER) {
      return
    }

    if (options.services || options.permission || options.permissionFactory) {
      if (!app.store || !app.store.registerModule) {
        throw new Error(`[Launcher auth plugin Exception] \`store\` plugin is necessary for auth plugin:
Try using
\`\`\`
import storePlugin from '@xhs/launcher-plugin-store'
app.use(storePlugin, ops)
\`\`\``)
      }

      // Register in store
      if (AuthStores.registered) {
        AuthStores.modules.state = defaultAuthStateGenerator()
        AuthStores.registered = false
      }
      app.store.registerModule('Auth', AuthStores.modules)
      AuthStores.registered = true

      // Install authService submodule,
      // which will mix login / logout / fetchUserInfo into $auth context, if user implemented,
      // since the module depend on some services those may be defined
      // on other modules (localStorage)
      // make sure it may be called only before permissionModule
      serviceModule.call(app, Launcher, options)

      // Install authPermisstion submodule,
      // which will mix permission, checkPermission, permissionState into $auth context
      // permission module depend on all of other modules, it should be called at the ned
      permissionModule.call(app, Launcher, options)
    }
  },
}

export default Auth

export const useAuth = () => inject(LAUNCHER_USE_KEY)
