// Migrate from @xhs/cube-net-type
import { getNetType } from '@xhs/ozone-detector'
import { getNetworkType } from '@xhs/ozone-bridge'

interface INetTypeMap {
  wifi: string
  '4g': string
  '3g': string
  '3g+': string
  '2g': string
  none: string
  other: string
}

function resolveNetType(): Promise<string> {
  return new Promise(resolve => {
    getNetworkType().then(({ result, value }) => {
      if (result === 0) {
        resolve(value.toLowerCase())
      } else {
        resolve('unknown')
      }
    }).catch(() => {
      try {
        const ua = window.navigator.userAgent

        resolve(getNetType(ua).toLowerCase())
      } catch (ex) {
        resolve('unknown')
      }
    })
  })
}

const netTypeMap = {
  wifi: 'wifi',
  '4g': 'mobile',
  '3g': 'mobile',
  '3g+': 'mobile',
  '2g': 'mobile',
  none: 'unknow',
  other: 'unknow',
}

const getNetInfo = resolveNetType().then(type => ({
  network: {
    type: 'Network',
    value: {
      networkType: {
        type: 'NetworkType',
        value: netTypeMap[type as keyof INetTypeMap] || 'unknow',
      },
    },
  },
}))

export const getNetInfoV2 = resolveNetType().then(type => ({
  context_networkType: netTypeMap[type as keyof INetTypeMap] || 'unknow',
}))

export default getNetInfo
