import { DEFAULT_RESULT } from './commonChecks.js'
import { getOS, iphoneXCheckNode, iphone14ProCheckNode } from './os.js'
import { getBrowser, getBrowserVersion } from './browser.js'
import { extractFromQuery } from './query.js'

/*
 * in node,
 * we don't have DOM window,
 * we rely on strings passed in, which contains request url and ua, packec in to a dict.
 * req as:
 * {
 *  url: 'http://www.xiaohongshu.com/discovery/item/55d4087cd6e4a95334e72296',
 *  userAgent: 'Mozilla/5.0 (Macintosh; ...',
 * }
 */

export function commonChecksInNode({ userAgent = '', url = '' }) {
  const os = getOS(userAgent)
  const browser = getBrowser(userAgent)
  const checkIPhoneX = iphoneXCheckNode({ os, userAgent })
  const isIOS = os === 'iOS'
  const isAndroid = os === 'Android'
  const isMobile = (userAgent || '').toLowerCase().indexOf('mobile') > -1

  // extract info from query
  const envFromQuery = extractFromQuery(url.split('?')[1] || '')

  const isPC = !envFromQuery.isXHS && !isAndroid && !isIOS && !isMobile

  const result = {
    ...DEFAULT_RESULT,
    isIOS,
    isAndroid,
    isWeixin: browser === 'micromessenger',
    isAlipay: browser === 'alipay',
    isWeibo: browser === 'weibo',
    isMiniprogram: false,
    isBaiduMiniprogram: false,
    isQQMiniprogram: false,
    isAlipayMiniprogram: false,
    isToutiaoMiniprogram: false,
    isIphone14Pro: iphone14ProCheckNode({ os, userAgent }),
    isIphoneX: checkIPhoneX.isIphoneX,
    iphoneXType: checkIPhoneX.iphoneXType,
    screenScale: checkIPhoneX.screenScale,
    screenHeight: checkIPhoneX.screenHeight,
    screenWidth: checkIPhoneX.screenWidth,
    themeType: 'light',
    isPC,
    isMobile,
  }

  if (envFromQuery.isXHS) {
    result.isXHS = envFromQuery.isXHS
  } else if (userAgent.toLowerCase().indexOf('discover') > -1) {
    result.isXHS = true
  }

  if (envFromQuery.isMiniprogram) {
    result.isMiniprogram = envFromQuery.isMiniprogram
  }

  if (result.isXHS) {
    const appVersion = getBrowserVersion(userAgent)
    const isSupportVersion = appVersion.major > 6
      || (appVersion.major === 6 && appVersion.minor >= 7)

    result.isFullscreen = (isSupportVersion && envFromQuery.isFullscreen)
      || (result.isIOS && envFromQuery.isNaviHidden)
  }

  if (envFromQuery.isThemeDark) {
    result.themeType = 'dark'
  }

  if (result.isXHS) {
    if (result.isIOS) {
      result.safeAreaBottom = 34
      result.safeAreaTop = result.isIphoneX ? 88 : 64
    } else {
      result.safeAreaTop = 44
    }
  }

  return result
}
