import { getOS, iphoneXCheck, iphone14ProCheck } from './os.js'
import { getBrowser, getBrowserVersion } from './browser.js'
import { MP_TYPES, getMiniprogramType } from './miniProgram.js'
import { extractFromQuery } from './query.js'

export const DEFAULT_RESULT = {
  isXHS: false,
  isIOS: false,
  isAndroid: false,
  isWeixin: false,
  isAlipay: false,
  isWeibo: false,
  isQQ: false,
  isQQBrowser: false,
  isMiniprogram: false,
  isBaiduMiniprogram: false,
  isQQMiniprogram: false,
  isAlipayMiniprogram: false,
  isToutiaoMiniprogram: false,
  isIphoneX: false,
  isFullscreen: false,
  isTop: false,
  isUniik: false,
  isSpark: false,
  isXhsMerchant: false,
  isSnowPeak: false,
  isInternation: false,
  isCatalog: false,
  isOdyssey: false,
  isPC: false,
}

export function commonChecks(win) {
  const { navigator, location } = win
  const { userAgent } = navigator

  const os = getOS(userAgent)
  const browser = getBrowser(userAgent)
  const miniProgramType = getMiniprogramType(userAgent, win)
  const checkIPhoneX = iphoneXCheck(os)

  const envFromQuery = extractFromQuery(location.search)

  const isIOS = os === 'iOS'
  const isAndroid = os === 'Android'

  const hasXhsBridgeForAndroid = !!window.xhsbridge
  const hasXhsBridgeForIOS = !!window.webkit?.messageHandlers?.getDeviceInfo
  const isXHS = hasXhsBridgeForAndroid || hasXhsBridgeForIOS || browser === 'discover' || envFromQuery.isXHS
  const isTop = browser === 'redtop'
  const isUniik = browser === 'uniik'
  const isSpark = browser === 'spark'
  const isXhsMerchant = browser === 'merchant'
  const isSnowPeak = browser === 'snowpeak'
  const isInternation = browser === 'internation'
  const isCatalog = browser === 'catalog'
  const isOdyssey = browser === 'odyssey'
  const isMobile = (userAgent || '').toLowerCase().indexOf('mobile') > -1
  const isPC = !isXHS && !isAndroid && !isIOS && !isMobile

  let isFullscreen = false

  if (isXHS || isTop) {
    const appVersion = getBrowserVersion(userAgent)
    const isSupportVersion = appVersion.major > 6
      || (appVersion.major === 6 && appVersion.minor >= 7)

    isFullscreen = (isSupportVersion && envFromQuery.isFullscreen)
      || (isIOS && envFromQuery.isNaviHidden)
      || (isTop && envFromQuery.isFullscreen) // 友友视频有 fullscreen 参数即可
  }

  return {
    ...DEFAULT_RESULT,
    isIOS,
    isAndroid,
    isXHS,
    isFullscreen,
    isWeixin: browser === 'micromessenger',
    isAlipay: browser === 'alipay',
    isWeibo: browser === 'weibo',
    isQQ: browser === 'qq',
    isQQBrowser: browser === 'mqqbrowser',
    isMiniprogram: miniProgramType === MP_TYPES.weixin || envFromQuery.isMiniprogram,
    isBaiduMiniprogram: miniProgramType === MP_TYPES.baidu,
    isQQMiniprogram: miniProgramType === MP_TYPES.qq,
    isAlipayMiniprogram: miniProgramType === MP_TYPES.alipay,
    isToutiaoMiniprogram: miniProgramType === MP_TYPES.toutiao,
    isIphone14Pro: iphone14ProCheck(os),
    isIphoneX: checkIPhoneX.isIphoneX,
    iphoneXType: checkIPhoneX.iphoneXType,
    isTop,
    isUniik,
    isSpark,
    isXhsMerchant,
    isSnowPeak,
    isInternation,
    isCatalog,
    isOdyssey,
    isPC,
    isMobile,
  }
}
