import type { ITrackerContent, ITrackerExtendDataPayload } from '../types/plugin'
// 业务埋点 event 字段中 pageLoadType 字段赋值
const pageInstanceViewLog: {
  [props: string]: boolean
} = {}
export default function setPageLoadType(data: ITrackerExtendDataPayload, trackerBuilder: ITrackerContent['builder']) {
  if (!trackerBuilder || !trackerBuilder.PageLoadType) {
    return data
  }
  const { PAGE_LOAD_TYPE_NORMAL_JUMP, PAGE_LOAD_TYPE_REGRESSION } = trackerBuilder.PageLoadType
  if (PAGE_LOAD_TYPE_NORMAL_JUMP && PAGE_LOAD_TYPE_REGRESSION && data?.event?.value?.action?.value === 'pageview') {
    const pageInstance = data?.page?.value?.pageInstance?.value as string
    if (pageInstanceViewLog[pageInstance]) {
      data.event.value.pageLoadType = PAGE_LOAD_TYPE_REGRESSION
    } else {
      pageInstanceViewLog[pageInstance] = true
      data.event.value.pageLoadType = PAGE_LOAD_TYPE_NORMAL_JUMP
    }
  }
  return data
}
