import { http } from '@xhs/launcher'
import { emitTrack, emitApmTrack } from '@xhs/ozone-bridge'
import { getFlagValue } from '@xhs/abtest'

import type { ITrackerExtendDataPayload, IApmXrayTrackerExtendDataPayload, ITrackerExtendDataPayloadHttpReturn } from '../types/plugin'
import type { ISendByHttpReturn } from '../types/emitter/client'

export function sendToXray(endpoint: string, payload: IApmXrayTrackerExtendDataPayload | IApmXrayTrackerExtendDataPayload[]) {
  // @ts-ignore 待 http 增加声明
  return http.axios.post(endpoint, payload, {
    headers: {
      'Biz-Type': 'apm_fe',
      Batch: 'true',
    },
    withCredentials: false,
  }).catch((err: unknown) => {
    if (process.env.BUILD_ENV !== 'production') {
      console.error(err)
    }
  })
}

export function sendByHttp(endpoint: string, payload: IApmXrayTrackerExtendDataPayload[] | ITrackerExtendDataPayload | string) {
  // @ts-ignore 待 http  增加声明
  return http.axios.post(endpoint, payload, {
    headers: {
      'Content-Type': 'text/plain',
    },
    withCredentials: false,
  }).catch((err: unknown) => {
    if (process.env.BUILD_ENV !== 'production') {
      console.error(err)
    }
  }) as Promise<ISendByHttpReturn<IApmXrayTrackerExtendDataPayload[] | ITrackerExtendDataPayloadHttpReturn | string>>
}

async function sendByNative(endpoint: string, payload: ITrackerExtendDataPayload | string, apm?: boolean) {
  try {
    if (apm) {
      await emitApmTrack(payload as string)
    } else {
      await emitTrack(payload as string)
    }
  } catch (error: unknown) {
    return sendByHttp(endpoint, payload)
  }
}

export async function sendByDefault(endpoint: string, payload: ITrackerExtendDataPayload | string, {
  preferNative,
  apm = false,
}: {
  preferNative?: boolean | ((params: (flagName: string | string[]) => Promise<unknown>) => Promise<boolean>) | Promise<boolean>
  apm?: boolean
}) {
  let byNative = preferNative

  if (typeof preferNative === 'function') {
    byNative = await preferNative(getFlagValue)
  }

  if (byNative === true) {
    return sendByNative(endpoint, payload, apm)
  }
  return sendByHttp(endpoint, payload)
}
