import PerfTiming from '../../timing'
import { toDict, getTimeFromMarks } from '../../helpers'

// 内存消耗
function getMemory() {
  const payload = toDict(window.performance.memory)

  return {
    jsHeapSizeLimit: payload.jsHeapSizeLimit,
    totalJsHeapSize: payload.totalJSHeapSize,
    usedJsHeapSize: payload.usedJSHeapSize,
  }
}

// 网络状态
function getNetwork() {
  const payload = toDict(window.navigator.connection)

  if (typeof payload.onchange !== 'undefined') {
    delete payload.onchange
  }

  return {
    downlink: payload.downlink,
    effectiveType: payload.effectiveType,
    rtt: payload.rtt,
    saveData: payload.saveData,
    type: payload.type,
  }
}

// 自定义渲染时间
function getRenderTimes() {
  const marks = window.performance.getEntriesByType('mark')
  const firstRenderStart = getTimeFromMarks(marks, 'firstRenderStart')
  const clientRenderStart = getTimeFromMarks(marks, 'clientRenderStart')

  return { firstRenderStart, clientRenderStart }
}

// 脚本执行时间
function getScriptsExecutionTiming(timing = window.__SCRIPTS_EXECUTION_TIMING__) {  // eslint-disable-line
  const entries = timing || []
  const payload = entries.map((entry = {}) => {
    const newEntry = { ...entry }

    if (newEntry.endTime && newEntry.startTime) {
      newEntry.duration = newEntry.endTime - newEntry.startTime
      delete newEntry.endTime
    } else {
      newEntry.duration = 0
    }

    return newEntry
  })

  return payload
}

// 兜底方案：资源加载失败
// 目的：在eaglet加载失败或window未挂载eaglet对象时，出现资源加载报错的情况，此时报错会暂存在local中，
// 该方案会在eaglet plugin加载完毕后，从local中读取暂存的报错，并在onload事件结束后执行
function getResourceError(resourceKey = '__APM__ResourceErrorTracker__') {
  const metrics = JSON.parse(localStorage.getItem(resourceKey))
  if (metrics?.length) {
      const filterItems = []
      const remainItems = []
      for (let i = 0; i < metrics.length; i++) {
        if (metrics[i].projectName === process.env.APP_NAME) {
          const { projectName, ...rest } = metrics[i]
          filterItems.push(rest)
        } else if (metrics[i].projectName) {
          // 兼容旧版本projectName没有的情况，需要过滤掉
          remainItems.push(metrics[i])
        }
      }
      localStorage.setItem(resourceKey, JSON.stringify(remainItems))
      return filterItems
  }
  return []
}

class EnhancedMetrics extends PerfTiming {
  timing() {
    return {
      memory: getMemory(),
      network: getNetwork(),
      renderTimes: getRenderTimes(),
      scriptsExecutionTiming: getScriptsExecutionTiming(),
      resourceError: getResourceError()
    }
  }
}

export default new EnhancedMetrics()
