import { getHost } from './utils'

const REGISTER_CANVAS_PATH = '/fe_api/burdock/v2/shield/registerCanvas'

let fpPromise = null

function setCanvas(resolve, reject, http, callFrom) {
  return window.xhsFingerprint.getV18(async (origin, map, params) => {
    const data = {
      sign: params,
      id: origin,
    }

    try {
      await http.post(REGISTER_CANVAS_PATH, data, {
        params: {
          p: 'cc',
          callFrom,
        },
        baseURL: getHost(),
        withCredentials: true,
      }).then(value => {
        resolve(value)
      })
    } catch (e) {
      reject(e)
    }
  })

}

function beforeRecallHandle(http, callFrom) {
  if (!fpPromise) {
    fpPromise = new Promise((resolve, reject) => {
      if (window.xhsFingerprint) {
        setCanvas(resolve, reject, http, callFrom)
      } else {
        const fpScript = document.createElement('script')
        fpScript.src = '//fe-video-qc.xhscdn.com/fe-platform/565a6c0720d87794072b272492de312ec8143fb7.js' // 混淆
        fpScript.type = 'text/javascript'
        fpScript.onload = () => {
          setCanvas(resolve, reject, http, callFrom)
        }
        document.body.appendChild(fpScript)
      }
    })
  }
  return fpPromise
}

// eslint-disable-next-line consistent-return
export async function tryGenDeviceFingerprint(http, config, response) {
  const {
    callFrom,
  } = config || {}
  const { config: httpConfig } = response

  await beforeRecallHandle(http, callFrom)

  const res = await http.axios(httpConfig)
  return res
}
