class PaintTiming {
  static _calculate(observedPoints = []) {
    if (observedPoints.length === 0) {
      return null
    }

    if (observedPoints.length === 1) {
      return observedPoints[0].t
    }

    let maxPoint = observedPoints[0]

    for (let i = 1; i < observedPoints.length; i++) {
      const point = observedPoints[i]

      if (point.layoutSignificance > maxPoint.layoutSignificance) {
        maxPoint = point
      }
    }

    return maxPoint.t
  }

  static get() {
    return new Promise(resolve => {
      if (window.__FULLY_LOADED__ && window.__FMP_OBSERVED_POINTS__) {
        resolve({
          firstPaint: window.__FP__,
          firstContentfulPaint: window.__FCP__,
          firstScreen: window.__FIRST_SCREEN__,
          firstMeaningfulPaint: PaintTiming._calculate(window.__FMP_OBSERVED_POINTS__),
          fullyLoaded: window.__FULLY_LOADED__,
        })

        return
      }

      window.addEventListener('__fullyloaded__', e => {
        resolve({
          firstPaint: e.detail.firstPaint,
          firstContentfulPaint: e.detail.firstContentfulPaint,
          firstScreen: window.__FIRST_SCREEN__,
          firstMeaningfulPaint: PaintTiming._calculate(e.detail.observedPoints),
          fullyLoaded: e.detail.fullyLoaded,
        })
      })
    })
  }
}

export default PaintTiming
