import { warn } from '@xhs/logger'

function forEach(obj, handle) {
  for (const key in obj) {
    if (obj.hasOwnProperty(key)) {
      handle(obj[key], key)
    }
  }
}

function makeError(msg) {
  const errorMsg = `[Launcher Auth Exception] ${msg}, please check your auth.config.js`

  return new Error(errorMsg)
}

/* ------------------------------------------------- */

function dispatchRegisterPermissionToStore(store, permissionNames) {
  store.dispatch('registerAuthPermission', permissionNames)
}

function dispatchPermissionStateToStore(store, permissionName, state, res) {
  store.dispatch('setAuthPermissionState', {
    key: permissionName,
    value: state,
  })
  store.dispatch('setAuthPermissionRes', {
    key: permissionName,
    value: res,
  })
}

function dispatchResetPermissionToStore(store) {
  store.dispatch('resetAuthPermission')
}

function getPermissionStateFromStore(store, permissionName) {
  const state = store.getters.authPermissionState[permissionName]
  const res = store.getters.authPermissionRes[permissionName]

  return {
    state,
    res,
  }
}

/* ------------------------------------------------- */

const PERMISSION_DIVIDER = '/'

/* ------------------------------------------------- */

class Permission {
  constructor({ store, permission, permissionFactory }) {
    this.store = store
    this.permissionFactory = permissionFactory || {}
    this.permission = {}
    this.throttle = {}

    this.events = {}

    forEach(permission, (val, key) => this.push(key, val))
  }

  has(permissionName) {
    return Boolean(this.permission[permissionName])
  }

  getFactory() {
    return this.permissionFactory
  }

  getList() {
    return this.permission
  }

  /* ------------------------------------------------- */

  on(eventName, func) {
    if (!this.events[eventName]) {
      this.events[eventName] = []
    }
    this.events[eventName].push(func)
  }

  emit(eventName, payloadArgs) {
    if (this.events[eventName]) {
      this.events[eventName].forEach(fn => fn(...payloadArgs))
    }
  }

  /* ------------------------------------------------- */

  // register new permission
  push(permissionName, permissionFn) {
    if (typeof permissionName !== 'string' || !permissionName) {
      throw makeError(`auth func must be string, must now ${typeof permissionName}`)
    }

    if (typeof permissionFn !== 'function') {
      throw makeError(`${permissionName} permission is not a function`)
    }

    if (this.permission[permissionName]) {
      warn(
        `[Launcher Auth Warning] duplicate permission registration ${permissionName}`,
      )
    }

    this.permission[permissionName] = permissionFn

    dispatchRegisterPermissionToStore(this.store, permissionName)
    this.emit('push', [permissionName, permissionFn])
  }

  /* ------------------------------------------------- */

  // to do the strict check during factoring new permission
  checkFactoryArgsAvailable(factoryName, factoryArgs) {
    const factoryFn = this.permissionFactory[factoryName]

    if (typeof factoryFn !== 'function') {
      throw makeError(`permission factory ${factoryName} is not a function`)
    }

    if (factoryArgs.length !== factoryFn.length) {
      throw makeError(`args for permission factory ${factoryName} is not matched, except: ${factoryFn.length}, but now: ${factoryArgs.join(',')}`)
    }

    factoryArgs.forEach(arg => {
      if (typeof arg === 'object') {
        throw makeError(`found object arg in permission factory ${factoryName} call, only supports plain type now`)
      }
    })

    return true
  }

  // make the permission name, like '${factoryName}/{arg1}/${arg2}'
  // , once call $auth.canAccess('pageA', buttonB)
  // , will create the new perssion as 'canAccess/pageA/buttonB'
  makePermissionNameUnderFactory(factoryName, factoryArgs) {
    this.checkFactoryArgsAvailable(factoryName, factoryArgs)

    return [factoryName, ...factoryArgs].join(PERMISSION_DIVIDER)
  }

  // the new permission name will store in store, once another call, revert it
  revertPermissionNameForFactory(permissionName) {
    const splited = permissionName.split(PERMISSION_DIVIDER)
    const factoryName = splited[0]
    const factoryArgs = splited.slice(1)

    return {
      factoryName,
      factoryArgs,
    }
  }

  /* ------------------------------------------------- */

  // create the new perssion via factory, permission name may like  '${factoryName}/{arg1}/${arg2}'
  create(permissionName) {
    const { factoryName, factoryArgs } = this.revertPermissionNameForFactory(permissionName)

    this.checkFactoryArgsAvailable(factoryName, factoryArgs)

    const factoryFn = this.permissionFactory[factoryName]
    const permissionFn = factoryFn(...factoryArgs)

    // must to push to itself, and do the registration
    this.push(permissionName, permissionFn)

    return permissionFn
  }

  // get the permission, if the permission not defined, will try to create it via factory
  get(permissionName) {
    if (this.has(permissionName)) {
      return this.permission[permissionName]
    }
    return this.create(permissionName)
  }

  /* ------------------------------------------------- */

  // do the check
  check(permissionName, checkPayload) {
    if (typeof permissionName !== 'string') {
      throw makeError(`permissionName must be a string, but now ${typeof permissionName}`)
    }

    const store = this.store
    const permissionFn = this.get(permissionName)

    if (typeof permissionFn !== 'function') {
      throw makeError(`${permissionName} is not defined`)
    }

    const { state, res } = getPermissionStateFromStore(store, permissionName)

    let checkPromise

    if (state === null || state === undefined) {
      if (this.throttle[permissionName]) {
        checkPromise = this.throttle[permissionName]
      } else {
        const actualPermissionResult = permissionFn(checkPayload)

        if (!actualPermissionResult || !actualPermissionResult.then) {
          throw makeError(`${permissionName} returning a non-promise result`)
        }

        checkPromise = actualPermissionResult.then(res => {
          dispatchPermissionStateToStore(store, permissionName, true, res)
          return Promise.resolve(res)
        }, res => {
          dispatchPermissionStateToStore(store, permissionName, false, res)
          return Promise.reject(res)
        })
      }
    } else if (state) {
      checkPromise = Promise.resolve(res)
    } else {
      checkPromise = Promise.reject(res)
    }

    this.throttle[permissionName] = checkPromise

    return checkPromise
  }

  reset() {
    dispatchResetPermissionToStore(this.store)
    this.throttle = {}
  }
}

export default Permission
