<template>
  <span
    ref="eleContainer"
    class="reds-image-container"
    :class="classNames"
  >
    <div
      v-if="loadStatus === 'loading' && loadingAnimation"
      class="reds-image-container__loading"
    />
    <span
      class="reds-img-placeholder"
      :style="sizeStyle"
    />
    <picture
      class="reds-img-box"
    >
      <source
        v-if="srcWebp"
        :srcset="srcWebp"
        type="image/webp"
      >
      <img
        ref="refImg"
        class="reds-img"
        :src="(renderSrc || undefined)"
        :alt="alt"
        :data-load-status="loadStatus"
        :loading="loading"
        :class="[{animated}]"
        :width="width"
        :height="height"
        @error="handleError"
        @load="handleLoad"
      >
    </picture>
    <div
      v-if="$slots.default || $slots.loading || $slots.error"
      class="reds-img-box"
    >
      <slot
        v-if="loadStatus==='loading'"
        name="loading"
      />
      <slot
        v-if="loadStatus==='error'"
        name="error"
      />
      <slot :load-status="loadStatus" />
    </div>
  </span>
</template>

<script lang="ts">
  import {
    ref, computed, defineComponent, watch, onMounted,
  } from 'vue'

  import PropsDefine from './props'
  import useLazyload from './utils/useLazyload'
  import getSizeStyleByProps from './utils/getSizeStyleByProps'
  import getSrcWebpBySrc from './utils/getSrcWebpBySrc'
  import getRenderSrc from './utils/getRenderSrc'
  import '@xhs/reds-h5/style/image.styl'

  enum LOAD_STATUS {
    Loading = 'loading',
    Loaded = 'loaded',
    Error = 'error',
  }

  export default defineComponent({
    name: 'RedsImage',
    props: PropsDefine,
    emits: ['load', 'error'],
    setup(props, { emit }) {
      const showEagerImage = computed(() => !props.lazyLoad || props.loading === 'eager')
      // 图片加载状态
      const loadStatus = ref(showEagerImage.value ? LOAD_STATUS.Loaded : LOAD_STATUS.Loading)
      const { lazyload, eleContainer } = useLazyload(props.lazyLoad, props.loading, props.observerOptions)

      const isMounted = ref(false)
      const refImg = ref()

      // 图片尺寸
      const sizeStyle = computed(() => getSizeStyleByProps(props.width, props.height, props.aspectRatio, props.enableRem, isMounted.value))

      // image container类名
      const classNames = computed(() => [
        {
          responsive: props.aspectRatio,
        },
      ])

      const loadingProgress = ref(80)

      // props.src 改变重新设置状态
      watch(() => props.src, () => {
        loadStatus.value = showEagerImage.value ? LOAD_STATUS.Loaded : LOAD_STATUS.Loading
      })

      // 图片加载成功
      const handleLoad = (e?: Event) => {
        loadStatus.value = LOAD_STATUS.Loaded
        loadingProgress.value = 100
        emit('load', e)
      }

      // 图片加载失败
      const handleError = (e: Event) => {
        loadStatus.value = LOAD_STATUS.Error
        emit('error', e)
      }

      onMounted(() => {
        isMounted.value = true
        // SSR返回图片src，可能出现图片加载已经完成了，但是此时js脚本还没返回，所以在CSR阶段，根据image的complete属性，触发load事件
        // TOODO: 更精确的实现方式是使用SSR图片插件
        // 触发图片 load 事件
        if (refImg.value?.src && refImg.value?.complete) {
          handleLoad()
        }
      })

      const isPure = computed(() => {
        if (props.pure) {
          return true
        }
        if (!props.src) {
          return false
        }
        if (props.src.startsWith('data:image/')) {
          return true
        }
        const PURE_TYPE = ['.gif', '.svg']
        for (let i = 0; i < PURE_TYPE.length; i++) {
          if (props.src.includes(PURE_TYPE[i])) {
            return true
          }
        }
        return false
      })

      const renderSrc = computed(() => {
        // 不支持 lazyload 为 eager 直接显示
        if (props.loading === 'eager') {
          return isPure.value ? props.src : getRenderSrc(props.src, props.width, props.height, props.clipConfig, props.ratio)
        }
        // 1. 没有 Mounted 不显示
        // 2. 如果是 Lazyload 模式不显示
        if (!isMounted.value || lazyload.value) {
          return ''
        }
        return isPure.value ? props.src : getRenderSrc(props.src, props.width, props.height, props.clipConfig, props.ratio)
      })

      const srcWebp = computed(() => {
        // width和height必须同时传number，不然webp失效
        if (typeof props.width !== 'number' || typeof props.height !== 'number') {
          return ''
        }
        // 用户显示指定不需要webp
        if (isPure.value || props.webp === false) {
          return ''
        }
        // 不支持 lazyload 直接显示
        if (showEagerImage.value) {
          return getSrcWebpBySrc(renderSrc.value)
        }
        // 2. 没有 Mounted 不显示
        // 3. 如果是 Lazyload 模式不显示
        if (!isMounted.value || lazyload.value) {
          return ''
        }
        // 用户自定义了 webp 链接
        if (props.webp && props.webp !== true) {
          return props.webp
        }
        return getSrcWebpBySrc(renderSrc.value)
      })

      return {
        classNames,
        sizeStyle,
        handleLoad,
        handleError,
        loadStatus,
        isMounted,
        renderSrc,
        loadingProgress,
        eleContainer,
        srcWebp,
      }
    },
  })

</script>
