import { getUserInfo } from '@xhs/ozone-bridge'

const DEFAULT_AB_INFO = {
  userId: '',
  userToken: '',
  sessionId: '',
  hashExp: '',
  flags: {},
}

function getABInfoByBridge() {
  return getUserInfo({ extractValue: true })
    .then(userInfo => {
      const {
        userId,
        userToken,
        sessionId,
        hashExp,
        flags,
      } = userInfo

      if (userId) {
        DEFAULT_AB_INFO.userId = userId
      }

      if (userToken) {
        DEFAULT_AB_INFO.userToken = userToken
      }

      if (sessionId) {
        DEFAULT_AB_INFO.sessionId = sessionId
      }

      if (hashExp && typeof hashExp === 'string') {
        DEFAULT_AB_INFO.hashExp = hashExp
      }

      if (flags) {
        DEFAULT_AB_INFO.flags = flags
      }
      return DEFAULT_AB_INFO
    })
}

export default function getABInfo() {
  return getABInfoByBridge().catch(() => DEFAULT_AB_INFO)
}

export function getFlagValue(...flagNames) {
  return getABInfo().then(userInfo => {
    const { flags } = userInfo
    const output = []

    flagNames.forEach(flagName => {
      if (!flags.shequ && !flags.fulishe) {
        output.push(undefined)

        return
      }

      const shequFlag = flags.shequ[flagName]
      const fulisheFlag = flags.fulishe[flagName]

      if (shequFlag !== undefined) {
        output.push(shequFlag)
      } else if (fulisheFlag !== undefined) {
        output.push(fulisheFlag)
      } else {
        if (process.env.NODE_ENV === 'development') {
          console.warn(`This flag ${flagName} was not found, please check if this experiment is over`) // eslint-disable-line no-console
        }

        output.push(undefined)
      }
    })

    return output.length === 1 ? output[0] : output
  })
}
