import getEnvInfo from './env'
import getDeviceInfo, { getDeviceInfoV2 } from './device'
import getNetInfo, { getNetInfoV2 } from './net-type'

import cachedUserInfo, { purgeUserInfo } from './user'
import getPageInfo from './page'
import getBrowserInfo, { getBrowserInfoV2 } from './browser'

import type {
  ITrackerExtendDataPayload,
  IApmXrayTrackerExtendDataPayload,
  IPurgeBaseInfoConfig,
  IEagletOps,
} from '../../types/plugin'

const getMetaInfo = (config: IPurgeBaseInfoConfig = {}, options: IEagletOps = {}) => {
  const getUserInfo = options.getUserInfo ? options.getUserInfo() : (config.purgeUser ? purgeUserInfo() : cachedUserInfo()) // eslint-disable-line
  const artifactInfo = {
    artifactName: process.env.APP_NAME,
    artifactVersion: process.env.APP_VERSION,
  }
  const getArtifactInfo = options.getArtifactInfo ? { ...artifactInfo, ...options.getArtifactInfo() } : artifactInfo

  return Promise.all([
    getDeviceInfo(options),
    getNetInfo,
    getEnvInfo,
    getUserInfo,
    getPageInfo(config.route),
    getBrowserInfo(config.route),
    getArtifactInfo,
  ]).then(([dvcInfo, netType, envInfo, userInfo, pageInfo, browserInfo, artifactInfo]) => {
    const output: ITrackerExtendDataPayload = {
      ...dvcInfo,
      ...netType,
      ...pageInfo,
      ...browserInfo,
    }

    if (userInfo?.user) {
      output.user = userInfo.user
    }
    if (output.app) {
      output.app.value = {
        ...output.app.value,
        ...artifactInfo,
      }
      output.app.value.sessionId = envInfo.sessionId
    }
    return output
  })
}

export const getBaseInfo = (config: IPurgeBaseInfoConfig = {}, options: IEagletOps = {}) => {
  const getUserInfo = options.getUserInfo ? options.getUserInfo() : (config.purgeUser ? purgeUserInfo() : cachedUserInfo()) // eslint-disable-line
  const artifactInfo = {
    artifactName: process.env.APP_NAME,
    artifactVersion: process.env.APP_VERSION,
  }
  const getArtifactInfo = options.getArtifactInfo ? { ...artifactInfo, ...options.getArtifactInfo() } : artifactInfo

  return Promise.all([
    getDeviceInfoV2(options),
    getNetInfoV2,
    getUserInfo,
    getBrowserInfoV2(config.route),
    getArtifactInfo,
  ]).then(([dvcInfo, netType, userInfo, browserInfo, artifactInfo]) => {
    const output = {
      ...dvcInfo as object,
      ...netType,
      ...browserInfo,
    } as IApmXrayTrackerExtendDataPayload

    output.context_artifactName = artifactInfo.artifactName
    output.context_artifactVersion = artifactInfo.artifactVersion

    if (userInfo?.user) {
      output.context_userId = userInfo.user.value.userId
    }
    return output
  })
}

export default getMetaInfo
