import type { IConfigBlankScreenError, IEagletMethods, IMetrics } from '../types/plugin'

interface IPoint {
  x: number
  y: number
}

let flag = false

export function generateRandomCheckPoint(lines?: number, columns?: number) {
  lines = lines || 5
  columns = columns || 10
  const height = window.innerHeight
  const width = window.innerWidth
  const points: IPoint[] = []
  for (let i = 1; i <= lines; i++) {
    const y = (height / (lines + 1)) * (i)
    for (let j = 1; j <= columns; j++) {
      const x = (width / (columns + 1)) * (j)
      points.push({ x, y })
    }
  }
  return points
}

function calculateDomDepth(element: HTMLElement | null, maxDepth: number = 10, rootElementSelector?: string) {
  let depth = 0
  const rootElement = rootElementSelector ? document.querySelector(rootElementSelector) : null
  while (element && element.tagName !== 'BODY' && rootElement !== element && depth < maxDepth) {
    element = element.parentElement
    depth++
  }
  return depth
}

export function calculateBlankScreenRatio(config: IConfigBlankScreenError) {
  try {
    let blankPointCount = 0
    const domThreshold = config.domThreshold
    const points = generateRandomCheckPoint(config.lines, config.columns)
    points.forEach(point => {
      const elements = document.elementsFromPoint(point.x, point.y) as HTMLElement[] || []
      const element = elements[0]
      if (element && calculateDomDepth(element, config.maxDepth, config.rootElementSelector) < domThreshold) {
        blankPointCount++
      }
    })
    return blankPointCount / points.length
  } catch (err: unknown) {
    if (process.env.BUILD_ENV !== 'production') {
      console.error(err)
    }
  }
  return 0
}

function screenshot(eaglet: IEagletMethods, metrics: IMetrics) {
  const html2image = function () {
    if (window.html2canvas) {
      window.html2canvas(document.body, {
        scale: 360 / window.innerWidth,
      }).then((e: HTMLCanvasElement) => {
        metrics.measurement_data.img = e.toDataURL('image/jpeg', 0.2)
        eaglet.push(metrics, 'ApmXrayTracker')
      })
    }
  }

  if (window.html2canvas) return html2image()
  const ele = document.createElement('script')
  ele.src = 'https://fe-video-qc.xhscdn.com/fe-platform/abf6e0874371419fa6fffa7540610e2013588fe6/html2canvas.min.js'
  document.head.appendChild(ele)
  ele.onload = function () {
    html2image()
  }
}

export function checkBlankScreenError(eaglet: IEagletMethods, config: IConfigBlankScreenError) {
  try {
    if (!document.elementsFromPoint || typeof document.elementsFromPoint !== 'function') return

    if (flag) return
    flag = true
    setTimeout(() => {
      flag = false
    }, 10 * 1000)

    const blankScreenRatio = calculateBlankScreenRatio(config)
    if (process.env.BUILD_ENV !== 'production') {
      console.info(`[apm] blankScreenRatio: ${blankScreenRatio}`)
    }
    if (blankScreenRatio > (config.threshold || 0.7)) {
      const measurementData: IMetrics['measurement_data'] = {
        ratio: blankScreenRatio,
      }
      if (window.apm_blankScreen_error?.errorType) {
        measurementData.errorType = window.apm_blankScreen_error.errorType
        measurementData.content = window.apm_blankScreen_error.content
      } else if (!performance.timing.loadEventStart) {
        measurementData.errorType = 'performance'
      } else {
        measurementData.errorType = 'other'
      }
      window.apm_blankScreen_error = null

      const metrics = {
        measurement_name: 'blankScreenError',
        measurement_data: measurementData,
      }
      if (config.screenshot) {
        screenshot(eaglet, metrics)
      } else {
        eaglet.push(metrics, 'ApmXrayTracker')
      }
    }
  } catch (err: unknown) {
    if (process.env.BUILD_ENV !== 'production') {
      console.error(err)
    }
  }
}
