import { getTrackEnv } from '@xhs/ozone-bridge'

const getEnvInfo: Promise<{sessionId?: string}> = new Promise(resolve => {
  getTrackEnv()
    .then(data => {
      resolve({
        sessionId: data.sessionId,
      })
    })
    .catch(() => {
      resolve({
        sessionId: '',
      })
    })
})

export default getEnvInfo
