import { provide, inject, InjectionKey } from 'vue'

export interface TIntersectionObserver extends IntersectionObserverInit {
  maxListenCount: number
}

const DefaultObserverOptions = {
  rootMargin: '0px 0px 300px',
  // 为了性能限制最大监听数
  maxListenCount: 200,
}

// eslint-disable-next-line symbol-description
const keyObserverOptions: InjectionKey<TIntersectionObserver> = Symbol()

export function useObserverOptionsProvider(options: TIntersectionObserver): void {
  // ObserverOptions 的全局配置
  provide(keyObserverOptions, { ...DefaultObserverOptions, ...options })
}

export default function useObserverOptions(): TIntersectionObserver {
  return inject(keyObserverOptions, DefaultObserverOptions)
}
