import { RouteLocationNormalizedLoaded } from 'vue-router'

interface IGetPageInfoReturn {
  page: {
    type: string
    value: {
      instanceId?: string | string[]
      pageInstance?: {
        type: 'PageInstance'
        value: string
      }
    }
  }
}
export default function getPageInfo(route: RouteLocationNormalizedLoaded | {[props: string]: unknown} = {}) {
  const {
    params = {},
    meta = {},
  } = route as RouteLocationNormalizedLoaded

  let pageInstance: string | undefined
  let instanceId: string | string[] | undefined

  // pageInstance
  if (meta.pageInstance) {
    pageInstance = meta.pageInstance
  }

  // instanceId
  if (meta.instanceId) {
    if (typeof meta.instanceId === 'function') {
      instanceId = meta.instanceId(route as RouteLocationNormalizedLoaded)
    } else {
      instanceId = meta.instanceId
    }
  } else if (params.id) {
    instanceId = params.id
  }

  const output: IGetPageInfoReturn = {
    page: {
      type: 'Page',
      value: {
        instanceId,
      },
    },
  }

  if (pageInstance) {
    output.page.value.pageInstance = {
      type: 'PageInstance',
      value: pageInstance,
    }
  }

  return output
}
