// 内联样式px转为rem单位
export const toRem = (property: number) => {
  if (typeof property === 'number' && document) {
    const html = document.querySelector('html')
    if (html) {
      const rootFontSize = window.getComputedStyle(html).fontSize
      if (rootFontSize) {
        const base = Number(rootFontSize.split('px')[0])
        const res = property / base
        return `${res.toPrecision(3)}rem`
      }
      return property
    }
    return property
  }
  return property
}
