/* eslint-disable no-underscore-dangle */
import { commonChecks } from '@xhs/ozone-detector'
import { subscribe } from '@xhs/ozone-schema'
import { sendByHttp } from '../emitter/client'

import type { IApmXrayTrackerExtendDataPayload, ITrackerExtendDataPayload } from '../types/plugin'

import type { IApmComboCacheGroupV2, IRequestComboOptions } from '../types/helps/requestCombo'

import type { ISendToXrayReturn } from '../types/emitter/client'

// eslint-disable-next-line
const globalThis = Function('return this')() as Window
globalThis.__APM_COMBO_CACHE_GROUP_V2__ = {}
let installed = false

export function sendToXrayByAjax(url: string, data: IApmXrayTrackerExtendDataPayload | IApmXrayTrackerExtendDataPayload[], batch?: boolean) {
  const xhr = new XMLHttpRequest()
  xhr.open('POST', url, true)
  xhr.setRequestHeader('Content-Type', 'application/json')
  xhr.setRequestHeader('Biz-Type', 'apm_fe')
  xhr.setRequestHeader('Batch', batch ? 'true' : 'false')
  xhr.send(JSON.stringify(data))
}

export function sendToXrayByFetch(url: string, data: IApmXrayTrackerExtendDataPayload | IApmXrayTrackerExtendDataPayload[], batch?: boolean) {
  if (typeof fetch === 'function') {
    fetch(url, {
      keepalive: true,
      method: 'POST',
      headers: {
        'content-type': 'application/json',
        'Biz-Type': 'apm_fe',
        Batch: batch ? 'true' : 'false',
      },
      body: JSON.stringify(data),
    }).catch((err: unknown) => {
      if (process.env.BUILD_ENV !== 'production') {
        console.error(err)
      }
    })
  } else {
    sendToXrayByAjax(url, data)
  }
}

/**
 * 兜底逻辑：用户离开页面触发，发送所有点位数据
 */
function viewDisappearListener() {
  const cb = () => {
    const keys = Object.keys(globalThis.__APM_COMBO_CACHE_GROUP_V2__)

    if (keys.length) {
      for (let i = 0; i < keys.length; i++) {
        const endpoint = keys[i]
        const { data, request } = globalThis.__APM_COMBO_CACHE_GROUP_V2__[endpoint] || {}
        if (data?.length) {
          if (request?.name === 'sendToXray') {
            // sendBeacon 不支持 自定义 header
            sendToXrayByFetch.call(null, endpoint, data, true)
            // navigator?.sendBeacon(endpoint, JSON.stringify(data))
          } else {
            // 端外 pe 打点采用 sendBeacon 上报
            const headers = {
              type: 'text/plain',
            }
            const blob = new Blob([JSON.stringify(data)], headers)
            navigator?.sendBeacon(endpoint, blob)
          }
          globalThis.__APM_COMBO_CACHE_GROUP_V2__[endpoint] = null
        }
      }
    }
  }

  // 端内
  if (commonChecks.isXHS) {
    if (commonChecks.isAndroid) {
      subscribe('viewDidDisappear', cb)
    }
    if (commonChecks.isIOS) {
      subscribe('viewDisappear', cb)
    }
  } else {
    // 端外
    const onWindow = window.addEventListener || window.attachEvent
    onWindow('visibilitychange', () => {
      if (document.visibilityState === 'hidden') {
        cb()
      }
    })
  }
}

/**
 * 接口聚合，批量发送请求
 * @param {string} url 接口 url
 * @param {object} data 数据
 * @param {object} param 可选配置参数
 * @param {function} param.request 请求函数体
 * @param {boolean} param.isCombo 是否合并，默认 true
 * @param {number} param.collectTime 接口发送频率，默认 5s
 * @param {number} param.maxComboNum 最大合并数量，默认 5
 * @param {function} param.callback 成功回调
 * @returns
 */

const requestCombo = function (url: string, data: IApmXrayTrackerExtendDataPayload | ITrackerExtendDataPayload, {
  request,
  isCombo = true,
  maxComboNum = 5,
  collectTime = maxComboNum * 1000,
  callback,
}: IRequestComboOptions = {}): ISendToXrayReturn | null {
  if (!url || JSON.stringify(data) === '{}') return null
  const fetch = async (endpoint: string) => {
    if (globalThis.__APM_COMBO_CACHE_GROUP_V2__[endpoint]?.data.length) {
      const { data, timer, request: curRequest } = globalThis.__APM_COMBO_CACHE_GROUP_V2__[endpoint] as IApmComboCacheGroupV2
      clearTimeout(timer)
      globalThis.__APM_COMBO_CACHE_GROUP_V2__[endpoint] = null
      const res = await (curRequest || sendByHttp).call(null, endpoint, data)
      return res
    }
  }

  if (!installed) {
    // 离开页面时上报所有数据
    viewDisappearListener()
    installed = true
  }

  return new Promise((resolve, reject) => {
    if (!isCombo) {
      request?.call(null, url, data).then(data => {
        resolve(data)
      }).catch((err: unknown) => {
        reject(err)
      })
    } else if (url) {
      if (!globalThis.__APM_COMBO_CACHE_GROUP_V2__?.[url]) {
        globalThis.__APM_COMBO_CACHE_GROUP_V2__[url] = {
          request,
          data: [],
          timer: window.setTimeout(() => {
            const comboData: ITrackerExtendDataPayload[] | undefined = globalThis.__APM_COMBO_CACHE_GROUP_V2__[url]?.data
            fetch(url).then(result => {
              callback?.(comboData)
              resolve(result)
            }).catch((err: unknown) => {
              reject(err)
            })
          }, collectTime),
        }
      }
      (globalThis.__APM_COMBO_CACHE_GROUP_V2__[url] as IApmComboCacheGroupV2).data.push(data)
      if ((globalThis.__APM_COMBO_CACHE_GROUP_V2__[url] as IApmComboCacheGroupV2).data.length >= maxComboNum) {
        const comboData: IApmXrayTrackerExtendDataPayload[] | undefined = globalThis.__APM_COMBO_CACHE_GROUP_V2__[url]?.data
        fetch(url).then(result => {
          callback?.(comboData)
          resolve(result)
        }).catch((err: unknown) => {
          reject(err)
        })
      }
    } else {
      reject(`接口 url: ${url} 不能为空`)
    }
  })
}
export default requestCombo

export async function destroy() {
  const keys = Object.keys(globalThis.__APM_COMBO_CACHE_GROUP_V2__)
  if (keys.length) {
    await Promise.all(keys.map(async endpoint => {
      const { data, request } = globalThis.__APM_COMBO_CACHE_GROUP_V2__[endpoint] as IApmComboCacheGroupV2
      if (data?.length) {
        await (request || sendByHttp).call(null, endpoint, data)
        globalThis.__APM_COMBO_CACHE_GROUP_V2__[endpoint] = null
      }
    }))
  }
}
