import writer from './writer'

export interface IInitData {
  [props: string]: {
    value: {
      [props: string]: string | string[] | {
        value: {
          [props: string]: string | (() => string)
        }
      }
    }
  }
}

function getBufferLength(length: number) {
  return writer.writeUnsignedVarint32(length)
}

function getData(item: string, initData: IInitData, trackerEnums: {
  [props: string]: string
}) {
  const ret = item.match(/\{(\w+):(\w+)\.(\w+)\}/) as string[]
  const dataType = ret[1]
  const setterTarget = ret[2]
  const setterTargetKey = ret[3]
  if (initData[setterTarget]?.value) {
    let value = initData[setterTarget].value[setterTargetKey]

    if (dataType === 'Enum') {
      try {
        // @ts-ignore reason: 数据类型未知
        if (typeof value.value.toUpperCase === 'function') {
          // @ts-ignore reason: 据类型未
          value = trackerEnums[(value).type][(value.value.toUpperCase)()]
        }
      } catch (e) {
        throw Error(`not support ${item}`)
      }
    }

    return {
      value,
      dataType,
    }
  }

  return {
    value: undefined,
    dataType,
  }
}

function encodeData(dataType: unknown, value: unknown) {
  if (value === undefined) {
    return []
  }

  let buffers = []

  if (dataType === 'String' && typeof value === 'string') {
    buffers = writer.writeString(value)
  } else if (dataType === 'Int64' && typeof value === 'number') {
    buffers = writer.writeInt64(value)
  } else if (dataType === 'Int32' && typeof value === 'number') {
    buffers = writer.writeSignedVarint32(value)
  } else if (dataType === 'Enum' && typeof value === 'number') {
    buffers = writer.writeUnsignedVarint32(value)
  } else if (dataType === 'Boolean' && typeof value === 'boolean') {
    buffers = writer.writeBoolean(value)
  } else {
    throw Error(`not support ${dataType}:${value}`)
  }

  return buffers
}

function buildBlock(block: string[], initData: IInitData, trackerEnums: {
  [props: string]: string
}) {
  const message: unknown[] = []

  // pop dataType
  const dataTypeString = block.pop() as string
  const { value, dataType } = getData(dataTypeString, initData, trackerEnums)
  let numberBlock: string[]

  if (value === undefined) {
    return []
  }
  if (dataType === 'RepeatedString') {
    const repeatedValue = value as string[]

    // pop {length}
    block.pop()
    numberBlock = block
    repeatedValue.forEach((item: unknown) => {
      const dataBuffer = encodeData('String', item)

      if (dataBuffer.length > 0) {
        numberBlock.forEach(ch => message.push(ch))
        getBufferLength(dataBuffer.length).forEach(ch => message.push(ch))
        dataBuffer.forEach(ch => message.push(ch))
      }
    })
  } else if (dataType === 'String') {
    // pop {length}
    block.pop()
    numberBlock = block
    const dataBuffer = encodeData('String', value)

    if (dataBuffer.length > 0) {
      numberBlock.forEach(ch => message.push(ch))
      getBufferLength(dataBuffer.length).forEach(ch => message.push(ch))
      dataBuffer.forEach(ch => message.push(ch))
    }
  } else {
    numberBlock = block
    numberBlock.forEach(ch => message.push(ch))
    encodeData(dataType, value).forEach(ch => message.push(ch))
  }

  return message
}

export function buildBuffer(initData: IInitData, buffers: unknown[], protobufHash: number, trackerEnums: {
  [props: string]: string
}) {
  let blocks: unknown[] = []

  buffers.forEach((line: unknown, lineIndex: number) => {
    const isMessage = lineIndex % 2 === 1

    if (isMessage) {
      const message: string[] = []
      const messageLengthBlock = buffers[lineIndex - 1] as unknown[]

      (line as unknown[]).forEach((block: unknown) => {
        buildBlock(block as string[], initData, trackerEnums).forEach(ch => {
          message.push(ch as string)
        })
      })

      if (message.length > 0) {
        messageLengthBlock.pop()
        getBufferLength(message.length).forEach(char => messageLengthBlock.push(char))
        blocks.push(message)
      } else {
        blocks.pop()
      }
    } else {
      blocks.push(line)
    }
  })

  let totalLength = 0

  blocks.forEach(block => {
    totalLength += (block as string).length

    return totalLength
  })

  // tracker version
  const extraBuffer = [
    ...writer.writeInt64(totalLength + 4),
    ...writer.writeInt32(protobufHash),
  ]

  blocks = [
    extraBuffer,
    ...blocks,
  ]

  totalLength += extraBuffer.length

  const flat = new Uint8Array(totalLength)
  let offset = 0

  for (let i = 0; i < blocks.length; i++) {
    const block = blocks[i]

    flat.set(block as number[], offset)
    offset += (block as number[]).length
  }
  return writer.encodeByteArray(flat)
}
