import { shouldSign, sign } from '../obfuscated/sign'


export default async (Launcher, requestConfig) => {
  let { url, headers } = requestConfig
  const xt = headers['X-t']
  const xs = headers['X-s']

  if(!(xt && xs) && shouldSign(url)) {
    // https://code.devops.xiaohongshu.com/fe/web/launcher/-/blob/master/packages/launcher/src/crawler-spam/index.js#L64
    let uri = url 

    if (url.indexOf('//') === 0) {
      url = `${window.location.protocol}${url}`
    }
    // uri 需要拿 path，而不是整个 url
    if (/^https?:/.test(url)) {
      try {
        const urlObj = new URL(url)

        uri = urlObj.href.replace(urlObj.origin, '')
      } catch (e) {
        uri = url
      }
    }
    const { params, paramsSerializer } = requestConfig
    const realUrl = Launcher.http.buildURL(uri, params, paramsSerializer)
    const newHeaders = sign(realUrl,requestConfig.data) || {}
    requestConfig.headers['X-t'] = newHeaders['X-t']
    requestConfig.headers['X-s'] = newHeaders['X-s']
  }
  return requestConfig
}