function noop() {
  return false
}

function makeHandlerFromOption(option, context) {
  let check = noop
  let handle = noop

  if (option) {
    if (typeof option === 'function') {
      handle = option
    } else if (typeof option === 'object') {
      check = option.check || noop
      handle = option.handle || noop
    }
  }

  return {
    check,
    handle: config => handle.call(context.app, config),
  }
}

export default function apiFallbackModule(Launcher, options) {
  const context = this // this is the app context
  const http = Launcher.http
  const apiFallback = options.apiFallback || {}
  const unauthorzied = makeHandlerFromOption(apiFallback.unauthorized, context)
  const forbidden = makeHandlerFromOption(apiFallback.forbidden, context)

  http.interceptors.response.use(data => {
    if (unauthorzied.check(data)) {
      unauthorzied.handle()
    } else if (forbidden.check(data)) {
      forbidden.handle()
    }

    return data
  }, (err, config) => {
    // err is created by http module, it will contains status
    if (err.status === 401) {
      unauthorzied.handle(config)
    } else if (err.status === 403) {
      forbidden.handle()
    }

    throw err /* must throw error for next handler */
  })
}
