import { commonChecks } from '@xhs/ozone-detector'
import { ORGANIZATION } from './const'

export function dealSmDeviceId(cb) {
  const smTimeoutTime = 100
  let smDeviceId = ''
  let smDeviceIdReady = false
  const smTimer = setTimeout(() => {
    smDeviceId = SMSdk.getDeviceId ? SMSdk.getDeviceId() : smDeviceId
    if (!smDeviceIdReady) {
      smDeviceIdReady = true
      // 执行业务逻辑
      cb && cb(smDeviceId)
    }
  }, smTimeoutTime)

  SMSdk.ready(() => {
    smDeviceId = SMSdk.getDeviceId ? SMSdk.getDeviceId() : smDeviceId
    clearTimeout(smTimer)
    if (!smDeviceIdReady) {
      smDeviceIdReady = true
      // 执行业务逻辑
      cb && cb(smDeviceId)
    }
  })
}

let callbackAfterCaptcha = null
let captchaOption = null
let instance = null

export function postData({ rid, pass }) {
  if (!pass) {
    instance.reset()
  } else {
    dealSmDeviceId(deviceId => {
      const sliderDiv = document.querySelector('#slider-captcha')
      const loadingDiv = document.querySelector('#slider-captcha-loading')

      window._smReadyFuncs = undefined
      sliderDiv.remove()
      loadingDiv.remove()
      callbackAfterCaptcha({
        rid,
        pass,
        deviceId
      })
    })
  }
}


export function initCaptcha() {
  const task = setInterval(() => {
    if (window.initSMCaptcha) {
      let loadingDivClass = ''
      captchaOption && Object.keys(captchaOption).forEach(key => {
        loadingDivClass += `${key}: ${captchaOption[key]};`
      })
      const sliderDiv = document.createElement('div')
      sliderDiv.id = 'slider-captcha'
      sliderDiv.style = 'position: fixed;left: 50%;top: 50%;display flex;justify-content centertransform: translate(-50%, -50%);-ms-transform: translate(-50%, -50%);-moz-transform: translate(-50%, -50%);-webkit-transform: translate(-50%, -50%); z-index:99999;'
      document.body.appendChild(sliderDiv)
      const loadingDiv = document.createElement('div')
      loadingDiv.id = 'slider-captcha-loading'
      loadingDiv.style = `position: fixed;left:0;right:0;top:0;bottom:0;background-color: rgba(0,0,0,.4);${loadingDivClass}`
      document.body.appendChild(loadingDiv)
      window.initSMCaptcha(
        {
          organization: ORGANIZATION,
          channel: 'web',
          width: commonChecks.isIOS || commonChecks.isAndroid ? '80%' : '400px',
          appendTo: '#slider-captcha',
        },
        instance => {
          instance = instance
          instance.onReady(() => {
            instance.verify()
          })
          instance.onSuccess(postData)
        },
      )
      clearInterval(task)
    }
  }, 200)
}

export function getCaptcha(captchaCallback, options) {
  captchaOption = options
  if (!process.env.BROWSER) {
    return
  }
  if (window._smReadyFuncs) {
    return
  }
  callbackAfterCaptcha = captchaCallback
  window._smReadyFuncs = []
  window.SMSdk = {
    ready(fn) {
      fn && _smReadyFuncs.push(fn)
    },
  }
  window._smConf = {
    organization: ORGANIZATION, // 传入 organization，不要传入 accessKey.
    staticHost: 'static.fengkongcloud.com',
  }
  const url = (function () {
    const originHost = 'static2.fengkongcloud.com'
    const isHttps = document.location.protocol === 'https:'
    const protocol = isHttps ? 'https://' : 'http://'
    const fpJsPath = '/fpv2.js'
    let url = protocol + _smConf.staticHost + fpJsPath
    const ua = navigator.userAgent.toLowerCase()
    const isWinXP = /windows\s(?:nt\s5.1)|(?:xp)/.test(ua)
    const isLowIE = /msie\s[678]\.0/.test(ua)

    if (isHttps && isWinXP && isLowIE) {
      url = protocol + originHost + fpJsPath
    }
    return url
  }())
  const sm = document.createElement('script')
  const s = document.getElementsByTagName('script')[0]

  sm.src = url
  s.parentNode.insertBefore(sm, s)
  const hm = document.createElement('script')

  hm.src = '//castatic.fengkongcloud.com/pr/v1.0.1/smcp.min.js'
  s.parentNode.insertBefore(hm, s)
  initCaptcha()
}
