/*
- 设置项：builder和emitter
- 提供发送数据的方法 push()
*/
import createBuilder from './pbBuilder'
import { throwError } from './utils'

export default class Eaglet {
  constructor({ name, builder, emitter }) {
    if (!name) {
      throwError('missing tracker name')
    }

    if (!builder) {
      throwError('missing builder')
    }

    if (!emitter) {
      throwError('missing emitter')
    }

    this.name = name
    this.builder = createBuilder(builder)
    this.emitter = emitter
  }

  push(data) {
    const payload = this.builder.build(this.name, data)
    this.emitter.push(payload)
  }

  flush() {
    this.emitter.flush()
  }

  destroy() {
    this.emitter?.destroy()
  }
}
