import { commonChecks } from '@xhs/ozone-detector';
import completeUrl, { webp2png } from './completeUrl';
import SchemaError from './SchemaError';
const transformMethods = ['getDeviceInfo', 'getSupportedEvents'];
const nativeCodeSuccess = 0;
export function transformArgs(method, config) {
    if (method === 'sendClientRequestV3') {
        let headers = { ...config.headers };
        const requestMethod = config.method.toUpperCase();
        const { 'Content-Type': contentType, ...restHeaders } = headers;
        /**
         * sendClientRequestV3 边界case:
         * 1、requestMethod 是 post, put, delete 的场景，需要有 Content-Type
         * 2、contentType 没有指定或者不是 'application/x-www-form-urlencoded' | 'application/json' 二者之一，则默认指定为 'application/json'
         */
        if (requestMethod !== 'GET'
            && !['application/x-www-form-urlencoded', 'application/json'].includes(contentType)) {
            headers['Content-Type'] = 'application/json';
        }
        else if (requestMethod === 'GET' && contentType) {
            // iOS 平台 get 请求不能有 Content-Type
            headers = restHeaders;
        }
        // headers 域需要前端转换为 string
        Object.keys(headers).forEach(key => {
            if (typeof headers[key] !== 'string') {
                headers[key] = String(headers[key]);
            }
        });
        const newConfig = {
            ...config,
            url: completeUrl(config.url),
            headers,
        };
        return newConfig;
    }
    return config;
}
function adaptGetDeviceInfo(data) {
    // ios的deviceId值对应安卓的uniqueId,最终需要消费的也是uniqueId的值
    if (commonChecks.isIOS) {
        data.value.deviceId = data?.value?.uniqueId;
    }
    try {
        // totalDiskCapacity和freeDiskStorage两个字段信息，使用到了getExternalStorageDirectory的权限，由于监管原因需要下掉字段
        // 客户端在 7.22 下掉字段，部分老版本 app 无法通过 schema 校验
        // @ts-ignore
        delete data.value.freeDiskStorage;
        // @ts-ignore
        delete data.value.totalDiskCapacity;
    }
    catch (error) {
        console.log('get device info interceptor error:', error.message);
    }
    return data;
}
const transformMap = {
    getDeviceInfo: adaptGetDeviceInfo,
    getUserInfo: (res) => {
        webp2png(res);
        return res;
    },
};
export function transformRes(method, res) {
    const { result, value, message } = res || {};
    let businessValue = value;
    /**
     * 'getDeviceInfo', 'getSupportedEvents' 两个个方法在 6.64 以下版本返回值没有区分业务 result 和框架层 result。
     * 所以，对于这四个方法， 如果返回的值只有一层 result。 则把整个返回值提供给业务方使用
     */
    if (transformMethods.includes(method) && result === nativeCodeSuccess) {
        const isNewValue = Object.prototype.toString.call(businessValue) === '[object Object]' && Object.prototype.hasOwnProperty.call(businessValue, 'result');
        if (!isNewValue) {
            businessValue = res;
        }
    }
    if (result === nativeCodeSuccess) {
        return transformMap[method] ? transformMap[method](businessValue) : businessValue;
    }
    throw new SchemaError(result, `${method}: ${message}`);
}
