import { SchemaType } from '../utils/type';
import { validateRes } from '../utils/check';
import { hasBridgeConsole, bridgeStart, bridgeSuccess, bridgeError, bridgeValidateError, } from '../utils/bridgeConsole';
import oldBridgeList from '../old/index';
import generateKey from '../utils/generateKey';
class Interceptor {
    constructor() {
        this.queue = [];
    }
    use(fulfilled, rejected) {
        this.queue.push({ fulfilled, rejected });
    }
}
export function createInterceptor() {
    return new Interceptor();
}
export const rnInterceptorMap = {
    xhs: {},
    top: {},
};
export const h5InterceptorMap = {
    xhs: {},
    top: {},
};
function transformBridgeResultForTest(res) {
    try {
        // 用于兼容安卓 部分 bridge 不需要返回 value 的场景
        // ios { message: xx, result: 0}
        // Android { message: xx, result: 0, value: null }
        // 期望 value 为 null 不需要返回，但经过安卓为统一数据结构改动成本高，因此在 js 侧兼容
        if (res?.value !== undefined && res?.value === null) {
            delete res.value;
        }
        // eslint-disable-next-line no-empty
    }
    catch (error) { }
    return res;
}
export function genBridgeQueue(method, args, handler, { type, app, enableValidate = true, }) {
    const isH5 = type === 'h5';
    const interceptorMap = isH5 ? h5InterceptorMap : rnInterceptorMap;
    const requestQueue = interceptorMap?.xhs?.[method]?.request?.queue || [];
    const resultQueue = interceptorMap?.xhs?.[method]?.result?.queue || [];
    const traceId = generateKey();
    const startTime = Date.now();
    let isV3 = true;
    const validateHandler = {
        fulfilled: (res) => {
            if (isV3) { // 新通道校验，降级为老通道时，去除校验
                validateRes(method, SchemaType.Method, transformBridgeResultForTest(res), app);
            }
            return res;
        },
        rejected: (err) => {
            throw err;
        },
    };
    // 小红书 app 新通道降级 handler，在新通道 bridge 不支持时降级为老通道发送 bridge
    const degradatHandler = {
        fulfilled: (res) => res,
        rejected: (err) => new Promise((resolve, reject) => {
            // code === -10000 时代表新通道不支持，此时降级为老通道发送
            if (app === 'xhs' && err.code === -10000) {
                isV3 = false;
                const oldHandler = oldBridgeList[method];
                if (oldHandler) {
                    return oldHandler(args).then((oldVal) => {
                        resolve({
                            result: oldVal?.result || 0,
                            message: `${method} 新通道不支持，降级为老通道发送`,
                            value: (oldVal?.value !== undefined) ? oldVal.value : oldVal,
                        });
                    }).catch((innerErr) => {
                        reject(innerErr);
                    });
                }
            }
            return reject(err);
            // throw err
        }),
    };
    const logHandler = {
        fulfilled: (res) => {
            const timing = Date.now() - startTime;
            bridgeSuccess({
                name: method, callbackId: traceId, time: timing, res,
            });
            return res;
        },
        rejected: (err) => {
            const timing = Date.now() - startTime;
            try {
                const { errorMessage } = JSON.parse(err.message);
                bridgeValidateError({
                    name: method,
                    callbackId: traceId,
                    time: timing,
                    err: JSON.stringify(errorMessage, null, 2),
                });
            }
            catch (error) {
                // 非校验类错误上报到控制台
                bridgeError({
                    name: method,
                    callbackId: traceId,
                    time: timing,
                    err,
                });
            }
            throw err;
        },
    };
    const chain = [
        ...requestQueue,
        handler,
        degradatHandler,
        ...resultQueue,
    ];
    if (enableValidate) {
        chain.push(validateHandler);
    }
    if (hasBridgeConsole()) {
        chain.push(logHandler);
    }
    const config = {
        method,
        args,
    };
    let promise = Promise.resolve(args);
    const wrapFulfilled = (h) => (payload) => (h ? h(payload, config) : payload);
    const wrapRejected = (h) => (ex) => {
        if (h) {
            return h(ex, null, config);
        }
        throw ex;
    };
    bridgeStart({
        name: method,
        callbackId: traceId,
    });
    while (chain.length) {
        const task = chain.shift();
        promise = promise.then(wrapFulfilled(task?.fulfilled), wrapRejected(task?.rejected));
    }
    return promise;
}
export function useInterceptor(method, app, type) {
    return {
        request: {
            use: (fulfilled, rejected) => {
                const interceptorMap = type === 'h5' ? h5InterceptorMap : rnInterceptorMap;
                if (!interceptorMap?.[app]) {
                    interceptorMap[app] = {};
                }
                if (!interceptorMap?.[app]?.[method]) {
                    interceptorMap[app][method] = {
                        request: createInterceptor(),
                        result: createInterceptor(),
                    };
                }
                interceptorMap?.[app]?.[method].request.use(fulfilled, rejected);
            },
        },
        response: {
            use: (fulfilled, rejected) => {
                const interceptorMap = type === 'h5' ? h5InterceptorMap : rnInterceptorMap;
                if (!interceptorMap?.[app]) {
                    interceptorMap[app] = {};
                }
                if (!interceptorMap?.[app]?.[method]) {
                    interceptorMap[app][method] = {
                        request: createInterceptor(),
                        result: createInterceptor(),
                    };
                }
                interceptorMap?.[app]?.[method].result.use(fulfilled, rejected);
            },
        },
    };
}
