/**
 * 判断卡顿的条件，默认如下
 * 指标在某个范围内，判断为卡顿
 * 1. frame队列不足 30个，正常60
 * 2. fpsGap两个统计周期间隔超过 1150ms
 * 3. fps低于30
 * */
 const isBlocking = (frame, fpsGap, fps, judgement) => {
  if ((frame >= judgement.frames[0] && frame <= judgement.frames[1])
    || (fpsGap >= judgement.fpsGap[0] && fpsGap <= judgement.fpsGap[1])
    || (fps >= judgement.fps[0] && fps < judgement.fps[1])) {
    return true
  }
  return false
}
/**
 * 启动fps统计
 * @param analysisCount 分析次数。 分析60次停止
 * @param percent 抽样率
 */
function fpsInit(fpsConfig) {
  const percentCount = fpsConfig.percent * 100
  const temp = Math.round(Math.random() * 100)
  const isInPercent = temp < percentCount

  let lastTime = performance.now()
  let lastFameTime = lastTime
  let calcCount = 0 // fps计算周期
  let frame = 0 // 一个周期内，记录帧数
  let fpsList = [] // fps数组
  const initData = () => {
    frame = 0
    lastTime = performance.now()
    fpsList = []
  }
  const loop = () => {
    const now = performance.now()
    const fs = now - lastFameTime // 两帧的时间间隔
    lastFameTime = now
    const fps = Math.round(1000 / fs) // 瞬间FPS
    fpsList.push(fps)
    frame += 1
    if (now >= 1000 + lastTime) {
      const fpsCircle = Math.round((frame * 1000) / (now - lastTime))
      const tempGap = ((now - lastTime) / 1000).toFixed(2)
      const fpsGap = parseFloat(tempGap) // 单位: 秒
      const isBlock = isBlocking(frame, fpsGap, fpsCircle, fpsConfig.judgement)
      const pageVisible = document.visibilityState === 'hidden' ? 2 : 1
      // 字段信息组合
      const metrics = {
        type: 'SnsFpsApmTracker',
        value: {
          FpsIndex: calcCount,
          Fps: fpsCircle,
          FpsGap: fpsGap,
          FpsCount: fpsList.length,
          PageVisible: pageVisible,
          isBlock,
        },
      }
      // 点位上报
      window?.eaglet?.push(metrics, 'ApmJSONTracker')

      // 恢复初始值
      frame = 0
      lastTime = now
      fpsList = []
      // 统计次数计数
      calcCount += 1
    }
    if (calcCount < fpsConfig.analysisCount && isInPercent) {
      window?.requestAnimationFrame(loop)
    } else {
      document.removeEventListener('visibilitychange', initData)
    }
  }
  // 页面显示的状态变化，则恢复初始值重新计算卡顿数据
  document.addEventListener('visibilitychange', initData)
  return loop
}

export default fpsInit
