/* eslint-disable no-useless-escape */
type TSvg = {
  viewBox: string
  content: string
}

function getSvgFromIcon({ viewBox = '', content = '' } = {}):TSvg {
  const regx = /<symbol["a-zA-Z0-9\.\/:"\s=_]+>\s*([<>"a-zA-Z0-9#\.\/:"\s=\-_\(\)]+)\s*<\/symbol>/
  const matched = content.match(regx)
  let renderContent = ''
  if (Array.isArray(matched) && matched[1]) {
    renderContent = matched[1]
  } else {
    // 兜底，硬截取字符串，以防正则没考虑到的情况
    const indexStart = content.indexOf('<path')
    const indexEnd = content.indexOf('</symbol>')
    renderContent = content.substring(indexStart, indexEnd)
  }
  return {
    viewBox, content: renderContent,
  }
}

export default getSvgFromIcon
