import { getPostData } from '../helpers/get-post-data'
import type { IEagletMethods } from '../types/plugin'

// eaglet 元素曝光逻辑:
// 1.元素滑入视口内的长 > 该元素的长 * 0.5 & 元素滑入视口内的宽 > 该元素的宽 * 0.5 即打曝光
// 2.元素滑入视口内的长 > 屏幕的长 * 0.3 &  元素滑入视口内的宽 > 屏幕的宽 * 0.3 即打曝光

// dom元素滑入视口的宽度与长度分别占比自身宽度和长度30%
const SHOW_PART_RATE = 0.3
// dom元素滑入视口的宽度与长度分别占比屏幕宽度和长度30%
const SHOW_DOM_ENTER_RATE = 0.3

class ImpressionCollector {
  tracker: IEagletMethods

  countDown: unknown

  intervalTimer: number

  intervalTickCount: number

  warningShown: boolean

  countDownTimer: number

  constructor(tracker: IEagletMethods) {
    this.tracker = tracker
    this.countDown = null

    this.intervalTimer = 0
    this.intervalTickCount = 0

    this.countDownTimer = 0

    this.warningShown = false

    // 初始化的时候打一次点
    this.postData()

    // check on user vent
    this.checkByEvent()

    // check by interval
    this.checkByInterval()

    document.addEventListener('trigger-impression', () => {
      this.postData()
    })
  }

  checkByEvent() {
    const eventName = this.isSupportTouch() ? 'touchmove' : 'scroll'

    window.addEventListener(eventName, () => {
      this.postData()
    }, false)
  }

  checkByInterval() {
    // 防止页面在后台运行 无限执行 10分钟后就不再监听
    if (this.intervalTickCount <= 120) {
      this.intervalTimer = window.setTimeout(() => {
        this.intervalTickCount += 1
        this.postData()
        this.checkByInterval()
      }, 5000)
    }
  }

  postData() {
    if (this.countDownTimer) {
      clearTimeout(this.countDownTimer)
    }

    this.countDownTimer = window.setTimeout(() => {
      const legacyNodes = [...document.querySelectorAll('[eaglet]')]

      if (legacyNodes.length > 0 && !this.warningShown) {
        console.warn('[WebTracker] custom data attribute `eaglet` was deprecated, please use `data-eaglet` instead') // eslint-disable-line no-console
        this.warningShown = true
      }

      const dataNodes = [...document.querySelectorAll('[data-eaglet],[data-impression]')]

      const nodes = [...legacyNodes, ...dataNodes] as HTMLElement[]

      nodes.forEach(node => {
        const domShowed = node.dataset?.eagletImp || node.getAttribute('data-eaglet-imp')

        if (!domShowed) {
          const isInnerWindow = this.isInnerWindow(node)
          const isHidden = this.isHidden(node)
          if (isInnerWindow && isHidden) {
            const postData = this.getData(node)

            node.setAttribute('data-eaglet-imp', 'true')

            if (postData) {
              this.tracker.push(postData)
            }
          }
        }
      })
    }, 500)
  }

  // 获取打点数据
  getData(targetDom: HTMLElement) {
    // 阻止冒泡
    const noPropagation = true
    const postData = getPostData(targetDom, noPropagation, 'impression')

    if (!postData) {
      return
    }

    if (postData.event) {
      postData.event.value.action = {
        type: 'NormalizedAction',
        value: 'impression',
      }

      postData.event.value.seAction = 'impression'
      if (postData.event?.value?.pointId !== undefined && postData.event?.value?.pointId < 20000) {
        delete postData.event.value.pointId
      }
    } else {
      postData.event = {
        type: 'Event',
        value: {
          action: {
            type: 'NormalizedAction',
            value: 'impression',
          },
          seAction: 'impression',
        },
      }
    }

    return postData
  }

  isSupportTouch() {
    return 'ontouchmove' in document
  }

  // 是否在屏幕内
  isInnerWindow(targetDom: HTMLElement) {
    const innerHeight = window.innerHeight
    const innerWidth = window.innerWidth
    if (targetDom && typeof targetDom.getBoundingClientRect === 'function') {
      const bounds = targetDom.getBoundingClientRect()
      const isDomVerticalVisibleEnough = bounds.bottom >= 0
        && bounds.top + bounds.height * SHOW_PART_RATE < innerHeight
      const isDomHorizonalVisibleEnough = bounds.right >= 0
        && bounds.left + bounds.width * SHOW_PART_RATE < innerWidth

      // 如果元素滑入视口的宽度和长度超过屏幕宽度和长度的30%,则曝光
      const isDomVerticalEnterEnough = bounds.bottom >= 0 && SHOW_DOM_ENTER_RATE <= (innerHeight - bounds.top) / window.screen.height
      const isDomHorizonalEnterEnough = bounds.right >= 0 && SHOW_DOM_ENTER_RATE <= (innerWidth - bounds.left) / window.screen.width

      return (isDomVerticalVisibleEnough && isDomHorizonalVisibleEnough) || (isDomVerticalEnterEnough && isDomHorizonalEnterEnough)
    }

    return false
  }

  isHidden(targetDom: HTMLElement) {
    return Boolean(targetDom.offsetParent)
  }
}

export default function createImpressionCollector(options: IEagletMethods) {
  return new ImpressionCollector(options)
}
