import {
  isObject,
  toUpperCase,
  capitalize,
  throwError,
} from './utils'

class ProtoBuilder {
  constructor(builder) {
    this.builder = builder
  }

  getMessageBuilder(messageType) {
    if (!this.builder[messageType]) {
      throwError(`no message type found: \`${messageType}\``)
    }

    return new this.builder[messageType]()
  }

  getProtoSetter(messageType, messageBuilder, field, fieldData) {
    let setter = `set${capitalize(field)}`

    let data = fieldData

    if (data && data.value) {
      data = data.value
    }

    if (Array.isArray(data)) {
      setter += 'List'
    }

    if (!messageBuilder[setter]) {
      throwError(`no match setter \`${setter}\` for \`${capitalize(field)}\` in proto.${messageType}`)
    }

    return setter
  }

  format(fieldType, fieldValue) {
    if (Array.isArray(fieldValue)) {
      return fieldValue.map(item => this.format(fieldType, item))
    }

    const messageBuilder = this.getMessageBuilder(fieldType)

    Object.keys(fieldValue).forEach(field => {
      const setter = this.getProtoSetter(fieldType, messageBuilder, field, fieldValue[field])

      if (isObject(fieldValue[field])) {
        const { type, value } = fieldValue[field]

        if (!type || !this.builder[type]) {
          throwError(`no match type \`${type}\` in \`${field}\` field`)
        }

        if (isObject(value) || Array.isArray(value)) { // message > message
          const val = this.format(type, value)

          messageBuilder[setter](val)
        } else { // message > enum
          const enumValue = this.builder[type][toUpperCase(value)]

          if (typeof enumValue === 'undefined') {
            throwError(`no match enum \`${value}\` in \`${type}\` field`)
          }

          messageBuilder[setter](enumValue)
        }
      } else { // message > primitive
        messageBuilder[setter](fieldValue[field])
      }
    })

    return messageBuilder
  }

  build(trackerName = 'Tracker', data = {}) {
    if (!trackerName) {
      throwError('missing tracker name')
    }

    const trackerBuilder = this.getMessageBuilder(trackerName)

    Object.keys(data).forEach(fieldName => {
      const fieldData = data[fieldName]

      if (!fieldData.type || !fieldData.value) {
        throwError('missing `type` or `value` field')
      }

      const trackerSetter = this.getProtoSetter(trackerName, trackerBuilder, fieldName, fieldData.value)
      const buildedProps = this.format(fieldData.type, fieldData.value)

      trackerBuilder[trackerSetter](buildedProps)
    })

    return trackerBuilder
  }
}

export default function createBuilder(builder) {
  if(typeof builder.build === 'function'){
    return builder
  }
  return new ProtoBuilder(builder)
}
