<template>
  <button
    type="button"
    :disabled="disabled"
    class="reds-button-new"
    v-bind="$attrs"
    :class="[
      size,
      type,
      {
        'has-icon': icon || $slots.icon || loading,
        'pure-icon': (icon || $slots.icon || loading) && !$slots.default,
        block,
        disabled,
        active,
        'reds-button--loading': loading,
      },
    ]"
    :style="{ width: fixedWidth }"
  >
    <span class="reds-button-new-box">
      <reds-icon
        v-if="icon"
        class="reds-button__icon"
        :icon="icon"
      />
      <div
        v-if="loading"
        class="reds-button__icon"
      >
        <ProgressIndicator
          type="pageLoading"
          :disable-theme-change="true"
        />
      </div>
      <slot name="icon" />
      <span
        v-if="$slots.default"
        class="reds-button-new-text"
      >
        <slot />
      </span>
    </span>
  </button>
</template>

<script lang="ts">
  import { defineComponent } from 'vue'
  import RedsIcon from '../Icon/Icon.vue'
  import { ProgressIndicator } from '../ProgressIndicator'
  import IconProps from '../Icon/props'
  import ButtonProps from './props'
  import '@xhs/reds-h5/style/button.styl'

  export default defineComponent({
    name: 'RedsButton',
    components: { RedsIcon, ProgressIndicator },
    props: {
      ...ButtonProps,
      icon: {
        ...IconProps.icon,
        required: false,
      },
    },
  })
</script>
