export function getMobileOS() {
  const userAgent = window.navigator.userAgent
  let tempArray

  // Check for iOS and extract the version
  if (/iPad|iPhone|iPod/.test(userAgent)) {
    tempArray = userAgent.match(/(iPhone|iPad|iPod).* OS ([0-9]+)_([0-9]+)/)
    if (tempArray) {
      return { systemName: 'iOS', version: `${tempArray[2]}.${tempArray[3]}` }
    }
  }

  // Check for Android and extract the version
  tempArray = userAgent.match(/Android\s+([\d.]+)/)
  if (tempArray) {
    return { systemName: 'Android', version: tempArray[1] }
  }

  return { systemName: '', version: '' }
}
