<template>
  <span
    class="reds-status-bar"
    :style="statusBarHeight?{
      height: `${statusBarHeight.value}px`
    }:null"
  />
</template>

  <script lang="ts" setup>
  import { ref } from 'vue'
  import useStatusBarHeight from './useStatusBarHeight'
  import baseProps from './props'
  import '@xhs/reds-h5/style/statusBar.styl'

  const props = defineProps(baseProps)
  const statusBarHeight = ref(0)
  const initialStatusBar = async () => {
    statusBarHeight.value = await useStatusBarHeight(props)
  }
  initialStatusBar()
  </script>
