import {
  error as logError, configure, LOG_LEVEL,
} from '@xhs/logger'

configure({
  transport: {
    console: process.env.NODE_ENV === 'production' ? LOG_LEVEL.ERROR : true,
    eaglet: true, // enable transport eaglet
  },
  fields: { // passing common business fields for tracking
    app: process.env.APP_NAME,
    prdline: 'internal-sso',
  },
})

const customLogError = (err, extra) => {
  const {
    code, name, status, data,
  } = err
  logError(err.message, {
    name,
    status,
    code,
    data,
    ...(extra || {}),
  })
}

const EDITH_INTERNAL_LOGIN_URL = '/sso/internal_login'
const EDITH_INTERNAL_USER_INFO_URL = '/sso/user_info'
const EDITH_INTERNAL_LOGOUT_URL = '/sso/logout'

const EDITH_HOSTS = {
  development: '//edith.sit.xiaohongshu.com',
  test: '//edith.sit.xiaohongshu.com',
  prerelease: '//edith.beta.xiaohongshu.com',
  production: '//edith.xiaohongshu.com',
}
const SSO_HOSTS = {
  development: 'http://login2.sit.xiaohongshu.com',
  test: 'http://login2.sit.xiaohongshu.com',
  prerelease: 'https://login2.xiaohongshu.com',
  production: 'https://login2.xiaohongshu.com',
}

const edithHost = EDITH_HOSTS[process.env.BUILD_ENV]
const ssoHost = SSO_HOSTS[process.env.BUILD_ENV]

const getAPIHost = apiHost => {
  if (!apiHost) return edithHost
  if (typeof apiHost === 'string') return apiHost
  return apiHost[process.env.BUILD_ENV] || edithHost
}

function getLoginService() {
  const { origin, pathname, search } = window.location
  const searchParmas = new URLSearchParams(search)
  searchParmas.delete('ticket')
  const searchWithoutTicket = searchParmas.toString()
  return `${origin}${pathname}${searchWithoutTicket ? '?' : ''}${searchWithoutTicket}`
}

function handleHttpModule(http) {
  const methodsMap = ['get', 'del', 'head', 'post', 'put', 'patch']

  methodsMap.forEach(method => {
    if (http[method]) {
      const httpFn = http[method]
      const configIdxFromArgs = httpFn.length - 1 // assume the last arguments is config

      httpFn.noAuth = function xhrNoAuth(...args) {
        const config = args[configIdxFromArgs] || {}

        args[configIdxFromArgs] = { ...config, launcherNoAuth: true }

        return httpFn.apply(http, args)
      }
    }
  })

  http.interceptors.request.use(config => {
    if (!config.launcherNoAuth && !config.noAuth) {
      config.withCredentials = true
    }
    return config
  })
}

const defaultLogin = (Launcher, schema, { ticket, subsystemAlias, apiHost = {} }) => {
  const host = getAPIHost(apiHost)
  return Launcher.http.post(`${host}${EDITH_INTERNAL_LOGIN_URL}`, {
    ticket,
    validate_service: getLoginService(),
    subsystem_alias: subsystemAlias,
    set_global_domain: schema.setCookieInRootDomain !== false,
  })
    .then(res => ({ userInfo: res }))
    .catch(err => {
      customLogError(err, { url: `${host}${EDITH_INTERNAL_LOGIN_URL}` })
      return { userInfo: {} }
    })
}

const defaultUnauthorized = () => {
  const loginService = encodeURIComponent(getLoginService())
  setTimeout(() => {
    window.location.href = `${ssoHost}?service=${loginService}`
  }, 300)
}

const defaultGetUserInfo = (Launcher, schema) => {
  const urlSearch = new URLSearchParams(window.location.search)
  const ticket = urlSearch.get('ticket')
  const host = getAPIHost(schema.apiHost)
  if (!ticket) {
    return Launcher.http.post(`${host}${EDITH_INTERNAL_USER_INFO_URL}`)
      .then(res => ({ ...res?.internalUser, accessToken: res?.['access-token'] }))
      .catch(err => {
        customLogError(err, { url: `${host}${EDITH_INTERNAL_USER_INFO_URL}` })
        return {}
      })
  }
  return defaultLogin(Launcher, schema, {
    ...schema,
    ticket,
  }).then(res => {
    const nextRouter = getLoginService()
    const { pathname, search } = new URL(nextRouter)
    // setTimeout(() => {
    //   router.push(`${pathname}${search}`)
    // })
    window.location.replace(`${pathname}${search}`)
    return res.userInfo || {}
  })
}

const defaultLogout = (Launcher, schema) => {
  const host = getAPIHost(schema.apiHost)
  return Launcher.http.post(`${host}${EDITH_INTERNAL_LOGOUT_URL}`)
    .catch(err => {
      customLogError(err, { url: `${host}${EDITH_INTERNAL_LOGOUT_URL}` })
      return {}
    })
}

export default function internalSSOModule(Launcher, options, scheme) {
  // Bind noAuth method to http
  handleHttpModule(Launcher.http)

  options.loginReturnUserInfo = true
  const apiFallback = options.apiFallback || {}
  options.apiFallback = Object.assign(apiFallback, {
    unauthorized: apiFallback.unauthorized || defaultUnauthorized,
  })

  const services = options.services || {}
  options.services = Object.assign(services, {
    login: services.login || defaultLogin.bind(this, Launcher, scheme),
    getUserInfo: services.getUserInfo || defaultGetUserInfo.bind(this, Launcher, scheme, this.router),
    logout: services.logout || defaultLogout.bind(this, Launcher, scheme),
  })
}
