import EmitterBase from '@xhs/eaglet-emitter-base'
import { buildBuffer, IInitData } from './builder'

import type { ILiteEagletConstructorOptions } from '../types/helps/lite-eaglet'

function titleCase(str: string) {
  return str.slice(0, 1).toUpperCase() + str.slice(1)
}

export default class LiteEaglet {
  name: string

  emitter: EmitterBase

  versionHash: number

  trackerEnums: unknown

  constructor({
    name,
    emitter,
    trackerEnums,
    versionHash,
  }: ILiteEagletConstructorOptions) { // I do not need builder !!
    if (!name) {
      throw Error('missing name')
    }

    if (!emitter) {
      throw Error('missing emitter')
    }

    if (!trackerEnums) {
      throw Error('missing trackerEnums')
    }

    if (!versionHash) {
      throw Error('missing versionHash')
    }

    this.name = name
    this.emitter = emitter
    this.versionHash = versionHash
    this.trackerEnums = trackerEnums
  }

  push({
    data,
    templateBuffers,
  }: {
    data: {
      [props: string]: unknown
    }
    templateBuffers: {
      [props: string]: unknown
    } | unknown[]
  }) {
    const handledData: {
      [props: string] : unknown
    } = {}

    Object.keys(data).forEach(element => {
      handledData[titleCase(element)] = data[element]
    })
    this.emitter.push(buildBuffer(
      handledData as IInitData,
      templateBuffers as unknown[],
      this.versionHash,
      this.trackerEnums as {
        [props: string]: string
      },
    ))
  }

  flush() {
    this.emitter.flush()
  }
}
