class Interceptor {
  constructor(queue = []) {
    this.queue = queue
  }

  use(fulfilled, rejected) {
    this.queue.push({ fulfilled, rejected })
  }
}

export function createInterceptor() {
  return new Interceptor()
}


export function forkInterceptor(interceptor) {
  return new Interceptor([...interceptor.queue])
}
