import { Eaglet } from '@xhs/eaglet'
import LiteEaglet from './lite-eaglet'
import DefaultEmitter from '../emitter/default-emitter'
import ApmEmitter from '../emitter/apm-emitter'
import { DEFAULT_TRACKER, APM_TRACKER, APM_JSON_TRACKER } from '../const'

import type { ITracker, ICreateTrackerOptions, IPayload } from '../types/helps/create-tracker'
import type { IOptions } from '../types/emitter/default-emitter'

import type {
  IApmXrayTrackerExtendDataPayload, ITrackerExtendDataPayload, ILitePushPayload, IFlushApmPayload,
} from '../types/plugin'

export default function createTracker(tracker: ITracker, {
  enableNativeEmitter: preferNative,
  enableBatchRequest,
  customEndPoint,
}: ICreateTrackerOptions) {
  const isDefaultTracker = tracker.NAME === DEFAULT_TRACKER.NAME

  const options: IOptions = {
    // endpoint: isDefaultTracker ? DEFAULT_TRACKER.ENDPOINT : APM_TRACKER.ENDPOINT,
    versionHash: tracker.PROTOBUF_HASH,
    debug: tracker.debug,
    preferNative,
  }

  if (isDefaultTracker) {
    options.endpoint = {
      development: customEndPoint?.defaultTracker?.development || DEFAULT_TRACKER.ENDPOINT.development,
      production: customEndPoint?.defaultTracker?.production || DEFAULT_TRACKER.ENDPOINT.production,
    }
  } else if (tracker.dataType === 'json') {
    options.enableBatchRequest = enableBatchRequest
    options.dataType = tracker.dataType
    options.endpoint = {
      development: customEndPoint?.apmJsonTracker?.development || APM_JSON_TRACKER.ENDPOINT.development,
      production: customEndPoint?.apmJsonTracker?.production || APM_JSON_TRACKER.ENDPOINT.production,
    }
  } else {
    options.enableBatchRequest = false
    options.endpoint = {
      development: customEndPoint?.apmTracker?.development || APM_TRACKER.ENDPOINT.development,
      production: customEndPoint?.apmTracker?.production || APM_TRACKER.ENDPOINT.production,
    }
  }
  let eaglet: LiteEaglet | Eaglet

  if (tracker.IS_LITE) {
    eaglet = new LiteEaglet({
      name: tracker.NAME,
      emitter: new DefaultEmitter({
        ...options,
        isLite: true,
      }),
      trackerEnums: tracker.tracker?.trackerEnums,
      versionHash: tracker.PROTOBUF_HASH,
    })
  } else {
    eaglet = new Eaglet({
      name: tracker.NAME,
      builder: tracker.builder,
      emitter: isDefaultTracker ? new DefaultEmitter(options) : new ApmEmitter({
        ...options,
        apmXrayTrackerEndPoint: customEndPoint?.apmXrayTracker,
        apmXrayProtoTrackerEndPoint: customEndPoint?.apmXrayProtoTracker,
      }),
    })
  }

  const output = {
    push: (payload: ITrackerExtendDataPayload | IApmXrayTrackerExtendDataPayload | ILitePushPayload) => {
      if (tracker.IS_LITE) {
        (payload as ILitePushPayload).data.app.value.trackerVersion = tracker.VERSION;
        (eaglet as LiteEaglet).push(payload as ILitePushPayload)
      } else if ((payload as ITrackerExtendDataPayload).app?.value) {
        (payload as IPayload).app.value.trackerVersion = tracker.VERSION;
        (eaglet as Eaglet).push(payload as ITrackerExtendDataPayload)
      } else if ((payload as IApmXrayTrackerExtendDataPayload).measurement_name) {
        (eaglet as Eaglet).emitter?.push(payload)
      }
    },

    flush() {
      eaglet.flush()
    },

    flushApm(payload: IFlushApmPayload) {
      (eaglet as Eaglet).emitter?.flushApm(payload)
    },

    destroy() {
      (eaglet as Eaglet)?.destroy()
    },
  }

  return output
}
