import { v4 as uuidv4 } from 'uuid'
import PerfTiming from '../../timing'
import { isForbiddenUrl } from '../../helpers'
import { isFunc } from '../../helpers/typeor'
import { createHttpTracker, serializeParams } from './lib'
import makeRng from '../../helpers/makeRng'

const TRACE_HEADER_NAME = 'x-b3-traceid'

class BrowserHttpRequestTiming extends PerfTiming {
  httpTracker = {}

  measure(mark, data) {
    this.httpTracker.track(mark, data)

    const { measure = {}, ...meta } = this.httpTracker.measure(mark)

    measure.name = meta.url
    delete meta.url

    return {
      meta,
      measure,
    }
  }

  observe(options = {}) {
    const { httpClient, callback, enableResponseData = false } = options

    this.validate(options, [{
      prop: 'httpClient',
      required: true,
    }, {
      prop: 'callback',
      required: true,
      validator: cb => typeof cb === 'function',
    }])

    this.httpTracker = createHttpTracker()

    // request
    httpClient.interceptors.request.use(config => {
      if (!isForbiddenUrl(config.url)) {
        config.rid = uuidv4({
          rng: makeRng,
        })

        this.httpTracker.track(config.rid, {
          method: config.method,
          matchedPath: config.matchedPath,
          traceId: config.headers[TRACE_HEADER_NAME],
        })
      }

      return config
    })

    // response
    httpClient.interceptors.response.use((data, res = {}, newConfig) => {
      // TODO: newConfig 是 http@1.4 新增的，标准化 interceptor 获取 config
      const config = newConfig ?? res.config ?? {}

      if (isForbiddenUrl(config.url)) {
        return data
      }

      const request = res.request || {}
      const measure = {
        status: res.status,
        url: request.responseURL,
      }
      const payload = this.measure(config.rid, measure)
      // 增加url字段
      payload.meta.url = config.url

      // 设置接口等级
      if (config.level) {
        payload.meta.level = config.level
      }

      // 响应体
      if (enableResponseData === true) {
        if (data && Object.prototype.toString.call(data) === '[object FormData]') {
          const formObj = {}
          for (let pair of data.entries()) {
            formObj[pair[0]] = pair[1]
          }
          payload.meta.data = JSON.stringify(formObj)
        } else if (data) {
          payload.meta.data = JSON.stringify(data)
        }
      }

      // TODO: 和 http 里的逻辑重复
      if (data.success !== undefined && Boolean(data.success) === false) {
        payload.meta.errorType = 'HTTPBizError'
        // 存放数字类型的 biz error code
        payload.meta.errorCode = data.code
        // 错误信息
        payload.meta.errorMsg = data.msg
      }
      
      callback(payload)

      return data
    }, (err, res = {}, newConfig) => { // eslint-disable-line
      // FIXME: res maybe null, should use undefined
      const config = newConfig ?? (res ? res.config : {}) ?? {}

      const payload = {
        meta: {
          method: config.method,
          matchedPath: config.matchedPath,
          status: err.status || err.statusCode || 0,
          errorType: err.name,
          traceId: config.headers[TRACE_HEADER_NAME],
          url: config.url,
          // 错误信息
          errorMsg: err.message
        },
        measure: {
          entryType: 'measure',
          name: serializeParams(config.url, config.params ?? {}),
        },
      }

      // 设置接口等级
      if (config.level) {
        payload.meta.level = config.level
      }

      if(['HTTPClientError', 'HTTPServerError'].includes(err.name)) {

        if (config.params && Object.keys(config.params).length > 0 && isFunc(httpClient.getUri)) {
          payload.meta.url = httpClient.getUri(config)
        }
        
        if (config.data && Object.prototype.toString.call(config.data) === '[object FormData]') {
          const formObj = {}
          for (let pair of config.data.entries()) {
            formObj[pair[0]] = pair[1]
          }
          payload.meta.data = JSON.stringify(formObj)
        } else if (config.data) {
          payload.meta.data = JSON.stringify(config.data)
        }

        if(err.name === 'HTTPClientError') {
          // 存放字符串类型的 client error code
          if(err.code && err.code!=='N/A') {
            payload.meta.code = err.code
          }
        } else if(err.name === 'HTTPServerError'){
          // 存放数字类型的 server error code
          if(err.code && err.code!=='N/A') {
            payload.meta.errorCode = err.code
          }
        }

        callback(this.responseCatchHook(payload, err, config))
      }
      
      throw err
    })
  }
}

export default new BrowserHttpRequestTiming()
