/**
 * 头像尺寸范围
 */
export enum SizeRangeEnum {
  /**
     * 未指定尺寸
     */
  Auto = 'size-auto',
  /**
     * 小于20
     */
  S = 'size-s',
  /**
     *  介于19-28
     */
  M = 'size-m',
  /**
     *  介于27-40
     */
  L = 'size-l',
  /**
     *  超过 40
     */
  XL = 'size-xl',
}

enum SizeThresholdEnum {
  S = 32,
  M = 44,
  L = 52,
}

const getSizeInfo = (size: number | undefined) : {
  classNameSize: string
  styleSize: null | { width: string}
} => {
  const numberSize = Number(size)

  // 没有指定 size 或者不是数字图片自适应
  const auto = !numberSize

  let classNameSize = ''

  // 用 className 区分头像尺寸范围
  if (auto) {
    classNameSize = SizeRangeEnum.Auto
  } else if (numberSize <= SizeThresholdEnum.S) {
    classNameSize = SizeRangeEnum.S
  } else if (numberSize <= SizeThresholdEnum.M) {
    classNameSize = SizeRangeEnum.M
  } else if (numberSize <= SizeThresholdEnum.L) {
    classNameSize = SizeRangeEnum.L
  } else {
    classNameSize = SizeRangeEnum.XL
  }

  // 容器样式
  const styleSize = auto ? null : { width: `${size}px` }

  return {
    classNameSize,
    styleSize,
  }
}

export default getSizeInfo
