export function getDeviceInfo() {
  function imeiType() {
    if (appVersionLt('5.24')) {
      return {}
    }

    return commonChecks.isIOS
      ? { idfa: T.string.isRequired, idfv: T.string.isRequired }
      : { imei: T.string.isRequired }
  }

  const spec = {
    resT: T.shape({
      result: resultType,
      appMarket: T.string.isRequired,
      appVersion: T.string.isRequired,
      buildNumber: T.string.isRequired,
      systemVersion: T.string.isRequired,
      deviceModel: T.string.isRequired,
      manufacturer: T.string.isRequired,
      timezone: T.string.isRequired,
      deviceScreenWidth: T.number.isRequired,
      deviceScreenHeight: T.number.isRequired,
      carrier: T.string.isRequired,
      uniqueId: T.string.isRequired,
      ...commonChecks.isIOS
        ? { applepayInfo: T.bool.isRequired }
        : {},
      ...imeiType,
    }),
  }

  return adapter('getDeviceInfo').then(res => {
    const newRes = toJsStyle(res)

    if (res.applepay_info) {
      newRes.applepay_info = res.applepay_info
      warnDeprecated('res.applepay_info', 'res.applepayInfo')
    }

    checkRes(newRes, spec.resT)
    return newRes
  })
}

export function getNetworkType(config = { extractValue: false }) {
  const spec = {
    resT: T.shape({
      result: resultType,
      value: T.oneOf(['WiFi', '4G', '3G+', '3G', '2G', 'NONE']),
    }),
  }

  return adapter('getNetworkType').then(res => {
    const newRes = { ...res }

    // FIXME: 兼容 iOS 返回的 'wifi'；兼容 'WIFI'
    if (res.value === 'wifi' || res.value === 'WIFI') {
      newRes.value = 'WiFi'
    }

    checkRes(newRes, spec.resT)

    if (config.extractValue) {
      if (newRes.result === 0) {
        return newRes.value
      }

      throw new BridgeError(`ERR_RESULT_${newRes.result}`, 'res.result is not 0')
    }
    return newRes
  })
}

export function getUserInfo(config = { extractValue: false }) {
  const supportHashExp = (
    commonChecks.isAndroid && appVersionGte('6.5')
  ) || (
    commonChecks.isIOS && appVersionGte('6.9')
  )

  const spec = {
    resT: T.shape({
      result: resultType,
      response: T.shape({
        userId: T.string.isRequired,
        nickname: T.string.isRequired,
        gender: T.number.isRequired,
        userToken: T.string.isRequired,
        image: urlType.isRequired,
        location: T.string.isRequired,
        flags: T.shape({
          fulishe: T.oneOfType([T.object]).isRequired,
          shequ: T.oneOfType([T.object]).isRequired,
        }),
        ...appVersionGte('5.24') ? {
          lat: T.number.isRequired,
          lon: T.number.isRequired,
          sessionId: T.string.isRequired,
        } : {},
        ...(supportHashExp ? {
          hashExp: T.string.isRequired,
        } : {}),
      }),
    }),
  }

  return adapter('getUserInfo').then(res => {
    const newRes = toJsStyle(res)

    if (res.response) {
      // flags 不处理 underscoreCase，因为 flag 是实验的 key，由开发者自行设置，且其消费
      // 该值时一般是通过框架，将 flag 作为字符串传入即可，不做转换就不需要额外的认知负担
      if (res.response.flags) {
        if (res.response.flags.fulishe) {
          newRes.response.flags.fulishe = res.response.flags.fulishe
        }

        if (res.response.flags.shequ) {
          newRes.response.flags.shequ = res.response.flags.shequ
        }
      }

      if (res.response.user_id) {
        newRes.response.user_id = res.response.user_id
        warnDeprecated('res.response.user_id', 'res.response.userId')
      }

      if (res.response.user_token) {
        newRes.response.user_token = res.response.user_token
        warnDeprecated('res.response.user_token', 'res.response.userToken')
      }

      if (res.response.session_id) {
        newRes.response.session_id = res.response.session_id
        warnDeprecated('res.response.session_id', 'res.response.sessionId')
      }

      if (res.response.images && !res.response.image) {
        newRes.response.image = res.response.images
        warnDeprecated('res.response.images', 'res.response.image')
      }
    }

    checkRes(newRes, spec.resT)

    if (config.extractValue) {
      if (newRes.result === 0) {
        return newRes.response
      }

      throw new BridgeError(`ERR_RESULT_${newRes.result}`, 'res.result is not 0')
    }
    return newRes
  })
}

export function getSession() {
  const spec = {
    resT: T.string.isRequired,
  }

  if (appVersionGte('5.26')) {
    return getUserInfo({ extractValue: true })
      .then(user => {
        checkRes(user.sessionId, spec.resT)

        return user.sessionId
      })
  }

  return adapter('getSession', spec)
}

