/**
 * 用于多 Web 页面间的通信
 * 如：订单支付页切换地址
 * 1. B 页面 registerNotice
 * 2. A 页面 broadcast data 给 App
 * 3. App 将 data 发送给 B 页面
 */
// FIXME: registerNotice should be a event rather than a bridge
export function registerNoticeLegacy(callback) {
  return callbackAdapter('registerNotice', undefined, callback)
}

export function registerNotice(callback) {
  warnDeprecated('registerNotice', 'subscribe(\'notice\', () => {})')

  return registerNoticeLegacy(callback)
}

