export async function getFileUrlFromLocalServerIOS(data) {
  const spec = {
    argsT: T.shape({
      url: urlType.isRequired,
    }),
    resT: T.shape({
      result: T.oneOf([0, -1]).isRequired,
      value: urlType,
      message: T.string,
    }),
  }

  const res = await adapter('getFileUrlFromLocalServer', data, spec)

  if (res.result === 0) {
    return res.value
  }

  throw new BridgeError(`ERR_RESULT_${res.result}`, res.message)
}

export async function toggleLocalDns(enable) {
  const res = await adapter('toggleLocalDns', {
    enable,
  })

  if (res.result === 0) {
    return
  }

  throw new BridgeError(`ERR_RESULT_${res.result}`, res.message)
}
