<template>
  <RedsImage
    class="reds-avatar"
    :class="sizeInfo.classNameSize"
    :style="sizeInfo.styleSize"
    :width="size || 48"
    :height="size || 48"
    :aspect-ratio="1"
    :ratio="ratio"
    :clip-config="clipConfig"
    v-bind="$attrs"
  >
    <i class="reds-avatar-border" />
    <slot />
  </RedsImage>
</template>

<script lang="ts">
  import { defineComponent, computed } from 'vue'
  import RedsImage from '../Image/Image.vue'
  import RedsAvatarOutline from './subcomps/avatar-outline.vue'
  import RedsAvatarLive from './subcomps/avatar-live.vue'
  import PropsDefine from './avatar.props'
  import getSizeInfo from './utils/getSizeInfo'
  import '@xhs/reds-h5/style/avatar/avatar.styl'

  const Avatar = defineComponent({
    name: 'RedsAvatar',
    components: {
      RedsImage,
    },
    props: PropsDefine,
    setup(props) {
      const sizeInfo = computed(() => getSizeInfo(props.size))
      return {
        sizeInfo,
        size: props.size || 48,
      }
    },
  })

  Avatar.RedsAvatarOutline = RedsAvatarOutline
  Avatar.RedsAvatarLive = RedsAvatarLive

  export default Avatar

</script>
