/* eslint-disable no-console, no-underscore-dangle */
import { showApmTrack } from '@xhs/ozone-bridge'
import EmitterBase from '@xhs/eaglet-emitter-base'
// eslint-disable-next-line import/no-unresolved

import { checkTrackEnv } from '../helpers/get-track-env'
import { sendByDefault, sendToXray } from './client'
import requestCombo, { sendToXrayByFetch, destroy as requestComboDestroy } from '../helpers/requestCombo'
import { APM_XRAY_TRACKER, APM_XRAY_PROTO_TRACKER } from '../const'

import type { IApmXrayTrackerExtendDataPayload, ITrackerExtendDataPayload } from '../types/plugin'
import type {
  IJspb, ISerializeBinary, IMessage, IOptions,
} from '../types/emitter/apm-emitter'

let isNativeDebugOn: boolean

const showTrackerMessage = (payload: unknown) => {
  const content = JSON.stringify(payload)

  showApmTrack({ content }).catch(() => { /* No-op */ })
}

export default class ApmEmitter extends EmitterBase {
  localDebug?: boolean

  preferNative?: boolean | ((params: (flagName: string | string[]) => Promise<unknown>) => Promise<boolean>) | Promise<boolean>

  enableBatchRequest?: boolean

  apmXrayProtoTrackerEndPoint?: {
    development?: string
    production?: string
  }

  apmXrayTrackerEndPoint?: {
    development?: string
    production?: string
  }

  constructor(options: IOptions) {
    super(options)
    this.localDebug = options.debug
    this.preferNative = options.preferNative
    this.enableBatchRequest = options.enableBatchRequest
    this.apmXrayTrackerEndPoint = options.apmXrayTrackerEndPoint
    this.apmXrayProtoTrackerEndPoint = options.apmXrayProtoTrackerEndPoint
  }

  shouldFlushBuffer() {
    return true
  }

  serializeBinary(message: (IApmXrayTrackerExtendDataPayload | ITrackerExtendDataPayload) & ISerializeBinary, BinaryWriter: IJspb['BinaryWriter']) {
    const writer = new BinaryWriter()

    const messageArr = message.serializeBinary()
    writer.encoder_.writeSignedVarint64(messageArr.byteLength + 4)
    writer.encoder_.writeInt32(this.versionHash)
    writer.encoder_.writeBytes(messageArr)

    const ret = writer.getResultBase64String()

    writer.reset()

    return ret
  }

  // 性能数据上报
  sendLocalMetric(messageForDebug: ITrackerExtendDataPayload) {
    if (this.localDebug) {
      let metric: string | string[]

      if (messageForDebug.apmLog) {
        metric = 'apmLog'
      }

      if (messageForDebug.browserApmTracker) {
        metric = Object.keys(messageForDebug.browserApmTracker)
          .filter((key: string) => {
            const value = (messageForDebug.browserApmTracker as ITrackerExtendDataPayload)[key as keyof ITrackerExtendDataPayload['browserApmTracker']]
            if (Array.isArray(value)) {
              return value.length > 0
            }

            return value
          })
      } else if (messageForDebug.frontApmTracker) {
        metric = 'frontApmTracker'
      }

      console.groupCollapsed(`[apm] ${metric!.toString()}`)
      console.log(messageForDebug)
      console.groupEnd()
    }
  }

  logJsonMetric(messageForDebug: ITrackerExtendDataPayload) {
    if (this.localDebug) {
      let metric: string | string[]

      if (messageForDebug.apmLog) {
        metric = 'apmLog'
      }

      if (messageForDebug.browserApmTracker) {
        const browserApmTracker = messageForDebug.browserApmTracker.value
        metric = Object.keys(browserApmTracker)
          .filter(key => {
            const value = browserApmTracker[key as keyof (typeof browserApmTracker)]

            if (Array.isArray(value)) {
              return (value as unknown[]).length > 0
            }
            return value
          })
      } else if (messageForDebug.frontApmTracker) {
        metric = 'frontApmTracker'
      }

      console.groupCollapsed(`[apm] ${metric!.toString()}`)
      console.log(messageForDebug)
      console.groupEnd()
    }
  }

  logXrayMetric(messageForDebug: IApmXrayTrackerExtendDataPayload) {
    if (this.localDebug) {
      const metric = messageForDebug.measurement_name
      console.groupCollapsed(`[apm] ${metric}`)
      console.log(messageForDebug)
      console.groupEnd()
    }
  }

  // apm endpoint 不返回 tracker 结构，需要传递 messageForDebug
  sendData(dataString: string | IMessage, messageForDebug: ITrackerExtendDataPayload) {
    const sendType = typeof dataString === 'string' ? 'proto' : 'json'
    if (process.env.BUILD_ENV !== 'production') {
      if (sendType === 'proto') {
        this.sendLocalMetric(messageForDebug)
        return
      }
    }

    let remoteUrl = (isNativeDebugOn || this.localDebug)
      ? this.endpoint.development
      : this.endpoint.production

    if (sendType === 'proto' && messageForDebug.frontApmTracker) {
      remoteUrl = this.apmXrayProtoTrackerEndPoint?.production || APM_XRAY_PROTO_TRACKER.ENDPOINT.production
    }
    let preferNative = this.preferNative
    if (sendType === 'json') {
      preferNative = false
    }
    sendByDefault(remoteUrl, dataString, {
      preferNative,
      apm: true,
    })
      .then(res => {
        if (!res) {
          return
        }
        if (isNativeDebugOn) {
          showTrackerMessage(res)
        }

        // this.sendLocalMetric(messageForDebug)
        if (sendType === 'proto') {
          this.sendLocalMetric(messageForDebug)
        } else {
          this.logJsonMetric(messageForDebug)
        }
      })
      .catch((err: {
        detail: unknown
      }) => {
        if (isNativeDebugOn) {
          showTrackerMessage(err)
        }

        if (this.localDebug) {
          console.group('[apm error]')
          console.error(err.detail || err)
          console.groupEnd()
        }
      })
  }

  flush() {
    if (!this.buffer.length) {
      return
    }
    if (this.enableBatchRequest) {
      checkTrackEnv.then(nativeDebugOn => {
        const remoteUrl = (nativeDebugOn || this.localDebug)
          ? this.endpoint.development
          : this.endpoint.production
        const xrayRemoteUrl = (nativeDebugOn || this.localDebug)
          ? (this.apmXrayTrackerEndPoint?.development || APM_XRAY_TRACKER.ENDPOINT.development)
          : (this.apmXrayTrackerEndPoint?.production || APM_XRAY_TRACKER.ENDPOINT.production)
        while (this.buffer.length) {
          const message = this.buffer.shift() as (IApmXrayTrackerExtendDataPayload | ITrackerExtendDataPayload)
          if ((message as IApmXrayTrackerExtendDataPayload).measurement_name) {
            requestCombo(xrayRemoteUrl, message, {
              request: sendToXray,
              callback: (data?: IApmXrayTrackerExtendDataPayload[] | ITrackerExtendDataPayload[]) => {
                if (data?.length) {
                  data.forEach(message => {
                    this.logXrayMetric(message)
                  })
                }
              },
            })
          } else {
            requestCombo(remoteUrl, message as (IApmXrayTrackerExtendDataPayload | ITrackerExtendDataPayload), {
              callback: (data?: IApmXrayTrackerExtendDataPayload[] | ITrackerExtendDataPayload[]) => {
                if (data?.length) {
                  data.forEach(message => {
                    this.logJsonMetric(message)
                  })
                }
              },
            })
          }
        }
      })
      return
    }

    // @ts-ignore reason: 动态导入的 google-protobuf 文件不进行类型声明检查
    const tasks: Promise<IJspb | boolean>[] = [import('google-protobuf'), checkTrackEnv]
    Promise.all(tasks).then(([jspb, nativeDebugOn]) => {
      isNativeDebugOn = nativeDebugOn as boolean

      while (this.buffer.length) {
        const message = this.buffer.shift() as IMessage
        if (typeof message.toObject === 'function') {
          this.sendData(this.serializeBinary(message, (jspb as IJspb).BinaryWriter), message.toObject())
        } else {
          this.sendData(message, message)
        }
      }
    })
  }

  flushApm(message: IApmXrayTrackerExtendDataPayload) {
    checkTrackEnv.then(nativeDebugOn => {
      const xrayRemoteUrl = (nativeDebugOn || this.localDebug)
        ? (this.apmXrayTrackerEndPoint?.development || APM_XRAY_TRACKER.ENDPOINT.development)
        : (this.apmXrayTrackerEndPoint?.production || APM_XRAY_TRACKER.ENDPOINT.production)
      if (message.measurement_name) {
        sendToXrayByFetch(xrayRemoteUrl, message)
      }
    })
  }

  destroy() {
    requestComboDestroy()
  }
}
