import { v4 as uuidv4 } from 'uuid'
import uniqueId from 'lodash.uniqueid'
import { DEFAULT_TRACKER } from '../const'
import { objectHasKey } from '../utils/objectHasKey'

import type {
  ITrackerExtendDataPayload,
  ITrackerName,
  ITrackerContent,
} from '../types/plugin'

// 应用会话 sessionId, 仅在 sdk 初始化更新
const getSdkSessionId = () => {
  const sessionId = window?.SDK__SESSION__ID || uuidv4()
  if (!window.SDK__SESSION__ID) {
    window.SDK__SESSION__ID = sessionId
  }
  return sessionId
}
const sdkSessionId = getSdkSessionId()
const addEventInfo = (payload: ITrackerExtendDataPayload = {}, trackerName: ITrackerName, builder: ITrackerContent['builder']) => {
  if (payload.event?.value) {
    payload.event.value.eventId = uuidv4()
    payload.event.value.dvceMicroTs = Date.now() * 1000
  } else {
    payload.event = {
      type: 'Event',
      value: {
        eventId: uuidv4(),
        dvceMicroTs: Date.now() * 1000,
      },
    }
  }

  if (trackerName === DEFAULT_TRACKER.NAME) {
    payload.event.value.requestId = uniqueId()
    // sdkSessionId 字段防御性写入
    const isSupportSdkSessionId = objectHasKey(builder, 'Event.prototype.getSdkSessionId') && objectHasKey(builder, 'Event.prototype.setSdkSessionId')
    if (isSupportSdkSessionId) {
      payload.event.value.sdkSessionId = sdkSessionId
    }
  }

  return payload
}

export default addEventInfo
