export function showNavigationRightBarButtonItem(config) {
    const spec = {
        argsT: T.shape({
            visible: T.bool,
            buttonTitle: T.string,
            buttonIcon: T.string,
            handler: T.func,
        }),
        resT: T.shape({
            result: resultType,
        }),
    };
    if (config.button_title) {
        warnDeprecated('args.button_title', 'args.buttonTitle');
    }
    if (config.button_icon) {
        warnDeprecated('args.button_icon', 'args.buttonIcon');
    }
    checkArgs(config, spec.argsT, 'showNavigationRightBarButtonItem');
    const { handler, ...other } = config;
    const newConfig = toPythonStyle(other);
    // FIXME: use standard callback
    if (handler && typeof handler === 'function') {
        window.XHSHandler = {
            ...window.XHSHandler,
            navigationRightBarButtonItem: handler,
        };
    }
    return adapter('showNavigationRightBarButtonItemV2', newConfig, { resT: spec.resT });
}
