/**
 * 分享
 */
// FIXME: 不传参数表示隐藏分享按钮
export function setShareInfo(data) {
    const spec = {
        argsT: T.shape({
            contentType: T.string,
            title: T.string,
            content: T.string,
            // @ts-ignore
            linkurl: urlType,
            // @ts-ignore
            imageurl: urlType,
            type: T.string,
            extension: T.shape({
                miniprogram: T.shape({
                    title: T.string,
                    desc: T.string,
                    // @ts-ignore
                    webpageurl: urlType,
                    path: T.string,
                    thumb: T.string,
                    username: T.string,
                }),
                friend: T.oneOfType([
                    T.string,
                    T.shape({
                        source: T.string,
                        type: T.oneOf([
                            'general',
                            'goodsDetail',
                            'center',
                            'topic',
                            'event',
                        ]),
                        title: T.string,
                        content: T.string,
                        cover: T.string,
                        image: T.string,
                        link: T.string,
                        btn: T.string,
                        avatar: T.string,
                        brandName: T.string,
                        id: T.string,
                        desc: T.string,
                        price: T.number,
                    }),
                ]),
            }),
        }),
        resT: T.shape({
            result: resultType,
        }),
    };
    let newData = data;
    // data maybe undefined
    if (newData) {
        newData = { ...newData };
        if (newData.type && !canShareMp) {
            newData.type = newData.type.replace(/WXMiniProgram/, 'WeixinSession');
        }
        if (newData.image && !newData.imageurl) {
            warnDeprecated('args.image', 'args.imageurl');
            newData.imageurl = newData.image;
        }
        // handle friend data
        const friend = arrangeExtensionFriend(newData.extension?.friend);
        if (friend) {
            newData.extension.friend = friend;
        }
    }
    if (commonChecks.isMiniprogram) {
        return setShareInfoMp(newData);
    }
    return adapter('setShareInfo', newData, spec);
}
// 内容来自 setShareInfo
