// 移除当前 ViewController
export function removeVCFromStackIOS() {
    return adapter('removeVCFromStack');
}
export function replaceSelfWithLink({ link, type, }) {
    const supportTypeIOS = commonChecks.isIOS && appVersionGte('5.39');
    const spec = {
        argsT: supportTypeIOS
            ? { link: T.string.isRequired, type: T.oneOf(['push', 'present']).isRequired }
            : T.string.isRequired,
    };
    if (supportTypeIOS) {
        return adapter('replaceSelfWithLink', { link, type }, spec);
    }
    return adapter('replaceSelfWithLink', link, spec);
}
// Android 关闭前一个页面时，会处理 H5 路由，而 iOS 不会，V2 和 iOS 表现一致
export function replaceSelfWithLinkV2Android(link) {
    return adapter('replaceSelfWithLinkV2', { link });
}
export function replaceRouteWith(newLink) {
    if (commonChecks.isIOS && appVersionGte('6.54')) {
        return replaceSelfWithLink({ link: newLink });
    }
    if (commonChecks.isAndroid && appVersionGte('6.56')) {
        return replaceSelfWithLinkV2Android(newLink);
    }
    if (commonChecks.isIOS && !isSupport('removeVCFromStack')) {
        return replaceSelfWithLink({ link: newLink });
    }
    return new Promise((resolve, reject) => {
        window.open(newLink);
        // @ts-ignore
        setTimeout(() => {
            if (commonChecks.isIOS) {
                removeVCFromStackIOS()
                    .then(resolve)
                    .catch(reject);
            }
            else {
                window.close();
                resolve(undefined);
            }
        }, 300);
    });
}
