import { commonChecks } from '@xhs/ozone-detector'
import { warnDeprecated } from './logDeprecated'

export function arrangeExtensionFriend(friend) {
  let result

  if (typeof friend === 'string') {
    warnDeprecated('args.extension.friend String', 'args.extension.friend Object')
  } else if (typeof friend === 'object') {
    if (friend.cover && !friend.image) {
      warnDeprecated('args.extension.friend.cover', 'args.extension.friend.image')

      friend.image = friend.cover
      result = { ...friend }
    }

    // android only support freind as string
    if (commonChecks.isAndroid) {
      result = JSON.stringify(friend)
    }
  }

  return result
}
