function getNetTypeFromUA(UA) {
  let networkType = 'Unrecognized'

  if (!process.env.BROWSER) {
    return networkType
  }

  const NETTYPE_REGEX = /\s?(?:NetType|nt:)\/?(\w+)\s?/g
  const SUPPORT_TYPES = ['2g', '3g', '4g', 'wifi', 'cellnetwork']
  const ua = UA || window.navigator.userAgent

  ua.replace(NETTYPE_REGEX, (match, type) => {
    if (SUPPORT_TYPES.indexOf(type.toLowerCase()) >= 0) {
      networkType = type
    }
  })

  return networkType.toLowerCase()
}

export function getNetType(ua) {
  /*
   * we should add webview env detect here:
   * if in wechat, we could use wechat js sdk;
   * if in XHS, we could use jsbridge
   * otherwise, use ua
   */

  return getNetTypeFromUA(ua)
}
