import { RouteLocationNormalizedLoaded } from 'vue-router'
import { objectHasKey } from '../utils/objectHasKey'

import type { ITrackerExtendDataPayload, ITrackerContent } from '../types/plugin'

let browserSource = ''
function addBrowserSource(payload: ITrackerExtendDataPayload, builder: ITrackerContent['builder']) {
  if (!payload || !builder) return
  const hasSource = objectHasKey(builder, 'Browser.prototype.setSource') && objectHasKey(builder, 'Browser.prototype.getSource')
  if (hasSource && typeof payload.browser === 'object') {
    payload.browser.value.source = browserSource
  }
}
function updateBrowserSource(route: RouteLocationNormalizedLoaded) {
  if (!route) return null
  const { urlSource } = route.meta
  if (urlSource === true) {
    // 兼容 source 为多个的情况
    browserSource = String(Array.isArray(route.query.source) ? route.query.source[0] : route.query.source || '')
  } else if (typeof urlSource === 'function') {
    browserSource = String(urlSource() || '')
  } else {
    browserSource = ''
  }
}
export { addBrowserSource, updateBrowserSource }
