export function getHost() {
  let burdockHost = '//www.sit.xiaohongshu.com'


  if (window.location.host.indexOf('sit.xiaohongshu.com') === -1) {
    burdockHost = '//www.xiaohongshu.com'
  }


  return burdockHost
}

const CHARSET = 'abcdefghijklmnopqrstuvwxyz1234567890'

export function genRandomString(length) {
  return Array(length).fill(undefined).map(()=>CHARSET[Math.floor(Math.random()*36)]).join('')
}

