import {
  error,
} from '@xhs/logger'

// if the service is not implemented, it will has a needImplmement flag,
// which defined on auth/index.js
const hasImplemented = service => (
  typeof service === 'function' && !service.needImplement
)

const KNOWN_TOKEN_PROPS = ['auth_token', 'token']

const extractAuthToken = res => {
  let token = null

  if (typeof res === 'string') {
    return res
  }

  if (res) {
    for (const key in res) {
      if (KNOWN_TOKEN_PROPS.indexOf(key) > -1) {
        token = res[key]
        break
      }
    }
  }

  if (!token) {
    error(
      '[Launcher Auth Exception] Could not found auth token from login() returining data',
    )

    token = 'not_found_from_response'
  }

  return token
}

/* ------------------------------------- */

const wrapLogin = (login, authServices, store, options) => (...args) => {
  const loginResult = Promise.resolve(login(...args))

  return loginResult.then(res => {
    // if setAuthToken implemented, store it (e.g. localStorage)
    if (hasImplemented(authServices.setAuthToken)) {
      authServices.setAuthToken(
        extractAuthToken(res),
      )
    }

    return res
  })
    .then(res => {
      if (options.loginReturnUserInfo) {
        store.dispatch('setUserInfo', res.userInfo)
      } else if (hasImplemented(authServices.fetchUserInfo)) {
        return authServices.fetchUserInfo().then(() => res)
      }
      return res
    })
    .catch(err => {
      if (hasImplemented(authServices.resetAuthToken)) {
        authServices.resetAuthToken()
      }

      store.dispatch('resetUserInfo')
      throw err
    })
}

const wrapLogout = (logout, authServices, store) => (...args) => {
  const logoutResult = logout(...args)

  return logoutResult.then(res => {
    if (hasImplemented(authServices.resetAuthToken)) {
      authServices.resetAuthToken()
    }

    store.dispatch('resetUserInfo')

    return res
  })
}

const wrapFetchUserInfo = (getUserInfo, authServices, store) => (...args) => {
  const getUserResult = Promise.resolve(getUserInfo(...args))

  return getUserResult.then(userInfo => {
    store.dispatch('setUserInfo', userInfo)
    return userInfo
  })
}

/* ------------------------------------- */

export default function serviceModule(Launcher, options) {
  const authContext = this.vm.config.globalProperties.$auth
  const authServices = authContext.services
  const optionsServices = options.services || {}
  const store = this.store // context is current app

  if (optionsServices.login) {
    authServices.login = wrapLogin(
      optionsServices.login, authServices, store, options,
    )
  }

  if (optionsServices.logout) {
    authServices.logout = wrapLogout(
      optionsServices.logout, authServices, store, options,
    )
  }

  if (optionsServices.getUserInfo) {
    // options is getUserInfo, but service is "fetchUserInfo", since it will set to store
    authServices.fetchUserInfo = wrapFetchUserInfo(
      optionsServices.getUserInfo, authServices, store, options,
    )
  }

  // Bind $auth
  authContext.login = authServices.login
  authContext.logout = authServices.logout
  authContext.fetchUserInfo = authServices.fetchUserInfo
}
