import { commonChecks} from '@xhs/ozone-detector'
import { RISK_ERROR_CODE_MAP } from '../const'

export default async response => {
  if (commonChecks.isXHS) return response
  if (response?.status !== 200) return response

  const { code, result, message, msg }  = response.data || {} // burdock 和 jarvis 网关的返回数据格式不同
  if (code === undefined && result === undefined) return response

  let errorCode, errorMessage 
  if (code !== undefined && result === undefined) {
    errorCode = code
    errorMessage = message
  } else if (result !== undefined && code === undefined) {
    errorCode = result
    errorMessage = msg
  } else {
    return response
  }
  if(Object.keys(RISK_ERROR_CODE_MAP).includes(String(errorCode))){
    window.location.href = `https://www.xiaohongshu.com/website-login/error?redirectPath=${window.location.href}&error_code=${errorCode}&error_msg=${RISK_ERROR_CODE_MAP[errorCode]}`
  }
    
  return response
}