<template>
  <div
    class="reds-progress-indicator"
    v-bind="$attrs"
  >
    <div
      v-if="type === 'assetLoading' && percentage"
      class="reds-progress-indicator__media"
    >
      <div
        class="reds-progress-indicator__media-box"
        :style="[{ 'width': size+'px','height': size+'px'}]"
      >
        <svg
          :width="size"
          :height="size"
          class="reds-progress-indicator__media-box-circle"
        >
          <circle
            :r="radius"
            :cx="cx"
            :cy="cy"
            fill="transparent"
            :stroke-width="strokeWidth"
            class="reds-progress-indicator__media-box-circle--inactive"
          />
          <circle
            :r="radius"
            :cx="cx"
            :cy="cy"
            fill="transparent"
            :stroke-width="strokeWidth"
            stroke-linecap="round"
            :stroke-dasharray="circumference"
            :stroke-dashoffset="progress"
            :stroke="stroke"
          />
        </svg>
        <span
          v-if="showPercentage"
          class="reds-progress-indicator__media-box__text"
        >{{ countDown }}%</span>
      </div>
    </div>
    <div
      v-if="type === 'assetLoading' && !percentage"
      class="reds-progress-indicator__media-spinner"
    >
      <div
        v-if="$slots.spinner"
        class="reds-progress-indicator__media-spinner__icon"
      >
        <slot name="spinner" />
      </div>
      <Icon
        v-else
        :icon="LoadingIcon"
        class="reds-progress-indicator__media-spinner__icon"
        :style="{'--assetLoadingColor': color === 'ThemeTertiaryLabel' ? 'ThemeLabel' : stroke}"
      />
    </div>
    <div
      v-if="type === 'activityIndicator'"
      ref="redsActivityIndicator"
      class="reds-progress-indicator__activity-indicator"
    />
    <div
      v-if="type === 'pageLoading'"
      class="reds-progress-indicator__page-loading"
      :class="showBackground ? 'has-background' : ''"
    >
      <div
        ref="redsPageLoading"
        class="reds-progress-indicator__page-loading__spinner"
      />
    </div>
  </div>
</template>

<script setup lang="ts">
  import {
    withDefaults, ref, computed, onMounted, inject, ComputedRef,
  } from 'vue'
  import { colors } from '@xhs/reds-h5-style/src'
  import { getTargetDom } from '../../composables/getTargetDom'
  import { ProgressIndicatorType } from './interface'
  import Icon from '../Icon/Icon.vue'
  import LoadingIcon from '../Icon/assets/loading_mini.svg'
  import { SystemLoadingLight } from './assets/system_loading_light'
  import { SystemLoadingDark } from './assets/system_loading_dark'
  import { LoadingLight } from './assets/loading_light_mode'
  import { LoadingDark } from './assets/loading_dark_mode'
  import { LoadingRed } from './assets/loading_red'
  import { LoadingAlwaysWhite } from './assets/loading_always_white'
  import '@xhs/reds-h5/style/progressIndicator.styl'
  import { getMobileOS } from '../../utils/common'

  const props = withDefaults(
    defineProps<{
      type?: ProgressIndicatorType
      percentage?: number
      showPercentage?: boolean
      color?: string
      showBackground?: boolean
      duration?: number
      disableThemeChange?: boolean
    }>(),
    {
      type: 'assetLoading',
      percentage: 0,
      showPercentage: false,
      color: 'ThemeTertiaryLabel',
      showBackground: false,
      duration: 1500,
      disableThemeChange: false,
    },
  )

  // 主题色，是dark mode 还是 light mode
  const redsTheme: ComputedRef<string> | (() => { value: string }) = inject('redsTheme', () => ({ value: '' }))

  const lessThenIos14 = computed(() => {
    const { version, systemName } = getMobileOS()

    if (systemName === 'iOS' && +version < 14) {
      return true
    }

    return false
  })

  /**
   * Asset Loading 进度条
   */
  // 尺寸固定，不能定制
  const size = ref(40)
  const now = ref(0)
  const strokeWidth = ref(2)
  const stroke = computed(() => colors[props.color])
  const countDown = computed(() => now.value)
  // 圆心 x 轴坐标
  const cx = computed(() => size.value / 2)
  // 圆心 y 轴坐标
  const cy = computed(() => size.value / 2)
  // 半径
  const radius = computed(() => (size.value - strokeWidth.value) / 2)
  // 圆周长
  const circumference = computed(() => 2 * Math.PI * radius.value)
  // 进度长度
  const progress = computed(() => (1 - now.value / 100) * circumference.value)

  const runPercentageAnimation = () => {
    if (props.percentage === 0) return
    const t = props.duration / props.percentage
    const timer = setInterval(() => {
      if (now.value >= props.percentage) {
        clearInterval(timer)
      } else {
        now.value += 1
      }
    }, t)
  }

  // cdn img
  const lottieFileFinish = (url: string) => new Promise(resolve => {
    const img = new Image()
    img.onload = () => resolve(img)
    img.src = url
  })

  const lottieFileCDNList = {
    LoadingAlwaysWhite: 'https://picasso-static.xiaohongshu.com/fe-platform/0bee338808794c252105f863b3236e1ee0e52049.webp',
    LoadingLight: 'https://picasso-static.xiaohongshu.com/fe-platform/f40670a46f8372b62c86c4c5e5ed9f72d66fe566.webp',
    LoadingDark: 'https://picasso-static.xiaohongshu.com/fe-platform/46f764d018da95739306543af3cf48d2452acfc5.webp',
    LoadingRed: 'https://picasso-static.xiaohongshu.com/fe-platform/e22734a60d66ae14ba68dbf697a29d2a5af28be8.webp',
    SystemLoadingLight: 'https://picasso-static.xiaohongshu.com/fe-platform/f1bed53a0b83bafafae2895fc54fe850d284e994.webp',
    SystemLoadingDark: 'https://picasso-static.xiaohongshu.com/fe-platform/f2f7d394173c00a7d58913f5e90694359441eb45.webp',
  }

  /**
   * Activity Indicator
   */
  const redsActivityIndicator = ref(null)
  const runLottieAnimation = (lottieFile: string, container: HTMLElement | null, url: string) => {
    if (container) {
      if (lessThenIos14.value) {
        container.innerHTML = `
          <svg 
            width="16px" 
            height="16px" 
            viewBox="0 0 16 16" 
            class="reds-icon reds-progress-indicator__media-spinner__icon" 
            style="--assetLoadingColor: ${props.color === 'ThemeTertiaryLabel' ? 'var(--ThemeLabel)' : stroke.value}">
            <path d="M8 2.4A5.6 5.6 0 1013.6 8a.9.9 0 111.8 0A7.4 7.4 0 118 .6a.9.9 0 010 1.8z" fill="#333"></path>
          </svg>
        `
      } else {
        container.innerHTML = `<img src=${lottieFile} />`
        // 静图切换成动图
        lottieFileFinish(url).then(img => {
          container.innerHTML = ''
          container.appendChild(img)
        })
      }
    }
  }

  /**
   * Page Loading
   */
  const redsPageLoading = ref<HTMLElement | null>(null)
  onMounted(() => {
    if (props.type === 'assetLoading' && props.percentage) {
      runPercentageAnimation()
    }
    if (props.showPercentage) {
      size.value = 48
    }
    if (props.type === 'activityIndicator') {
      if ((redsTheme as any)?.value === 'dark') {
        runLottieAnimation(SystemLoadingDark, redsActivityIndicator.value, lottieFileCDNList.SystemLoadingDark)
      } else {
        runLottieAnimation(SystemLoadingLight, redsActivityIndicator.value, lottieFileCDNList.SystemLoadingLight)
      }
    }
    if (props.type === 'pageLoading' && !props.disableThemeChange) {
      if (props.showBackground) {
        runLottieAnimation(LoadingRed, redsPageLoading.value, lottieFileCDNList.LoadingRed)
      } else if ((redsTheme as any)?.value === 'dark') {
        runLottieAnimation(LoadingDark, redsPageLoading.value, lottieFileCDNList.LoadingDark)
      } else {
        runLottieAnimation(LoadingLight, redsPageLoading.value, lottieFileCDNList.LoadingLight)
      }
      // 颜色不是默认值，修改 pageloading 的颜色
      if (props.color !== 'ThemeTertiaryLabel') {
        const domTarget:Element[] = getTargetDom(redsPageLoading.value, 'stroke')

        // 找到目标 dom 节点
        if (domTarget.length !== 0) {
          domTarget.forEach(item => item.setAttribute('stroke', stroke.value))
        }
      }
    }

    if (props.type === 'pageLoading' && props.disableThemeChange) {
      runLottieAnimation(LoadingAlwaysWhite, redsPageLoading.value, lottieFileCDNList.LoadingAlwaysWhite)
    }
  })

</script>
