/* eslint-disable @typescript-eslint/explicit-module-boundary-types */

export default {
  show: {
    type: Boolean,
  },
  title: {
    type: String,
  },
  width: {
    type: Number,
  },
  message: {
    type: String,
  },
  messageAlign: {
    type: String,
    validator: (value: string) => ['left', 'center'].includes(value),
    default: 'center',
  },
  confirmText: {
    type: String,
  },
  cancelText: {
    type: String,
  },
  footerLayout: {
    type: String,
    validator: (value: string) => ['horizontal', 'vertical'].includes(value),
    default: 'horizontal',
  },
  // 按钮类型
  isBtnRounded: {
    type: Boolean,
    default: false,
  },
  isBtnMono: {
    type: Boolean,
    default: false,
  },
  // 文本内容溢出时，是否需要加透明遮罩
  showScrollOverlay: {
    type: Boolean,
    default: false,
  },
  // 媒体素材类型
  mediaType: {
    type: String,
    validator: (value: string) => ['none', 'cover', 'asset'].includes(value),
    default: 'none',
  },
  mediaSrc: {
    type: String,
    default: '',
  },
  containerClass: {
    type: String,
    default: '',
  },
  lockScroll: {
    type: Boolean,
    default: false,
  },
}
