<template>
  <div
    ref="alertRef"
    class="reds-alert"
    v-bind="$attrs"
    @touchmove.stop.prevent
  >
    <Transition
      name="reds-alert-mask-slide-fade"
      enter-active-class="reds-alert-mask-slide-fade-enter-active"
      leave-active-class="reds-alert-mask-slide-fade-leave-active"
      enter-from-class="reds-alert-mask-slide-fade-enter-from"
      leave-to-class="reds-alert-mask-slide-fade-leave-to"
    >
      <div
        v-show="show"
        class="reds-alert-mask"
        @click="emit('maskClick')"
        @touchmove.stop.prevent
        @scroll.stop.prevent
      >
        <slot name="background" />
      </div>
    </Transition>
    <Transition
      name="reds-alert-slide-fade"
      enter-active-class="reds-alert-slide-fade-enter-active"
      leave-active-class="reds-alert-slide-fade-leave-active"
      enter-from-class="reds-alert-slide-fade-enter-from"
      leave-to-class="reds-alert-slide-fade-leave-to"
      @touchmove.stop.prevent
      @scroll.stop.prevent
    >
      <div
        v-show="show"
        class="reds-alert-wrapper"
        :style="width ? {width: width + 'px'} : {}"
        :class="[
          {
            'has-content': message,
            'slot-content': $slots.default,
            'has-media': mediaType === 'cover' || mediaType === 'asset'
          }
        ]"
        @touchmove.stop.prevent
        @scroll.stop.prevent
        @touchstart.stop
        @touchend.stop
      >
        <slot name="foreground">
          <Image
            v-if="(mediaSrc && mediaType === 'cover')"
            :src="mediaSrc"
            :width="width"
            :height="135"
          />
          <slot name="image" />
          <div
            v-if="(mediaSrc && mediaType === 'asset')"
            class="reds-alert-wrapper__media__asset"
          >
            <Avatar
              :src="mediaSrc"
              :size="48"
            />
          </div>
          <div
            v-if="title || $slots.title"
            class="reds-alert-title"
          >
            <slot name="title" />
            {{ title }}
          </div>
          <div
            v-if="message || $slots.default"
            class="reds-alert-content"
            @touchmove="handleSlotTouchmove"
          >
            <div
              v-if="(showScrollOverlay && !hasScrollToBottom)"
              class="reds-dialog__message__overlay"
            />
            <div
              v-if="message"
              ref="messageRef"
              class="reds-dialog__message"
              :class="[
                {
                  left: messageAlign === 'left'
                }
              ]"
              @scroll="debouncedScroll"
              @touchmove.stop
            >
              {{ message }}
            </div>
            <slot />
          </div>
          <div
            v-if="confirmText || cancelText || $slots.footer"
            class="reds-alert-footer"
            :class="[
              {
                'reds-alert-footer__round-button': isBtnRounded,
                'vertical': footerLayout === 'vertical',
                'mono': isBtnMono,
              }
            ]"
          >
            <slot name="footer" />
            <Button
              v-if="cancelText"
              :type="(footerLayout === 'horizontal' && isBtnRounded) ? 'outlinedGray' : 'text'"
              class="reds-alert-footer__left"
              :size="cancelBtnSize"
              @click="emit('cancel')"
            >{{ cancelText }}</Button>
            <Button
              v-if="confirmText"
              :type="isBtnRounded ? 'primary' : 'text' "
              :size="isBtnRounded ? 'medium' : 'extraLarge' "
              class="reds-alert-footer__right"
              :class="[
                {
                  block: confirmText && !cancelText,
                  rounded: isBtnRounded,
                }
              ]"
              @click="emit('confirm')"
            >{{ confirmText }}</Button>
          </div>
        </slot>
      </div>
    </Transition>
  </div>
</template>

<script setup lang="ts">
  import {
    defineProps, watch, ref, nextTick, computed,
  } from 'vue'
  import debounce from 'lodash.debounce'
  import Button from '../Button/Button.vue'
  import Image from '../Image/Image.vue'
  import Avatar from '../Avatar/Avatar.vue'
  import AlertProps from './props'
  import '@xhs/reds-h5/style/alert.styl'

  const props = defineProps(AlertProps)
  const emit = defineEmits(['cancel', 'confirm', 'maskClick', 'close', 'open'])
  const messageRef = ref(null)
  const alertRef = ref(null)
  const scrollTop = ref(0)
  const hasScrollToBottom = ref(false)

  const onScroll = () => {
    if (messageRef?.value) {
      const ele = messageRef.value as HTMLElement
      if (ele.scrollTop + ele.clientHeight + 30 > ele.scrollHeight) {
        hasScrollToBottom.value = true
      } else {
        hasScrollToBottom.value = false
      }
    }
  }
  const debouncedScroll: any = debounce(onScroll, 100)
  const handleSlotTouchmove = event => {
    event.stopPropagation()
  }

  const handler = (ev: Event) => {
    ev.preventDefault()
    ev.stopImmediatePropagation()
  }

  const cancelBtnSize = computed(() => {
    if (props.isBtnRounded) {
      return props.footerLayout === 'vertical' ? 'small' : 'medium'
    }
    return 'extraLarge'
  })

  watch(() => props.show, (current, prev) => {
    if (prev && !current) {
      emit('close')
      document.body.style.overflow = 'visible'
      nextTick(() => {
        const top = scrollTop.value
        if (!props.lockScroll) {
          if (window.scrollTo) {
            window.scrollTo({ top })
          }
        }
      })
      if (alertRef?.value) {
        let parentNode = (alertRef.value as any).parentNode
        if (props.containerClass && document.getElementsByClassName(props.containerClass)[0]) {
          parentNode = document.getElementsByClassName(props.containerClass)[0]
        }
        if (parentNode) {
          parentNode.style.overflow = 'visible'
          parentNode.removeEventListener('touchmove', handler, false)
        }
      }
    }
    if (!prev && current) {
      emit('open')
      document.body.style.overflow = 'hidden'
      if (alertRef?.value) {
        let parentNode = (alertRef.value as any).parentNode
        scrollTop.value = window.pageYOffset
        if (props.containerClass && document.getElementsByClassName(props.containerClass)[0]) {
          parentNode = document.getElementsByClassName(props.containerClass)[0]
        }
        if (parentNode) {
          scrollTop.value = (parentNode as HTMLElement).scrollTop || window.pageYOffset
          parentNode.style.overflow = 'hidden'
          parentNode.addEventListener('touchmove', handler, false)
        }
      }
    }
  })

</script>
