const MUTATION_TYPE = {
  REGISTER_AUTH_PERMISSION: 'Auth/REGISTER_AUTH_PERMISSION',
  SET_AUTH_PERMISSION_STATE: 'Auth/SET_AUTH_PERMISSION_STATE',
  SET_AUTH_PERMISSION_RES: 'Auth/SET_AUTH_PERMISSION_RES',
  RESET_AUTH_PERMISSION: 'Auth/RESET_AUTH_PERMISSION',

  SET_USER_INFO: 'Auth/SET_USER_INFO',
  RESET_USER_INFO: 'Auth/RESET_USER_INFO',
}

const DEFAULT_USER_INFO_STATE = null

export const defaultAuthStateGenerator = () => ({
  permission: {
    state: {
      isLogin: null,
    },
    res: {
      isLogin: null,
    },
  },
  userInfo: DEFAULT_USER_INFO_STATE,
})

const state = defaultAuthStateGenerator()

const getters = {
  authPermissionState() {
    return state.permission.state
  },
  authPermissionRes() {
    return state.permission.res
  },
  userInfo() {
    return state.userInfo
  },
}

const mutations = {
  [MUTATION_TYPE.REGISTER_AUTH_PERMISSION](state, payload) {
    const permissionNames = Array.isArray(payload) ? payload : [payload]

    permissionNames.forEach(name => {
      state.permission.state[name] = null
      state.permission.res[name] = null
    })
  },
  [MUTATION_TYPE.SET_AUTH_PERMISSION_STATE](state, payload) {
    state.permission.state[payload.key] = payload.value
  },
  [MUTATION_TYPE.SET_AUTH_PERMISSION_RES](state, payload) {
    state.permission.res[payload.key] = payload.value
  },
  [MUTATION_TYPE.RESET_AUTH_PERMISSION](state) {
    const permissionState = state.permission.state
    const permissionRes = state.permission.res

    for (const func in permissionState) {
      if (permissionState.hasOwnProperty(func)) {
        permissionState[func] = null
      }
    }

    for (const func in permissionRes) {
      if (permissionRes.hasOwnProperty(func)) {
        permissionRes[func] = null
      }
    }
  },
  [MUTATION_TYPE.SET_USER_INFO](state, payload) {
    state.userInfo = payload
  },
  [MUTATION_TYPE.RESET_USER_INFO](state) {
    state.userInfo = DEFAULT_USER_INFO_STATE
  },
}

const actions = {
  registerAuthPermission({
    commit,
  }, payload) {
    commit(MUTATION_TYPE.REGISTER_AUTH_PERMISSION, payload)
    return Promise.resolve()
  },
  setAuthPermissionState({
    commit,
  }, payload) {
    commit(MUTATION_TYPE.SET_AUTH_PERMISSION_STATE, {
      key: payload.key,
      value: payload.value,
    })
    return Promise.resolve()
  },
  setAuthPermissionRes({
    commit,
  }, payload) {
    commit(MUTATION_TYPE.SET_AUTH_PERMISSION_RES, {
      key: payload.key,
      value: payload.value,
    })
    return Promise.resolve()
  },
  resetAuthPermission({
    commit,
  }) {
    commit(MUTATION_TYPE.RESET_AUTH_PERMISSION)
    return Promise.resolve()
  },
  setUserInfo({
    commit,
  }, payload) {
    commit(MUTATION_TYPE.SET_USER_INFO, payload)
    return Promise.resolve()
  },
  resetUserInfo({
    commit,
  }) {
    commit(MUTATION_TYPE.RESET_USER_INFO)
    return Promise.resolve()
  },
}

export default {
  modules: {
    state,
    getters,
    actions,
    mutations,
  },
  // 用于微前端场景重复注册store标记
  registered: false,
}
