<template>
  <div
    class="reds-navbar-new"
    v-bind="$attrs"
    :class="[
      {
        bordered: hasHeaderBorder,
        fixed,
      }
    ]"
    :style="{backgroundColor: bgColor}"
  >
    <StatusBar v-if="fixed && showStatusBar" />
    <div class="reds-navbar__content">
      <div
        v-if="$slots.left"
        class="reds-navbar__left"
      >
        <slot name="left" />
      </div>
      <div
        v-else
        class="reds-navbar__left"
        @click="$emit('clickLeft')"
      >
        <Icon
          v-if="showLeftArrow && themeMode !== 'dark'"
          :icon="LeftBack"
          size="24px"
          class="reds-navbar__left__back"
        />
        <Icon
          v-if="showLeftArrow && themeMode === 'dark'"
          :icon="DarkLaftBack"
          size="24px"
          class="reds-navbar__left__back"
        />
        <Button
          v-if="leftText"
          class="reds-navbar__left__btn"
          type="text"
          size="extraLarge"
        >{{ leftText }}</Button>
      </div>
      <div
        v-if="$slots.title"
        class="reds-navbar__title"
        :class="titleAlign === 'left' ? 'leftAlign' : ''"
      >
        <slot name="title" />
      </div>
      <div
        v-if="title"
        class="reds-navbar__title reds-navbar__titletext"
        :class="titleAlign === 'left' ? 'leftAlign' : ''"
      >{{ title }}</div>
      <div
        v-if="$slots.right"
        class="reds-navbar__right"
      ><div class="reds-navbar__right__slot"><slot name="right" /></div></div>
      <div
        v-else
        class="reds-navbar__right"
      >
        <Button
          v-if="rightText"
          type="text"
          class="reds-navbar__right__btn"
          size="extraLarge"
          @click="$emit('clickRight')"
        >{{ rightText }}</Button>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
  import { computed, inject } from 'vue'
  import { StatusBar } from '@xhs/reds-h5'
  import { colors } from '@xhs/reds-h5-style/src'
  import navBarProps from './props'
  import Icon from '../Icon/Icon.vue'
  import Button from '../Button/Button.vue'
  import LeftBack from '../Icon/assets/back_left_b.svg'
  import DarkLaftBack from '../Icon/assets/back_left_b_white.svg'
  import '@xhs/reds-h5/style/navbar.styl'

  const props = defineProps(navBarProps)
  const themeMode = inject('redsTheme', () => ({ value: '' }))
  defineEmits(['clickLeft', 'clickRight'])

  const bgColor = computed(() => {
    if (props.backgroundColor) {
      return colors[props.backgroundColor] || props.backgroundColor
    }
    return colors.ThemeBg2
  })

</script>
