import { onMounted, onBeforeUnmount, Ref } from 'vue'
import createObserver, { IObserverOptions } from './createObserver'
import useObserverOptions from './useObserverOptions'

/**
 * 使用交叉观察器
 * @param nowEl
 * @param observerOptions
 */
function useLazy(element: Ref<HTMLElement | undefined>, observerOptions:IObserverOptions):void {
  // 销毁 Observer 的方法
  let destroyObserver: (()=> void) | null = null

  const globalObserverOptions = useObserverOptions()

  onMounted(async ():Promise<void> => {
    if (!element.value) {
      return
    }
    const observerInfo = await createObserver(element.value, {
      ...globalObserverOptions,
      ...observerOptions,
    })
    if (observerInfo) {
      destroyObserver = observerInfo.destroyObserver
    }
  })

  onBeforeUnmount(() => {
    if (destroyObserver) {
      destroyObserver()
    }
  })
}

export default useLazy
