<script lang="ts">
  import { defineComponent, computed } from 'vue'
  import RedsIconProps from './props'
  import getSvgFromIcon from './getSvgFromIcon'
  import '@xhs/reds-h5/style/icon.styl'

  export default defineComponent({
    name: 'RedsIcon',
    props: RedsIconProps,
    setup(props) {
      // 自定义icon，图标宽高可不一致
      const objIcon = computed(() => {
        if (typeof props.icon === 'object') {
          return getSvgFromIcon(props.icon)
        }
        return null
      })
      // 内置icon，图标宽高一致
      const iconSvg = computed(() => {
        if (typeof props.icon === 'function') {
          const ele = props.icon(
            {
              size: props.size,
              fill: props.fill,
            },
          )
          return ele
        }
        return null
      })

      // 只有在width和height不一致的情况，才会使用props.width和props.height
      const computedWidth = computed(() => {
        if (props.width !== props.height) {
          return props.width
        }
        return props.size
      })

      const computedHeight = computed(() => {
        if (props.width !== props.height) {
          return props.height
        }
        return props.size
      })
      return {
        objIcon,
        iconSvg,
        computedWidth,
        computedHeight,
      }
    },
  })
</script>

<template>
  <span
    v-if="iconSvg"
    class="reds-icon"
    v-bind="$attrs"
    v-html="iconSvg"
  />
  <svg
    v-if="objIcon"
    :width="computedWidth"
    :height="computedHeight"
    :viewBox="objIcon.viewBox"
    class="reds-icon"
    :class="theme === 'filled' ? 'filled' : ''"
    v-bind="$attrs"
    v-html="objIcon.content"
  />

</template>

<style lang="stylus" scoped>
</style>
