import { PropType } from 'vue'
import { ClipConfig, ObserverOptions } from './interface'

/**
 * 背景颜色, 取值为 white, gray
 */
export type BackgroundColorEnum = 'white' | 'gray'

/**
 * 告诉浏览器不在可视视口内的图片该如何加载
 */
export type LoadingEnum = 'eager' | 'lazy'

export default {
  /**
   * 图片的资源，包括网络图片 url、本地图片、base64 图片
   */
  src: {
    type: String,
    required: true,
  },

  /**
   * 图片描述
   */
  alt: {
    type: String,
  },

  /**
   * <img/> width
   */
  width: {
    type: [String, Number],
    required: true,
  },

  /**
   * <img/> height
   */
  height: {
    type: [String, Number],
    required: true,
  },

  /**
   * 图片（宽 / 高）比
   */
  aspectRatio: {
    type: Number,
  },

  /**
   * 背景颜色, 取值为 white, gray
   */
  backgroundColor: {
    type: (String as PropType<BackgroundColorEnum>),
  },

  /**
   * 告诉浏览器不在可视视口内的图片该如何加载。如果设置为true，loading属性为"lazy"。
   */
  lazyLoad: {
    type: Boolean,
    default: true,
  },

  /**
   * 图片展示时会有有 0 到 100 的透明度变化, 默认值是 false
   */
  animated: {
    type: Boolean,
    default: false,
  },
  loadingAnimation: {
    type: Boolean,
    default: false,
  },
  enableRem: {
    type: Boolean,
    default: false,
  },
  pure: {
    type: Boolean,
    default: false,
  },
  /**
   * webp链接
   */
  webp: {
    type: [String, Boolean],
    default: () => true,
  },
  /**
   * 懒加载loading属性
   */
  loading: {
    type: (String as PropType<LoadingEnum>),
    validator: (value: string) => ['eager', 'lazy', 'observer'].includes(value),
    default: () => 'lazy',
  },
  /**
   * 交叉观察器的配置
   */
  observerOptions: {
    type: Object as PropType<ObserverOptions>,
  },
  /**
   * 万象裁剪配置
   */
  clipConfig: {
    type: Object as PropType<ClipConfig>,
    default: () => null,
  },
  /**
   * 几倍图，默认undefined，走三倍图逻辑
   */
  ratio: {
    type: Number,
  },
}
