import { invoke } from '@xhs/ozone-schema'
import type { StatusBar } from './interface'

enum DEFAULT_HEIGHT {
  android = 32,
  ios = 20,
  iphonex = 44,
  browser = 24,
}

type STATUS_HEIGHT = {
  isXHS:boolean
  isFullscreen:boolean
  heightFromHeader:number
  isIOS:boolean
  isIphoneX:boolean
  isIphone14Pro?:boolean
} & StatusBar

const getStatusHeight = async ({
  defaultAndroid = DEFAULT_HEIGHT.android,
  defaultIos = DEFAULT_HEIGHT.ios,
  defaultIphonex = DEFAULT_HEIGHT.iphonex,
  defaultBrowser = DEFAULT_HEIGHT.browser,
  isXHS = false,
  isFullscreen = false,
  heightFromHeader = 0,
  isIOS = false,
  isIphoneX = false,
  isIphone14Pro = false,
}:STATUS_HEIGHT):Promise<number> => {
  // 不在小红书端内
  if (!isXHS) {
    return defaultBrowser
  }

  // 不是全屏状态使用客户端状态栏
  if (!isFullscreen) {
    return 0
  }
  // 从服务端获取端高度
  if (heightFromHeader > 0) {
    // 安卓客户端这边的高度, 把导航栏一起算进去了
    if (!isIOS && (heightFromHeader > 48)) {
      if ((heightFromHeader - 48) > 48) {
        return 48
      }
      return heightFromHeader - 48
    }
    return heightFromHeader
  }

  const getBridgeStatusBarHeight = await invoke('getDeviceInfo').catch(error => {
    if (process.env.BUILD_ENV !== 'production') {
      console.warn(error)
    }
  })
  if (getBridgeStatusBarHeight?.value?.statusBarHeight) {
    return getBridgeStatusBarHeight.value.statusBarHeight as number
  }

  // android
  if (!isIOS) {
    return defaultAndroid
  }

  // 这里仅兼容 IPhone 14 Pro 版本
  if (isIphone14Pro) {
    return 54
  }

  // 当前 IPhone 14 逻辑会命中 isIphoneX 逻辑，不用处理
  // 刘海屏
  if (isIphoneX) {
    return defaultIphonex
  }

  // ios 非刘海屏
  return defaultIos
}

export default getStatusHeight
