import { APM_TRACKER } from '../const'
import { isUpgrade } from './base'

import type { IEagletMethods } from '../types/plugin'

const unsupportedMethod = (method: unknown) => typeof method === 'undefined' || typeof method !== 'function'

const sendMetrics = (measure: PerformanceEntry, tracker: IEagletMethods) => {
  if (unsupportedMethod(measure.toJSON)) {
    return
  }
  const payload = {
    browserApmTracker: {
      type: 'BrowserApmTracker',
      value: {
        browserPerformanceMeasure: {
          type: 'BrowserPerformanceMeasure',
          value: measure.toJSON() as object,
        },
      },
    },
  }

  if (tracker && typeof tracker.push === 'function') {
    if (isUpgrade(true)) {
      window.insight?.push(payload)

      return
    }
    tracker.push(payload, APM_TRACKER.NAME)
  }
}

export default function createPerfUtils(tracker: IEagletMethods) {
  return {
    mark(name: string, immediate: boolean = false) {
      if (unsupportedMethod(window.performance.mark)) {
        return
      }

      window.performance.mark(name)

      if (immediate) {
        this.sendEntriesByName(name, 'mark')
      }
    },

    markPoint(name: string) {
      this.mark(name, true)
    },

    measure(name: string, startMarker: string, endMarker: string) {
      if (unsupportedMethod(window.performance.measure)) {
        return
      }

      window.performance.measure(name, startMarker, endMarker)

      if (startMarker) {
        window.performance.clearMarks(startMarker)
      }

      if (endMarker) {
        window.performance.clearMarks(endMarker)
      }

      this.sendEntriesByName(name, 'measure')
    },

    sendEntriesByName(name: string, entryType?: string) {
      if (unsupportedMethod(window.performance.getEntriesByName)) {
        return
      }
      const [entry] = window.performance.getEntriesByName(name, entryType)

      sendMetrics(entry, tracker)
    },
  }
}
