const REDPASS_HOSTS = {
  development: 'https://redpass.sit.xiaohongshu.com',
  test: 'https://redpass.sit.xiaohongshu.com',
  prelease: 'https://redpass.sit.xiaohongshu.com',
  production: 'https://redpass.xiaohongshu.com',
}

const SSO_HOSTS = {
  development: 'http://login2.sit.xiaohongshu.com',
  test: 'http://login2.sit.xiaohongshu.com',
  prerelease: 'https://login2.xiaohongshu.com',
  production: 'https://login2.xiaohongshu.com',
}

export default function redpassModule(Launcher, options) {
  let setXRequestedWith = options.scheme?.setXRequestedWith
  if (setXRequestedWith === undefined) setXRequestedWith = 'devops'
  if (![false, 'all', 'devops'].includes(setXRequestedWith)) {
    throw new Error('[Launcher auth plugin Exception] `setXRequestedWith` field is invalid.')
  }
  if (setXRequestedWith !== false) {
    Launcher.http.interceptors.dispatch.use(requestConfig => {
      let url = requestConfig.url
      if (url.indexOf('//') === 0) url = `${window.location.protocol}${url}`
      if (url.indexOf('/') === 0) url = `${window.location.origin}${url}`
      if (!/^https?:/.test(url)) return requestConfig

      let isAddHeader = false
      const pattern = setXRequestedWith === 'devops' ? 'devops' : 'xiaohongshu.com'
      try {
        isAddHeader = new URL(url)?.host.indexOf(pattern) > -1
      } catch (e) {
        console.error(`[Launcher auth plugin Exception] Request url is invalid: ${requestConfig.url}`)
      }

      if (isAddHeader) {
        requestConfig.headers['X-Requested-With'] = 'XMLHttpRequest'
      }
      return requestConfig
    })
  }

  Launcher.http.interceptors.response.use(null,
    (err, response) => {
      if (err.status === 418) {
        try {
          const { code = '51105', locationTag } = JSON.parse(response.data?.data || '{}')
          const retUrl = encodeURIComponent(window.location.href)
          const redpassHost = REDPASS_HOSTS[process.env.BUILD_ENV]
          const ssoHost = SSO_HOSTS[process.env.BUILD_ENV]

          if (locationTag === 'redpass') {
            window.location.href = `${redpassHost}/exception?code=${code}&retUrl=${retUrl}`
          } else if (locationTag === 'sso') {
            window.location.href = `${ssoHost}/login?service=${encodeURIComponent(window.location.href)}`
          } else {
            window.location.href = `${redpassHost}/exception?code=22102&retUrl=${retUrl}`
          }
        } catch (error) {
          console.error('[Launcher auth plugin Exception]', error)
        }
      }
      throw err /* must throw error for next handler */
    })
}
