import { Eaglet } from '@xhs/eaglet'

import DefaultEmitter from './sendBeaconEmitter'
import { DEFAULT_TRACKER } from '../../const'

import type { ITracker } from '../../types/helps/create-tracker'
import type { ITrackerExtendDataPayload, ICustomEndPoint } from '../../types/plugin'

export default function createTracker(tracker: ITracker, customEndPoint?: ICustomEndPoint) {
  const isDefaultTracker = tracker.NAME === DEFAULT_TRACKER.NAME
  const options: {
    versionHash: number
    debug?: boolean
    [props: string]: unknown
  } = {
    versionHash: tracker.PROTOBUF_HASH,
    debug: tracker.debug,
  }

  if (isDefaultTracker) {
    options.endpoint = {
      development: customEndPoint?.defaultTracker?.development || DEFAULT_TRACKER.ENDPOINT.development,
      production: customEndPoint?.defaultTracker?.production || DEFAULT_TRACKER.ENDPOINT.production,
    }
  }
  const eaglet = new Eaglet({
    name: tracker.NAME,
    builder: tracker.builder,
    emitter: new DefaultEmitter(options),
  })

  const output = {
    push: (payload: ITrackerExtendDataPayload) => {
      if (payload.app?.value) {
        payload.app.value.trackerVersion = tracker.VERSION
        eaglet.push(payload)
      }
    },
  }

  return output
}
