/* eslint-disable no-console, no-underscore-dangle */
import { http } from '@xhs/launcher'
import { subscribe } from '@xhs/ozone-bridge'

import {
  paintTiming,
  resourceTiming,
  navigationTiming,
  httpRequestTiming,
  enhancedMetrics,
  supportUserTiming,
  supportNavigationTiming,
} from '@xhs/perf-metrics'

import type { IEagletMethods } from '../types/plugin'
import type {
  IBrowserModernizrFeature, IOptions, IGetResourceTimingReturn, IHttpRequestTimingObserveCallBackProps, ISendMetricsProps,
} from '../types/collector/performance'

const __DEBUG__ = process.env.BUILD_ENV === 'development'

function splitEntries(list: PerformanceResourceTiming[] = [], size = 30) {
  const ret: PerformanceResourceTiming[][] = []

  while (list.length) {
    const slice = list.slice(0, size)

    ret.push(slice)
    list = list.slice(size)
  }

  return ret
}

class PerformanceCollector {
  tracker: IEagletMethods

  constructor(tracker: IEagletMethods, options: IOptions = {}) {
    this.tracker = tracker

    this.observeRequestTiming(options)
    // `window.performance.timing.loadEventEnd` not ready util the next tick
    window.addEventListener('load', () => setTimeout(() => this.track(), 0))

    if (supportUserTiming()) {
      this.observePaintTiming()

      // handle buffer full
      this.handleResourceBuffer()
    }

    // Make sure run feature detection when idle
    if (__DEBUG__ || Math.random() < 0.01) { // Set sample rate
      // Close for compatible problem
      // setTimeout(() => {
      //   if (window.requestIdleCallback) {
      //     requestIdleCallback(this.detectBrowserFeatures.bind(this))
      //   } else {
      //     this.detectBrowserFeatures()
      //   }
      // }, 5e3)
    }
  }

  handleResourceBuffer() {
    // send buffering if resource buffer is full
    window.performance.onresourcetimingbufferfull = window.performance.onwebkitresourcetimingbufferfull = () => { // eslint-disable-line max-len, no-multi-assign
      this.sendResourceBuffer()
    }

    // send buffering before view disappear
    subscribe('viewDidDisappear', () => this.sendResourceBuffer())
  }

  getResourceTiming(): IGetResourceTimingReturn {
    return {
      type: 'BrowserResourceTiming',
      value: resourceTiming.timing(),
    }
  }

  track() {
    const metrics = enhancedMetrics.timing()
    this.sendMetrics({
      browserMemory: {
        type: 'BrowserMemory',
        value: metrics.memory,
      },
    })

    this.sendMetrics({
      browserNetwork: {
        type: 'BrowserNetwork',
        value: metrics.network,
      },
    })

    if (supportNavigationTiming()) {
      this.sendMetrics({
        browserNavigationTiming: {
          type: 'BrowserNavigationTiming',
          value: navigationTiming.timing(),
        },
      })
    }

    if (metrics.scriptsExecutionTiming.length > 0) {
      this.sendMetrics({
        browserScriptsExecutionTiming: {
          type: 'BrowserPerformanceMeasure',
          value: metrics.scriptsExecutionTiming,
        },
      })
    }

    if (supportUserTiming()) {
      this.sendMetrics({
        browserResourceTiming: this.getResourceTiming(),
      })

      if (metrics.renderTimes) {
        this.sendMetrics({
          browserRenderTimes: {
            type: 'BrowserRenderTimes',
            value: metrics.renderTimes,
          },
        })
      }
    }

    if (metrics.resourceError?.length > 0) {
      this.sendMetrics({
        browserResourceError: {
          type: 'BrowserResourceError',
          value: metrics.resourceError,
        },
      })
    }
  }

  detectBrowserFeatures() {
    const browserModernizrFeature: IBrowserModernizrFeature = {
      type: 'BrowserModernizrFeature',
      value: [],
    }
    import('../../dist/collector/modernizr').then(() => {
      Object.keys(window.Modernizr).forEach(featureKey => {
        const feature = window.Modernizr[featureKey]

        if (typeof feature === 'boolean') {
          browserModernizrFeature.value.push({
            name: featureKey,
            isSupport: feature,
          })
        } else {
          browserModernizrFeature.value.push({
            name: featureKey,
            detail: JSON.stringify(feature),
          })
        }
      })

      this.sendMetrics({
        browserModernizrFeature,
      })
    })
  }

  observePaintTiming() {
    paintTiming.observe().then(({ paintTimes, ...fmpTimes }: {
      paintTimes: {
        firstPaint?: number
        firstContentfulPaint?: number
      }
      fullyLoaded: number
      firstMeaningfulPaint: number
      firstScreenPaint: number
    }) => {
      this.sendMetrics({
        browserPaintTimes: {
          type: 'BrowserPaintTimes',
          value: paintTimes,
        },
      })

      this.sendMetrics({
        browserFmpTimes: {
          type: 'BrowserFmpTimes',
          value: fmpTimes,
        },
      })
    })
  }

  observeRequestTiming(options: IOptions = {}) {
    httpRequestTiming.observe({
      httpClient: http,
      callback: (payload: IHttpRequestTimingObserveCallBackProps) => {
        // TODO: 删除旧的HttpRequestTimingPerformanceMeasure
        this.sendMetrics({
          measurement_name: 'httpRequestTiming',
          measurement_data: {
            ...(payload.meta || undefined),
            duration: payload.measure?.duration,
          },
        })
      },
      ...options,
    })
  }

  sendMetrics(metrics: ISendMetricsProps = {}) { // eslint-disable-line no-shadow
    if (
      !this.tracker
      || typeof this.tracker.push === 'undefined'
      || typeof this.tracker.push !== 'function'
    ) {
      return
    }

    if (Object.keys(metrics).length === 0) {
      return
    }

    if (metrics.measurement_name) {
      this.tracker.push((metrics), 'ApmXrayTracker')
      return
    }

    const payload = {
      browserApmTracker: {
        type: 'BrowserApmTracker',
        value: metrics,
      },
    }
    this.tracker.push(payload, 'ApmTracker')
  }

  sendResourceBuffer() {
    const { type, value: latestEntries } = this.getResourceTiming()

    if (!latestEntries.length) {
      return
    }

    // Splitting the entries to several pieces avoid too large payload send once
    // The size defaults to `50` since most browser's buffer limits to 150~250 per-frame
    const entries = splitEntries(latestEntries, 50)

    while (entries.length) {
      this.sendMetrics({
        [type]: { type, value: entries.shift() },
      })
    }
  }
}

export default function createPerformanceCollector(tracker: IEagletMethods, options = {}) {
  return new PerformanceCollector(tracker, options)
}
