import { commonChecks, appVersionGte, appVersionLt } from '@xhs/ozone-detector';
import BridgeError from '../../../utils/BridgeError';
import { checkArgs } from '../../../utils/checkOldBridge';
import callbacks from './callbacks';
export function isSupportAdapter(methodName) {
    if (!process.env.BROWSER) {
        return false;
    }
    const handler = commonChecks.isIOS
        ? (window.webkit?.messageHandlers)
        : window.xhsbridge;
    return handler && !!handler[methodName];
}
/**
 * 老版本 bridge 调用函数
 */
export function bridge(method, options, funcName) {
    const postData = {};
    if (options !== undefined) {
        postData.data = options;
    }
    if (funcName) {
        postData.callback = funcName;
    }
    if (commonChecks.isIOS) {
        // FIXME: 兼容 iOS 5.29、5.30 从 postData.data 取回调函数 key，而不是 postData.callback
        if (method === 'registerNotice'
            && appVersionGte('5.29')
            && appVersionLt('5.31')) {
            try {
                window.webkit.messageHandlers[method].postMessage({
                    data: funcName,
                });
            }
            catch (e) {
                throw new BridgeError('ERR_NATIVE', e.message);
            }
            return;
        }
        try {
            window.webkit.messageHandlers[method].postMessage(postData);
        }
        catch (e) {
            throw new BridgeError('ERR_NATIVE', `${e.message}, bridge is ${method}`);
        }
    }
    else {
        const postDataStr = JSON.stringify(postData);
        try {
            window.xhsbridge[method](postDataStr);
        }
        catch (e) {
            throw new BridgeError('ERR_NATIVE', `${e.message}, bridge is ${method}`);
        }
    }
}
export function callbackAdapter(method, data, callback, spec) {
    if (!isSupportAdapter(method)) {
        throw new BridgeError('ERR_NOT_SUPPORT', `not support ${method}`);
    }
    if (spec?.argsT) {
        checkArgs(data, spec.argsT, method);
    }
    let funcName;
    if (typeof callback === 'function') {
        funcName = callbacks.add(method, callback, spec);
    }
    bridge(method, data, funcName);
}
export function adapter(method, data, spec) {
    return new Promise((resolve, reject) => {
        try {
            let newData = data;
            let newSpec = spec;
            if (newData && (newData.argsT || newData.resT)) {
                newSpec = newData;
                newData = undefined;
            }
            callbackAdapter(method, newData, resolve, newSpec);
        }
        catch (e) {
            reject(e);
        }
    });
}
