import { addCalledJsListener, generateCalledJsCallback, removeCalledJsListener } from './event';
import { bridgeStart } from '../utils/bridgeConsole';
/**
 * Web 平台添加event的监听
 * @param eventName 监听的事件名
 * @param callback 收到事件的回调
 */
export function subscribe(eventName, callback, app) {
    bridgeStart({
        name: eventName,
        type: 'event',
    });
    // callback添加到事件监听列表
    addCalledJsListener(eventName, callback);
    // 如果没有事件回调函数，则添加一个
    if (!window.XHSEvents[eventName]) {
        window.XHSEvents[eventName] = generateCalledJsCallback(eventName, app);
    }
    // 参考RN,返回有remove方法移除监听的对象
    return removeCalledJsListener(eventName, callback);
}
