// User-centric Performance Metrics
// see: https://developers.google.com/web/fundamentals/performance/user-centric-performance-metrics

import paintTiming from '@xhs/paint-timing'
import PerfTiming from '../../timing'
import { getTimeFromMarks } from '../../helpers'

function getPaintTimes() {
  let firstPaint = null
  let firstContentfulPaint = null

  const paint = window.performance.getEntriesByType('paint')

  if (paint && paint.length) {
    firstPaint = getTimeFromMarks(paint, 'first-paint')
    firstContentfulPaint = getTimeFromMarks(paint, 'first-contentful-paint')
  } else if (typeof window.performance.timing.msFirstPaint === 'number') {
    // IE
    firstPaint = window.performance.timing.msFirstPaint - window.performance.timing.navigationStart // eslint-disable-line max-len
  } else {
    return null
  }

  return { firstPaint, firstContentfulPaint }
}

class BrowserPaintTiming extends PerfTiming {
  observe() {
    return paintTiming.get().then(({
      firstPaint = null,
      firstContentfulPaint = null,
      firstMeaningfulPaint = null,
      fullyLoaded = null,
      firstScreen = null,
    }) => {
      let paintTimes = getPaintTimes()

      if (!paintTimes) {
        paintTimes = { firstPaint, firstContentfulPaint }
      }

      return {
        paintTimes,
        fullyLoaded,
        firstMeaningfulPaint,
        firstScreenPaint: firstScreen,
      }
    })
  }
}

export default new BrowserPaintTiming()
