import type { TIntersectionObserver } from './useObserverOptions'

export interface IObserverOptions extends TIntersectionObserver {
  // 进入视区触发的逻辑
  onShow?: (el: Element)=> void
}

// 统计当前监听数量
let listenCount: number = 0

/**
 * 创建交叉观察器
 * @param nowEl
 * @param observerOptions
 */
async function createObserver(nowEl: HTMLElement, observerOptions:IObserverOptions): Promise<({
  destroyObserver:()=> void
})| null> {
  // 异步加载 IntersectionObserver
  if (!(
    'IntersectionObserver' in window
    && 'IntersectionObserverEntry' in window
    && 'isIntersecting' in window.IntersectionObserverEntry.prototype)) {
    // @ts-ignore
    await import('intersection-observer')
  }

  const { onShow, maxListenCount, ...restOptions } = observerOptions

  // 超过了最大监听数量
  if (listenCount > maxListenCount) {
    if (typeof onShow === 'function') {
      // 直接显示
      onShow(nowEl)
    }
    return null
  }

  // 创建交叉观察器
  const observer = new IntersectionObserver(entries => {
    const firstEntry = entries[0]
    if (!firstEntry.isIntersecting || typeof onShow !== 'function') {
      return
    }
    // 触发进入视窗事件
    onShow(firstEntry.target)
    if (observer) {
      // 执行完事件之后取消绑定
      observer.unobserve(firstEntry.target)
      // 监听数量减1
      listenCount -= 1
    }
  }, restOptions)
  observer.observe(nowEl)
  // 监听数量加1
  listenCount += 1

  // 销毁交叉观察器
  const destroyObserver = () => {
    if (observer) {
      observer.unobserve(nowEl)
      observer.disconnect()
      // 监听数量减1
      listenCount -= 1
    }
  }

  return { destroyObserver }
}

export default createObserver
