import getABInfo from '@xhs/abtest'
import { commonChecks } from '@xhs/ozone-detector'

interface IUseInfoCache {
  user: {
    type: string
    value: {
      userId: string
      hashUserId: string
      wxOpenid: string
      expV4: string
    }
  }
}

interface IUserInfo {
  userId: string
  userToken: string
  sessionId: string
  hashExp: string
  flags?: object
}

function getOpenId() {
  if (commonChecks.isMiniprogram) {
    if (window?.location) {
      const searchParams = new URLSearchParams(window.location.search)

      return searchParams.get('openid')
    }
  }
  return null
}

// 缓存 bridge 获取的 useInfo
let useInfoCache: IUseInfoCache | Promise<IUseInfoCache>

const getUserInfo = () => {
  if (useInfoCache) {
    return useInfoCache
  }
  useInfoCache = getABInfo().then((userInfo: IUserInfo) => ({
    user: {
      type: 'User',
      value: {
        userId: userInfo.userId || '',
        hashUserId: userInfo.userToken,
        wxOpenid: getOpenId() || '',
        expV4: userInfo.hashExp,
      },
    },
  }))
  return useInfoCache
}

export default getUserInfo

// 用户重新登陆,刷新获取用户信息
export const purgeUserInfo = () => getABInfo().then((userInfo: IUserInfo) => ({
  user: {
    type: 'User',
    value: {
      userId: userInfo.userId || '',
      hashUserId: userInfo.userToken,
      wxOpenid: getOpenId() || '',
      expV4: userInfo.hashExp,
    },
  },
}))
