export function supportPerfObserver() {
  return 'PerformanceObserver' in window
}

export function supportNavigationTiming() {
  return Boolean(window.performance && window.performance.timing)
}

export function supportUserTiming() {
  return Boolean(window.performance.mark
    && window.performance.measure
    && window.performance.clearMarks
    && window.performance.clearMeasures
    && window.performance.getEntries
    && window.performance.getEntriesByType
    && window.performance.getEntriesByName)
}
