import axios from 'axios'

import factory from './lib/factory'

/* factory have default validate status, so disable axios here */
axios.defaults.validateStatus = undefined

const http = factory(axios.request.bind(axios))

const {
  get,
  del,
  head,
  post,
  put,
  patch,
  getUri,
  makeUri,
  interceptors,
  configure,
  buildURL,
  fork,
} = http

export default http
export {
  get,
  del,
  head,
  post,
  put,
  patch,

  getUri,
  makeUri,
  interceptors, /* it is http interceptors, not axios`s interceptors */
  configure,
  buildURL,
  fork,
  axios,
}
