/* eslint-disable no-console, no-underscore-dangle */
// @ts-ignore reason: 'google-protobuf' 依赖无需声明文件
import googleProtobuf from 'google-protobuf'
import EmitterBase from '@xhs/eaglet-emitter-base'
import { checkTrackEnv } from '../get-track-env'

import type { IOptions, IJspb, ISerializeBinary } from '../../types/emitter/default-emitter'

let isNativeDebugOn: boolean

export default class DefaultEmitter extends EmitterBase {
  isLite?: boolean

  localDebug?: boolean

  // preferNative?: boolean | ((params: Promise<unknown>) => Promise<boolean>)

  constructor(options: IOptions) {
    super(options)
    this.isLite = options.isLite
    this.localDebug = options.debug
  }

  serializeBinary(message: ISerializeBinary, BinaryWriter: IJspb['BinaryWriter']) {
    const writer = new BinaryWriter()
    const messageArr = message.serializeBinary()
    writer.encoder_.writeSignedVarint64(messageArr.byteLength + 4)
    writer.encoder_.writeInt32(this.versionHash)
    writer.encoder_.writeBytes(messageArr)

    const ret = writer.getResultBase64String()

    writer.reset()

    return ret
  }

  sendData(dataString: string) {
    const remoteUrl = ((isNativeDebugOn || this.localDebug)
      ? this.endpoint.development
      : this.endpoint.production) as string
    // 端外 pe 打点采用 sendBeacon 上报
    const headers = {
      type: 'text/plain',
    }
    const blob = new Blob([dataString], headers)
    // 对浏览器是否支持sendBeacon判断
    if (typeof navigator?.sendBeacon === 'function') {
      navigator.sendBeacon(remoteUrl, blob)
    }
  }

  flush() {
    if (!this.buffer.length) {
      return
    }
    const tasks: (Promise<boolean | IJspb>)[] = [checkTrackEnv]
    if (!this.isLite) {
      tasks.push(Promise.resolve(googleProtobuf))
    }
    Promise.all(tasks).then(([nativeDebugOn, jspb]) => {
      isNativeDebugOn = nativeDebugOn as boolean
      while (this.buffer.length) {
        const message = this.buffer.shift()

        this.sendData(this.serializeBinary(message as ISerializeBinary, (jspb as IJspb).BinaryWriter))
      }
    })
  }
}
