import PerfTiming from '../../timing'
import { toDict } from '../../helpers'

class BrowserNavigationTiming extends PerfTiming {
  timing() {
    const payload = toDict(window.performance.timing)

    // remove non-standard fields in FF
    if (typeof payload.timeToNonBlankPaint !== 'undefined') {
      delete payload.timeToNonBlankPaint
    }

    if (typeof payload.timeToDOMContentFlushed !== 'undefined') {
      delete payload.timeToDOMContentFlushed
    }

    return payload
  }
}

export default new BrowserNavigationTiming()
