import {
  useSSRContext, Ref, ref, onMounted, onBeforeMount,
} from 'vue'
import * as FullLauncher from '@xhs/launcher'
import { commonChecks, getCommonChecksInNode } from '@xhs/ozone-detector'
import type { StatusBar } from './interface'
import getStatusHeight from './getStatusHeight'

async function useStatusBarHeight(defaultHeight:StatusBar): Promise<Ref<number | undefined>> {
  // 有可能 launcher 中没有 useServerPrefetch 这个方法
  // @ts-ignore
  const isSupportServerPreFetch = typeof FullLauncher?.useServerPrefetch === 'function'

  const statusBarHeight: Ref<number | undefined> = isSupportServerPreFetch ? FullLauncher.useServerPrefetch(
    // @ts-ignore
    () => {
      try {
        const ssrContext = useSSRContext()
        const xhsHeightServer = ssrContext?.headers['xhs-statusbar-height']
        const checks = getCommonChecksInNode({
          userAgent: ssrContext?.headers['user-agent'],
          url: ssrContext?.url?.href,
        })
        return getStatusHeight({
          ...checks,
          ...defaultHeight,
          heightFromHeader: Number(xhsHeightServer),
        })
      } catch (e) {
        // void
      }
      return undefined
    },
    {
      key: 'reds-status-bar',
      serverOnly: true,
    },
  ) : ref(undefined)

  // 判断是否是SSR
  const isSSR = isSupportServerPreFetch ? FullLauncher.useServerPrefetch(
    // @ts-ignore
    () => true,
    {
      key: 'reds-status-ssr-flag',
      serverOnly: true,
    },
  ) : ref(false)

  const setClientHeight = async () => {
    // SSR 没有拿到高度
    // CSR 阶段获取高度
    if (statusBarHeight.value !== 0 && !statusBarHeight.value) {
      statusBarHeight.value = await getStatusHeight({
        ...commonChecks,
        ...defaultHeight,
      })
    }
  }

  onBeforeMount(async () => {
    // SSR 执行这个会导致水合失败，并不会生效，这里忽略
    if (isSSR.value) {
      return
    }
    await setClientHeight()
  })

  onMounted(async () => {
    // 这里作为 SSR 项目服务端完全没有获取到任何信息的兜底方案
    await setClientHeight()
  })

  return statusBarHeight
}

export default useStatusBarHeight
