export type ScopeItem = 'infraJsError' | 'httpRequestTiming'

export type Tracker = {
  // eslint-disable-next-line camelcase
  [props: string]: unknown
  measurement_name?: ScopeItem | string
  measurement_data?: {
    [props: string]: string | number | object
  }
}

type InterceptHandler = (data: Tracker) => Tracker

type Options = {
  scopes: ScopeItem[]
  handler: InterceptHandler
}

const INTERCEPT_SCOPES: ScopeItem[] = ['infraJsError', 'httpRequestTiming']

export default function interceptFactory(options: Options | undefined) {
  const { scopes, handler } = options || {}
  const effectiveScope = scopes?.filter(scope => INTERCEPT_SCOPES.find(c => c === scope)) || []

  if (process.env.BUILD_ENV === 'development' && scopes?.length && effectiveScope.length !== scopes?.length) {
    console.warn('please check interceptor config, scopes only set \'infraJsError\' | \'httpRequestTiming\'')
  }

  return function interceptorImplement(data: Tracker) {
    const inEffectiveScope = effectiveScope.find(scope => scope === data.measurement_name)

    if (inEffectiveScope) {
      try {
        return handler?.(data) || data
      } catch (err: unknown) {
        return data
      }
    }

    return data
  }
}
