import { SchemaType } from '../utils/type';
import { validateRes } from '../utils/check';
import { eventSuccess } from '../utils/bridgeConsole';
const EventKey = 'CallbackList';
class XHSEventsCB {
    constructor() {
        this.#cb = {};
    }
    #cb;
    get(key) {
        return this.#cb[key] || [];
    }
    set(key, value) {
        this.#cb[key] = value;
    }
}
if (process.env.BROWSER) {
    window.XHSEvents = window.XHSEvents || {};
    window.XHSEventsCB = window.XHSEventsCB || new XHSEventsCB();
}
/**
 * 把callback添加到event的监听列表里
 * @param eventName 事件名
 * @param callback 事件监听回调
 */
export function addCalledJsListener(eventName, callback) {
    const eventCBKey = `${eventName}${EventKey}`;
    const callbacks = window.XHSEventsCB.get(eventCBKey);
    for (let i = 0; i < callbacks.length; i++) {
        if (callbacks[i] === callback) {
            return;
        }
    }
    callbacks.push(callback);
    window.XHSEventsCB.set(eventCBKey, callbacks);
}
/**
 * 收到事件,迭代调用事件对应的callback
 * @param eventName 事件名
 */
export function generateCalledJsCallback(eventName, app) {
    return (res) => {
        validateRes(eventName, SchemaType.Event, res, app);
        eventSuccess({
            name: eventName,
            res,
        });
        const callbacks = window.XHSEventsCB.get(`${eventName}${EventKey}`);
        callbacks.forEach((cb) => cb(res));
    };
}
/**
 * 把指定的callback从监听列表移除
 * @param eventName 需要移除监听的事件名
 * @param callback 移除的监听回调
 */
export function removeCalledJsListener(eventName, callback) {
    return {
        remove() {
            const eventCBKey = `${eventName}${EventKey}`;
            const callbacks = window.XHSEventsCB.get(eventCBKey);
            window.XHSEventsCB.set(eventCBKey, callbacks.filter((item) => item !== callback));
        },
    };
}
