export default {
  /**
     * 图标名称
     */
  icon: {
    type: [Function, Object],
    required: true,
  },

  /**
     * 图标宽度
     */
  width: {
    type: String,
    default: '16px',
  },

  /**
     * 图标高度
     */
  height: {
    type: String,
    default: '16px',
  },

  size: {
    type: String,
    default: '16px',
  },
  fill: {
    type: String,
  },
  theme: {
    type: String,
    validator: (value: string) => ['outline', 'filled'].includes(value),
    default: 'outline',
  },
}
