import { objectHasKey } from '../utils/objectHasKey'
import type { IExtendData } from '../types/plugin'

function setUserId(extendData: IExtendData, value: string) {
  const trackerHasUserId = objectHasKey(extendData, 'Tracker.user.value')
  const apmTrackerHasUserId = objectHasKey(extendData, 'ApmTracker.user.value')
  const apmXrayTrackerHasUserId = objectHasKey(extendData, 'ApmXrayTracker.context_userId')
  if (trackerHasUserId && extendData?.Tracker?.user) {
    extendData.Tracker.user.value.userId = value
  }
  if (apmTrackerHasUserId && extendData?.ApmTracker?.user) {
    extendData.ApmTracker.user.value.userId = value
  }
  if (apmXrayTrackerHasUserId && extendData.ApmXrayTracker) {
    extendData.ApmXrayTracker.context_userId = value
  }
}
export default setUserId
