import { getPostData } from '../helpers/get-post-data'
import type { IEagletMethods } from '../types/plugin'

class ClickCollector {
  tracker: IEagletMethods

  constructor(tracker: IEagletMethods) {
    this.tracker = tracker

    document.body.addEventListener('click', ev => {
      const targetDom = ev.target
      const postData = this.getData(targetDom)

      if (postData) {
        this.tracker.push(postData)
      }
    }, true)
  }

  // 获取打点数据
  getData(targetDom: EventTarget| Node | Element | null) {
    const postData = getPostData(targetDom as (Node | Element | null))

    if (!postData) {
      return
    }

    if (postData.event) {
      // 仅在没有action时补充默认的action
      if (!postData.event.value.action) {
        postData.event.value.action = {
          type: 'NormalizedAction',
          value: 'click',
        }

        postData.event.value.seAction = 'click'
      }
    } else {
      postData.event = {
        type: 'Event',
        value: {
          action: {
            type: 'NormalizedAction',
            value: 'click',
          },
          seAction: 'click',
        },
      }
    }

    return postData
  }
}

export default function createClickCollector(options: IEagletMethods) {
  return new ClickCollector(options)
}
