import { warn } from '@xhs/logger'
import { commonChecks } from '@xhs/ozone-detector'
import { subscribe as schemaSubscribe } from '@xhs/ozone-schema'

import { appVersionGte, appVersionLt } from './libs/version'
import BridgeError from './utils/BridgeError'
import { warnDeprecated } from './utils/logDeprecated'

import { setNaviBackCallbackLegacy } from './ui'
import { registerNoticeLegacy } from './data'

// FIXME: viewAppearh 和 viewDisappear 没有 namespace，容易和其他代码冲突
const NATIVE_CALL_JS_CONFIG = {
  viewWillAppearIOS: {
    ios: '5.6.0',
  },
  viewDidAppear: {
    ios: '5.39.0',
    android: '5.7.0',
  },
  viewWillDisappearIOS: {
    ios: '5.6.0',
  },
  viewDidDisappear: {
    ios: '5.39.0',
    android: '5.7.0',
  },
  // deprecated
  viewAppear: {
    ios: '5.6.0',
    android: '5.7.0',
  },
  // deprecated
  viewDisappear: {
    ios: '5.6.0',
    android: '5.7.0',
  },
  keyboardChange: {
    ios: '5.7.0',
    namespace: 'XHSHandler',
  },
  afterLogin: {
    ios: '5.22.0',
    android: '5.22.0',
    namespace: 'XHSHandler',
  },
  dataTrafficChange: {
    ios: '5.42',
    android: '5.42',
    namespace: 'XHSHandler',
  },
  themeTypeChange: {
    ios: '6.13',
    android: '6.12',
    namespace: 'XHSHandler',
  },
  faceRecognitionResult: {
    ios: '6.58',
    android: '6.58',
    namespace: 'XHSHandler',
  },
  onBomLotteryStart: {
    ios: '6.63',
    android: '6.63',
    namespace: 'XHSHandler',
  },
  onBomLotteryEnd: {
    ios: '6.63',
    android: '6.63',
    namespace: 'XHSHandler',
  },
  onHitCouponFinish: {
    ios: '6.65',
    android: '6.65',
    namespace: 'XHSHandler',
  },
  receiveTrickleMessage: {
    android: '6.81.0',
    namespace: 'XHSHandler',
  },
}

const SCHEMA_EVENT_LIST = {
  viewDidAppear: {
    ios: '6.62.0',
  },
  viewDidDisappear: {
    ios: '6.62.0',
  },
  pause: {
    android: '6.62.0',
  },
  resume: {
    android: '6.62.0',
  },
  receiveTrickleMessage: {
    ios: '6.81.0',
    android: '6.83.0',
  },
  backgroundFetchFileProgress: {
    android: '6.92.0',
  },
  crossPlatformEvent: {
    ios: '7.1.0',
    android: '7.1.0',
  },
}

function supportEvent(name) {
  const meta = NATIVE_CALL_JS_CONFIG[name]

  if (!meta) {
    return false
  }

  return (commonChecks.isIOS && meta.ios && appVersionGte(meta.ios))
    || (commonChecks.isAndroid && meta.android && appVersionGte(meta.android))
}

function supportSchemaEvent(name) {
  const meta = SCHEMA_EVENT_LIST[name]
  if (!meta) {
    return false
  }
  return (commonChecks.isIOS && meta.ios && appVersionGte(meta.ios))
    || (commonChecks.isAndroid && meta.android && appVersionGte(meta.android))
}

function addCalledJsListener(eventName, callback) {
  window.XHSHandler = window.XHSHandler || {}

  window.XHSHandler[`${eventName}CallbackList`] = window.XHSHandler[`${eventName}CallbackList`] || []
  const callbacks = window.XHSHandler[`${eventName}CallbackList`]

  for (let i = 0; i < callbacks.length; i++) {
    if (callbacks[i] === callback) {
      return
    }
  }

  callbacks.push(callback)
}

function generateCalledJsCallback(eventName) {
  return data => {
    const callbacks = (window.XHSHandler?.[`${eventName}CallbackList`])
        || []

    callbacks.forEach(cb => {
      cb(data)
    })
  }
}

export function subscribe(event, callback) {
  let eventName = event

  if (eventName === 'viewAppear') {
    warnDeprecated('viewAppear', 'viewDidAppear', 'v2.6.0')
  }

  if (eventName === 'viewDisappear') {
    warnDeprecated('viewDisappear', 'viewDidDisappear', 'v2.6.0')
  }

  if (eventName === 'viewWillAppearIOS') {
    if (commonChecks.isAndroid) {
      return false
    }

    eventName = 'viewAppear'
  }

  if (eventName === 'viewDidAppear') {
    if (commonChecks.isAndroid || (commonChecks.isIOS && appVersionLt('5.39.0'))) {
      eventName = 'viewAppear'
    }
  }

  if (eventName === 'viewWillDisappearIOS') {
    if (commonChecks.isAndroid) {
      return false
    }

    eventName = 'viewDisappear'
  }

  if (eventName === 'viewDidDisappear') {
    if (commonChecks.isAndroid || (commonChecks.isIOS && appVersionLt('5.39.0'))) {
      eventName = 'viewDisappear'
    }
  }

  if (eventName === 'navBack' || eventName === 'setNaviBackCallback') {
    try {
      setNaviBackCallbackLegacy(callback)

      return true
    } catch (e) {
      return false
    }
  }

  if (eventName === 'notice' || eventName === 'registerNotice') {
    try {
      registerNoticeLegacy(callback)

      return true
    } catch (e) {
      return false
    }
  }

  if (!supportEvent(eventName)) {
    if (commonChecks.isXHS) {
      warn(`[OzoneBridge] ${eventName} is not supported`)
    }
    return false
  }

  addCalledJsListener(eventName, callback)

  const { namespace } = NATIVE_CALL_JS_CONFIG[eventName]

  if (!namespace) {
    if (!window[eventName]) {
      window[eventName] = generateCalledJsCallback(eventName)
    }
  } else {
    window[namespace] = window[namespace] || {}
    const ns = window[namespace]

    if (!ns[eventName]) {
      ns[eventName] = generateCalledJsCallback(eventName)
    }
  }

  return true
}

export function addEventListener(event, callback) {
  if (event === 'pause' || event === 'resume') {
    const message = `[ozone-bridge Exception], you should not use ${event} directly, please use viewDidAppear | viewDidDisappear to replace resume | pause`
    throw new BridgeError('ERR_NOT_SUPPORT', message)
  }

  // 迁移的 event 比如 viewDidDisappear、viewDidAppear优先走新通道
  if (supportSchemaEvent(event)) {
    let lastEvent = event
    if (event === 'viewDidDisappear' && commonChecks.isAndroid) {
      lastEvent = 'pause'
    }
    if (event === 'viewDidAppear' && commonChecks.isAndroid) {
      lastEvent = 'resume'
    }
    return schemaSubscribe(lastEvent, callback)
  }
  // 老通道的 event
  const isSupport = subscribe(event, callback)
  if (isSupport) {
    return {
      remove() {
        // old event, remove do nothing
      },
    }
  }
  return schemaSubscribe(event, callback)
}

export function registerNativeCalledJS(eventName, callback) {
  warnDeprecated('registerNativeCalledJS', 'subscribe')

  subscribe(eventName, callback)
}
