import { ClipConfig } from '../interface/index'
/**
 * 获取裁剪信息
 * @param width
 * @param height
 */
function getSizeString(width?:number, height?:number, ratio:number = 3) {
  return `${width ? `/w/${width * ratio}` : ''}${height ? `/h/${height * ratio}` : ''}`
}

/**
 * 去掉 https: 和 http
 * @param origin
 */
export function getPureOrigin(origin:string) {
  if (origin.indexOf('https:') === 0) {
    return origin.substr(6)
  }
  if (origin.indexOf('http:') === 0) {
    return origin.substr(5)
  }
  return origin
}

/**
 * 获取渲染时需要的链接
 * @param src
 * @param width
 */
function getRenderSrc(src?: string, width?:number | string, height?:number | string, clipConfig?: ClipConfig, ratio:number = 3):string {
  if (!src) {
    return ''
  }
  // 如果width或height传入的是string，比如100%，不做万象处理
  if (typeof width === 'string' || typeof height === 'string') {
    return src
  }

  try {
    let { search = '', origin, pathname } = new URL(src.indexOf('//') === 0 ? `https:${src}` : src)

    // 用户自定义了万象
    if (clipConfig) {
      const mode = clipConfig.mode || 1
      const clipWidth = clipConfig.width
      const clipHeight = clipConfig.height
      const clipFormat = clipConfig.format
      const clipQuality = clipConfig.quality
      search = `?imageView2/${mode}`
      if (clipWidth) {
        search += `/w/${clipWidth * ratio}`
      }
      if (clipHeight) {
        search += `/h/${clipHeight * ratio}`
      }
      if (clipFormat) {
        search += `/format/${clipFormat}`
      }
      if (clipQuality) {
        search += `/q/${clipQuality}`
      }
    }
    // 用户没有指定万象，用 3 倍 png 做兜底
    if (search.indexOf('imageView2') === -1 && width && height) {
      return `${getPureOrigin(origin)}${pathname}?imageView2/1${getSizeString(width, height, ratio)}/format/png`
    }
    // ios13 不支持 webp 强制转换为 png 兜底
    if (search.indexOf('/format/webp') > -1) {
      search = search.replace('/format/webp', '/format/png')
    }
    // 没有指定 w 和 height 其中一个
    if (search.indexOf('/w') === -1 && search.indexOf('/h') === -1 && width && height) {
      search = search.replace('imageView2', `imageView2/1${getSizeString(width, height, ratio)}`)
    }
    return `${getPureOrigin(origin)}${pathname}${search}`
  } catch (error) {
    // void
  }

  return getPureOrigin(src)
}

export default getRenderSrc
