/**
 * Polyfill implements of User Timing API
 * https://developer.mozilla.org/en-US/docs/Web/API/User_Timing_API
 */

import { now } from '../../helpers'
import { isUndef } from '../../helpers/typeor'

// Protected keys of PerformanceTiming interface
const protectedTimingKeys = [
  'connectEnd',
  'connectStart',
  'domComplete',
  'domContentLoadedEventEnd',
  'domContentLoadedEventStart',
  'domInteractive',
  'domLoading',
  'domainLookupEnd',
  'domainLookupStart',
  'fetchStart',
  'loadEventEnd',
  'loadEventStart',
  'navigationStart',
  'redirectEnd',
  'redirectStart',
  'requestStart',
  'responseEnd',
  'responseStart',
  'secureConnectionStart',
  'unloadEventEnd',
  'unloadEventStart',
]

let entryHash = {}
let entryList = []

let navigationStart = now()
const getEntryList = () => entryList

function resetEntries(type) {
  const newEntryHash = {}
  const newEntryList = []

  entryList.forEach(entry => {
    if (entry.entryType !== type) {
      newEntryHash[entry.name] = entry
      newEntryList.push(entry)
    }
  })

  entryHash = newEntryHash
  entryList = newEntryList
}

function removeEntry(type, name) {
  if (isUndef(name)) {
    resetEntries(type)
    return
  }

  entryList = entryList.filter(entry => entry.name !== name || entry.entryType !== type)

  const entry = entryHash[name]

  if (entry && entry.entryType === type) {
    entryHash[name] = undefined
  }
}

function ShadowPerformanceEntry(name, entryType, startTime, duration) {
  this.name = name
  this.duration = duration
  this.entryType = entryType
  this.startTime = startTime
}

ShadowPerformanceEntry.prototype.toJSON = function toJSON() {
  return {
    name: this.name,
    duration: this.duration,
    entryType: this.entryType,
    startTime: this.startTime,
  }
}

export default function wrap(global) {
  global.performance = global.performance || {}

  function setupPolyfill(method, handler) {
    if (isUndef(global.performance[method])) {
      global.performance[method] = handler
    }
  }

  setupPolyfill('now', () => {
    if (typeof navigationStart === 'function') {
      return now() - navigationStart() // for rn
    }
    return now() - navigationStart
  })

  setupPolyfill('mark', name => {
    if (protectedTimingKeys.indexOf(name) >= 0) {
      throw Error(`Failed to execute 'mark' on 'Performance': '${name}' is part of the PerformanceTiming interface, and cannot be used as a mark name.`)
    }

    const entry = new ShadowPerformanceEntry(name, 'mark', performance.now(), 0)

    entryList.push(entry)
    entryHash[name] = entry
  })

  setupPolyfill('measure', (name, startMark, endMark) => {
    let startTime = 0
    let endTime = 0

    const currentTime = now()

    if (isUndef(startMark)) {
      startTime = 0
    } else if (entryHash[startMark]) {
      startTime = entryHash[startMark].startTime
    } else {
      throw Error(`Failed to execute 'measure' on 'Performance': The mark '${startMark}' dose not exist.`)
    }

    if (isUndef(endMark)) {
      endTime = currentTime
    } else if (entryHash[endMark]) {
      endTime = entryHash[endMark].startTime
    } else {
      throw Error(`Failed to execute 'measure' on 'Performance': The mark '${endMark}' dose not exist.`)
    }

    const entry = new ShadowPerformanceEntry(name, 'measure', startTime, endTime - startTime)

    entryList.push(entry)
    entryHash[name] = entry
  })

  setupPolyfill('clearMarks', name => {
    removeEntry('mark', name)
  })

  setupPolyfill('clearMeasures', name => {
    removeEntry('measure', name)
  })

  setupPolyfill('getEntries', filter => {
    const entries = getEntryList()

    if (isUndef(filter)) {
      return entries
    }

    let callback

    if (filter.entryType) {
      if (filter.name) {
        callback = entry => entry.entryType === filter.entryType && entry.name === filter.name
      } else {
        callback = entry => entry.entryType === filter.entryType
      }
    } else if (filter.name) {
      callback = entry => entry.name === filter.name
    }

    return entries.filter(callback)
  })

  setupPolyfill('getEntriesByName', (name, type) => {
    const filter = { name }

    if (type) {
      filter.entryType = type
    }

    return global.performance.getEntries(filter)
  })

  setupPolyfill('getEntriesByType', type => global.performance.getEntries({
    entryType: type,
  }))

  global.performance.setupPolyfill = setupPolyfill
  global.performance.setNavigationStart = func => {
    navigationStart = func
  }

  return global.performance
}
