import { commonChecks } from '@xhs/ozone-detector';
import { addCallback } from './callbacks';
import { SchemaErrorCode } from '../utils/type';
import SchemaError from '../utils/SchemaError';
import { transformArgs, transformRes } from '../utils/transform';
import { addMeasure } from '../utils/addMeasure';
import { bridgeNotSupport, } from '../utils/bridgeConsole';
import { genBridgeQueue, useInterceptor as useInterceptorHandler } from './interceptor';
function isSupportInvoke() {
    const handler = commonChecks.isIOS ? window?.webkit?.messageHandlers : window?.XHSBridge;
    return handler && !!handler.invoke;
}
/**
 * Web平台Bridge方法请求
 * @param method Bridge方法名
 * @param args Bridge方法参数
 */
export function invoke(method, args) {
    const startTime = Date.now();
    return new Promise((resolve, reject) => {
        try {
            // 是否支持新版本的写法
            if (!isSupportInvoke()) {
                bridgeNotSupport(method);
                throw new SchemaError(SchemaErrorCode.NotSupport, 'not support invoke');
            }
            const callback = addCallback(method, (res) => {
                const timing = Date.now() - startTime;
                try {
                    const businessRes = transformRes(method, res);
                    // 只上报成功bridge的耗时
                    addMeasure({
                        platformType: 'H5',
                        bridgeName: method,
                        timing,
                    });
                    resolve(businessRes);
                }
                catch (error) {
                    addMeasure({
                        platformType: 'H5',
                        bridgeName: method,
                        isError: true,
                        errorInfo: error?.message || `${method} call error`,
                    });
                    reject(error);
                }
            });
            const postData = {
                method,
                callback,
                args,
            };
            try {
                if (commonChecks.isIOS) {
                    // 新app(spark uniik)不stringify
                    const postMessageData = (commonChecks.isSpark || commonChecks.isUniik || commonChecks.isSnowPeak || commonChecks.isInternation || commonChecks.isOdyssey || commonChecks.isCatalog) ? postData : JSON.stringify(postData);
                    window.webkit.messageHandlers.invoke.postMessage(postMessageData);
                }
                else {
                    window.XHSBridge.invoke(JSON.stringify(postData));
                }
            }
            catch (error) {
                throw new SchemaError(SchemaErrorCode.Native, error?.message);
            }
        }
        catch (error) {
            if (!(error instanceof SchemaError)) {
                reject(new SchemaError(SchemaErrorCode.UnKnow, error?.message || `[ozone-bridge] [${method}] call error`));
            }
            else {
                reject(error);
            }
        }
    });
}
export function useInterceptor(method, app) {
    return useInterceptorHandler(method, app, 'h5');
}
export function invokeInterceptor(method, args, app = 'xhs', enableValidate = true) {
    const handler = {
        fulfilled: (innerArgs) => invoke(method, transformArgs(method, innerArgs)),
        rejected: undefined,
    };
    return genBridgeQueue(method, args, handler, {
        type: 'h5',
        app,
        enableValidate,
    });
}
export function invokeInterceptorWithoutValidate(method, args, app = 'xhs') {
    return invokeInterceptor(method, args, app, false);
}
/* eslint-disable @typescript-eslint/no-unused-vars */
export function invokeSync(method, args) {
    throw new SchemaError(SchemaErrorCode.NotSupport, 'not support invokeSync');
}
/* eslint-enable @typescript-eslint/no-unused-vars */
