/* eslint-disable no-continue */
import PerfTiming from '../../timing'
import { REGEXP_HOSTNAME } from '../../consts'
import { isForbiddenUrl } from '../../helpers'
import { isFunc, isUndef } from '../../helpers/typeor'

class BrowserResourceTiming extends PerfTiming {
  timing() {
    const resources = window.performance.getEntriesByType('resource')
    const jsonResources = this.filterResources(resources)

    const payload = jsonResources.map(entry => {
      const jsonEntry = entry.toJSON()

      // Add hostname
      try {
        jsonEntry.hostname = jsonEntry.name.match(REGEXP_HOSTNAME)[1]
      } catch (ex) {
        // Do nothing
      }

      // TODO
      delete jsonEntry.serverTiming
      delete jsonEntry.workerTiming

      return jsonEntry
    })

    // clear the buffer for next track
    if (isFunc(window.performance.clearResourceTimings)) {
      window.performance.clearResourceTimings()
    }

    return payload
  }

  filterResources(resources = []) {
    const result = []

    for (let i = 0; i < resources.length; i++) {
      const entry = resources[i]

      if (isUndef(entry.toJSON) || !isFunc(entry.toJSON)) {
        continue
      }

      if (entry.name.indexOf('data:') === 0) {
        continue
      }

      // skip non-resources URLs
      if (entry.name.indexOf('about:') === 0 || entry.name.indexOf('javascript:') === 0) { // eslint-disable-line
        continue
      }

      // skip URLs we don't want to report
      if (isForbiddenUrl(entry.name)) {
        continue
      }

      result.push(entry)
    }

    return result
  }
}

export default new BrowserResourceTiming()
