import { commonChecks, getOS, getBrowserVersion } from '@xhs/ozone-detector'
import { getDeviceInfo, confirmAntiSpam } from '@xhs/ozone-bridge'
import throttle from 'lodash.throttle'
import Cookies from 'js-cookie'
import { tryGenDeviceFingerprint } from './genDeviceFingerprint'
import reportBroswerInfo  from './reportBroswerInfo'
import { PULL_BLOCK_STATUS, NONE_FINGERPRINT_STATUS,LOCAL_ID_SECRET_VERSION,NEED_XSCOMMON_URLS ,LOCAL_ID_KEY,MINI_BROSWER_INFO_KEY, SIGN_COUNT_KEY, RC4_SECRET_VERSION, WEB_ID_KEY } from './const'
import { getCaptcha } from './getShumeiCaptcha'
import { getHost,genRandomString ,getPlatformCode } from './utils'
import { mcr,b64Encode ,encodeUtf8, crc32 } from './obfuscated/encrypt'
import { ANTI_SPAM_PLUGIN_VERSION } from './version'
import riskHandler from './interceptors/riskHandler'
import signAdaptor from './interceptors/signAdaptor'
import { MD5 } from './obfuscated/sign'

const AntiSpamPlugin = {
  name: 'AntiSpam',

  install(app, Launcher, options = {}) {
    if (!Launcher.isBrowser) {
      return
    }

    const {
      callFrom = 'web',
      carryDeviceInfo = false,
      includes = ['www.xiaohongshu.com'],
      process461Response = true,
      captchaCallback,
      captchaOption,
      captchaInCurrent = false,
    } = options
    const platform = getOS()


    if (commonChecks.isXHS && carryDeviceInfo) {
      const { major, minor, patch } = getBrowserVersion()
      const getDeviceInfoPromise = getDeviceInfo()

      const xyCommonParams = new URLSearchParams()

      xyCommonParams.append('platform', platform)
      xyCommonParams.append('versionName', `${major}.${minor}.${patch}`)

      Launcher.http.interceptors.request.use(async config => {
        try {
          if (!xyCommonParams.has('deviceId')) {
            const {
              deviceId, // Android only
              uniqueId,
              deviceFingerprint,
              deviceFingerprint1,
              fid,
            } = await getDeviceInfoPromise

            xyCommonParams.append('deviceId', deviceId || uniqueId)
            xyCommonParams.append('device_fingerprint', deviceFingerprint)
            xyCommonParams.append('device_fingerprint1', deviceFingerprint1)
            xyCommonParams.append('fid', fid)
          }
        } catch (error) {
          // eslint-disable-line
        }

        for (const include of includes) {
          if (config.url.includes(include)) {
            config.headers['xy-common-params'] = xyCommonParams.toString()

            break
          }
        }

        return config
      })
    }

    Launcher.http.interceptors.dispatch.use(signAdaptor.bind(null, Launcher))

    // 生成local_id并存储在cookie
    if(!(Cookies.get(LOCAL_ID_KEY) && Cookies.get(WEB_ID_KEY))) {
      const platformCode = getPlatformCode(platform)//TODO 平台码
      const remain = '000' // 预留位
      const origin = `${(+new Date()).toString(16)}${genRandomString(30)}${platformCode}${LOCAL_ID_SECRET_VERSION}${remain}`
      const crc = crc32(origin)
      const localId = `${origin}${crc}`.substring(0,52)
      Cookies.set(LOCAL_ID_KEY, localId, { domain: 'xiaohongshu.com', expires: 365 })
      Cookies.set(WEB_ID_KEY, MD5(localId), { domain: 'xiaohongshu.com', expires: 365 })
    }

    Launcher.http.interceptors.dispatch.use(requestConfig => {
     
      const needXSCommon = NEED_XSCOMMON_URLS.some(function (url) {
       return  new RegExp(url).test(requestConfig.url) 
      })
    
      if(needXSCommon){
        const xt = requestConfig.headers['X-t']||''
        const xs = requestConfig.headers['X-s']||''
        const xSign = requestConfig.headers['X-Sign']||''
        let sigCount = Number(sessionStorage.getItem(SIGN_COUNT_KEY)) || 0
        if((xt && xs) || xSign) {
          sigCount++
          sessionStorage.setItem(SIGN_COUNT_KEY, sigCount)
        }

        const xscommon = {
            s0: getPlatformCode(platform), //TODO  0-web/h5 1-小程序
            s1: '', //TODO  wx/qq/bd 
            x0: RC4_SECRET_VERSION, 
            x1: ANTI_SPAM_PLUGIN_VERSION , 
            x2: platform || 'PC', 
            x3: process.env.APP_NAME,
            x4: process.env.APP_VERSION,
            x5: Cookies.get(LOCAL_ID_KEY), 
            x6: xt,
            x7: xs,
            x8: localStorage.getItem(MINI_BROSWER_INFO_KEY), // 环境检测字段和部分指纹信息
            x9: mcr(`${xt}${xs}${localStorage.getItem(MINI_BROSWER_INFO_KEY)}`), 
            x10: sigCount
        }
        requestConfig.headers['X-S-Common'] = b64Encode(encodeUtf8(JSON.stringify(xscommon)))
      }
      
      return requestConfig
    })

    Launcher.http.interceptors.spam.use(async response => {
      if (response && response.status) {
        const { status } = response

        if (status === PULL_BLOCK_STATUS && process461Response) {
          confirmAntiSpam()
            .catch(throttle(() => {
              if (captchaCallback) {
                getCaptcha(captchaCallback, captchaOption)
                return response
              }
              const biz = response.headers && response.headers['x-biz']
              if (captchaInCurrent) {
                function captchaFunc({ rid, pass, deviceId }) {
                  let burdockHost = getHost()

                  return Launcher.http.post(`${burdockHost}/fe_api/burdock/v2/shield/captcha?c=pp`, {
                      biz,
                      rid,
                      callFrom,
                      deviceId,
                      status: pass ? 1 : 0,
                    }
                    , {
                      withCredentials: true,
                    })
                }
                getCaptcha(captchaFunc, captchaOption)
                return response
              }
              let url = `https://www.xiaohongshu.com/web-login/captcha?redirectPath=${window.location.href}&callFrom=${callFrom}`

              if (biz) {
                url = `${url}&biz=${biz}`
              }
              window.location.href = url
            }, 5000))
        } else if (status === NONE_FINGERPRINT_STATUS) {
          const result = await tryGenDeviceFingerprint(Launcher.http, { callFrom }, response)

          return result
        }
      }
      return response
    })

    
    Launcher.http.interceptors.spam.use(riskHandler)

    reportBroswerInfo(Launcher, { ...options, platform })

  },
}

export { getCaptcha }

export default AntiSpamPlugin
