import { getHost } from './utils'
import { rc_enc } from './obfuscated/encrypt'
import { RC4_SECRET ,MINI_BROSWER_INFO_KEY, PROFILE_BLOCKED_PATHS, PROFILE_COUNT_KEY, PROFILE_TRIGGER_TIME_KEY, PROFILE_SERVER_TIME_KEY, DES_SECRET_VERSION } from './const'
import { ANTI_SPAM_PLUGIN_VERSION } from './version'

const REPORT_BROSWER_INFO_PATH = '/fe_api/burdock/v2/shield/profile'



const report = async (Launcher,options)=>{
  window.xhsFingerprintV3.getV18(async (miniComponents,encryptString) => {
    const data = {
      platform: options.platform || 'PC',
      sdkVersion:ANTI_SPAM_PLUGIN_VERSION,
      svn: DES_SECRET_VERSION, // des密钥版本
      profileData: encryptString,
    }
    const miniUA = rc_enc(RC4_SECRET, JSON.stringify(miniComponents))
    localStorage.setItem(MINI_BROSWER_INFO_KEY, miniUA)

    const startTime = Date.now()

    let burdockHost = getHost()

    Launcher.http.post(`${burdockHost}${REPORT_BROSWER_INFO_PATH}`, data, {
      baseURL: burdockHost,
      withCredentials: true,
      transform: false,
      extractData: false
    })
    .then((res) => {
      if( res.code !== 0 || !res.success ) throw new Error(JSON.stringify(res))
      localStorage.setItem(PROFILE_TRIGGER_TIME_KEY ,startTime)
      localStorage.setItem(PROFILE_SERVER_TIME_KEY ,res.data?.st)
      localStorage.setItem(PROFILE_COUNT_KEY ,(Number(localStorage.getItem(PROFILE_COUNT_KEY)) || 0) + 1)
    })
    .catch(err=>{
      Launcher.logger.error('profile error', data,err)
    })
  })
}

export default async function reportBroswerInfo(Launcher, options) {
  const currentPath = new URL(window.location.href)?.pathname
  const isBlockProfile = PROFILE_BLOCKED_PATHS.some(function (blockedPath) {
    return blockedPath === currentPath
   })
  if(isBlockProfile) return 

  if (window.xhsFingerprintV3) {
   report(Launcher,options)
  } else {
    const fpScript = document.createElement('script')
    fpScript.src = "https://fe-video-qc.xhscdn.com/fe-platform/597bb5e4d4df1318ce37423c6e2e40401a9eddf1.js?attname=fe-platform/597bb5e4d4df1318ce37423c6e2e40401a9eddf1.js.js"
    // fpScript.src = 'https://fe-video-qc.xhscdn.com/fe-platform/5361a82abb83e59519eb98ac9bca3b5ae29a4cde.js?attname=fe-platform/5361a82abb83e59519eb98ac9bca3b5ae29a4cde.js.js'
    fpScript.type = 'text/javascript'
    fpScript.onload = () => {
      report(Launcher,options)
    }
    document.body.appendChild(fpScript)
  }
}
