/**
 * 获取 webp 的链接
 * @param src
 * @param width
 */
function getSrcWebpBySrc(src?:string):string {
  if (!src) {
    return ''
  }
  let srcWebp = ''
  try {
    const { search = '' } = new URL(`https:${src}`)
    let transform: string = ''
    if (search.indexOf('/format/jpg') > -1) {
      transform = search.replace('/format/jpg', '/format/webp')
    } else if (search.indexOf('/format/png') > -1) {
      transform = search.replace('/format/png', '/format/webp')
    } else {
      transform = `${search}/format/webp`
    }
    srcWebp = src.replace(search, transform)
  } catch (error) {
    // void
  }
  return srcWebp
}

export default getSrcWebpBySrc
