import { warn } from '@xhs/logger'

import {
  getSession,
  getUserInfoV2,
  checkLoginWithAction as checkLogin,
  addEventListener,
} from '@xhs/ozone-bridge'

function silenceFail() { // eslint-disable-line
}

function createServicesForNonBrowser() {
  return {
    getSid: silenceFail,
  }
}

function createServices() {
  if (typeof window === 'undefined') {
    return createServicesForNonBrowser()
  }

  // sidFromUrl 定义放到上面 createServices 入口，
  // 极端情况下可能出现 SPA 第1个页面没有任何网络调用，第2个页面才有请求，此时 url query 中已经没有 sid
  const searchParams = new URLSearchParams(window.location.search)
  const sidFromUrl = searchParams.get('sid')
  let sid

  function getSid() {
    if (typeof sid !== 'undefined') {
      return Promise.resolve(sid)
    }

    return new Promise(resolve => {
      getSession().then(ret => {
        sid = ret

        resolve(sid)
      })
        .catch(() => {
          sid = sidFromUrl

          resolve(sid)
        })
    })
  }

  function setSid(value) {
    sid = value
  }

  function resetSid() {
    sid = ''
  }

  return {
    getSid,
    setSid,
    resetSid,
  }
}

function handleHttpModule(http, authServices, passSidByQuery) {
  if (passSidByQuery) {
    warn(`[Launcher Auth]: Transport \`sid\` by query is not a recommended way any more for safety.
We recommend that your backend gateway could get user session from \`Authorization\` Header which is supported in this launcher-auth version`)
  }

  const methodsMap = ['get', 'del', 'head', 'post', 'put', 'patch']

  methodsMap.forEach(method => {
    if (http[method]) {
      const httpFn = http[method]
      const configIdxFromArgs = httpFn.length - 1 // assume the last arguments is config

      httpFn.noAuth = function xhrNoAuth(...args) {
        const config = args[configIdxFromArgs] || {}

        args[configIdxFromArgs] = { ...config, launcherNoAuth: true }

        return httpFn.apply(http, args)
      }
    }
  })

  http.interceptors.request.use(config => {
    if (!config.launcherNoAuth && !config.noAuth) {
      return authServices.getSid().then(sid => {
        if (sid) {
          if (passSidByQuery) {
            config.params = config.params || {}
            config.params.sid = sid
          }

          config.headers.Authorization = sid
        }

        return config
      })
    }

    return config
  })
}

export default function sidModule(Launcher, options, {
  passSidByQuery = true,
}) {
  const authContext = this.vm.config.globalProperties.$auth
  const authServices = authContext.services

  const sidServices = createServices()

  const afterLoginCallbacks = []
  let resolvePriorityAfterLoginCallbacks

  function resolveAfterLoginCallbacks(onlyPriority) {
    const priorityCallbacks = afterLoginCallbacks
      .filter(afterLoginCallback => afterLoginCallback.priority)
    const normalCallbacks = afterLoginCallbacks
      .filter(afterLoginCallback => !afterLoginCallback.priority)

    if (!resolvePriorityAfterLoginCallbacks) {
      resolvePriorityAfterLoginCallbacks = Promise.all(priorityCallbacks
        .map(priorityCallback => priorityCallback.callback()))
    }

    return resolvePriorityAfterLoginCallbacks.then(() => {
      if (!onlyPriority) {
        normalCallbacks.map(priorityCallback => priorityCallback.callback())
      }
    })
  }

  function registerAfterLoginCallback(callback, priority) {
    afterLoginCallbacks.push({
      priority,
      callback,
    })
  }

  function checkLoginWithAction({
    type = 0,
  } = {}) {
    return new Promise(resolve => {
      getUserInfoV2().then(({ value: userInfo }) => {
        const isEverLogin = userInfo && userInfo.guest === false

        checkLogin({ type }).then(response => {
          if (response.result === 0) { // 一顿操作后，已登录
            if (isEverLogin) { // 本来就已经登录
              resolve({
                isLogin: true,
                loginChange: false,
              })
            } else { // 本来没登录
              resolveAfterLoginCallbacks(true).then(() => {
                resolve({
                  isLogin: true,
                  loginChange: true,
                })
              })
            }
          } else { // 一顿操作后，未登录
            resolve({
              isLogin: false,
              loginChange: false,
            })
          }
        })
          .catch(() => {
            resolve({
              isLogin: true,
              loginChange: false,
            })
          })
      })
        .catch(() => {
          resolve({
            isLogin: true,
            loginChange: false,
          })
        })
    })
  }

  registerAfterLoginCallback(() => (
    new Promise(resolve => {
      getSession().then(sid => {
        sidServices.setSid(sid)

        resolve()
      })
        .catch(() => {
          // eslint-disable-line
        })
    })
  ), true)

  // TODO
  // 现在的实现下对于当前 view 触发的 checkLoginWithAction 行为
  // high priority 的 callbacks 会分别被 checkLoginWithAction 的回调 和 afterLogin 的通知调用 2 次
  // 更好的做法是 callbacks 均在 afterLogin 的通知中触发
  // checkLoginWithAction 中接收的回调携带是否有登录的信息，决定是否要等 callbacks resolve 完成后再 resolve promise
  addEventListener('afterLogin', () => {
    resolveAfterLoginCallbacks()
  })

  // Bind $auth
  Object.assign(authServices, sidServices, {
    registerAfterLoginCallback,
    checkLoginWithAction,
    onLoginChange(callback) {
      registerAfterLoginCallback(callback)
    },
  })

  // Bind noAuth method to http
  handleHttpModule(Launcher.http, authServices, passSidByQuery)
}
