import { isFunc, isRegExp } from './helpers/typeor'

export default class PerfTiming {
  timing() {
    return {}
  }

  responseCatchHook(payload, err, config) { // eslint-disable-line
    return payload
  }

  observe() {
    // TODO
  }

  /**
   * Vue-like validator
   * @param {Array} props rules
   * {
   *  prop: String,
   *  required: Boolean,
   *  validator: Function | RegExp,
   *  default: Any
   * }
   */
  validate(options, props) {
    while (props.length) {
      const param = props.shift()

      if (!param.prop) {
        break
      }

      if (param.default) {
        options[param.prop] = param.default
      }

      if (param.required && !options[param.prop]) {
        throw new Error(`[${this.constructor.name}] Property '${param.prop}' is missing`)
      }

      let valid = true

      if (param.validator && isFunc(param.validator)) {
        valid = param.validator(options[param.prop], options)
      } else if (param.pattern && isRegExp(param.pattern)) {
        valid = param.pattern.test(options[param.prop])
      }

      if (!valid) {
        throw new TypeError(`[${this.constructor.name}] Property '${param.prop}' is invalid`)
      }
    }
  }
}
