import { commonChecks } from '@xhs/ozone-detector'
import { invoke, GetDeviceInfoRes } from '@xhs/ozone-schema'

import type { IEagletOps } from '../../types/plugin'

interface IDeviceBuilderRes {
  appVersion?: string
  systemVersion?: string
  uniqueId?: string
  appMarket?: string
  buildId?: string
  bundleId?: string
  buildNumber?: string
  cold_start_id? :number
  idfa?: string
  idfv?: string
  deviceModel?: string
  manufacturer?: string
  timezone?: string
  deviceScreenWidth?: number
  deviceScreenHeight?: number
}
interface IOtherDeviceData {
  deviceId?: string
  gid?: string
}

interface IDeviceBuilderV2Return {
  context_nameTracker: string
  // 平台
  context_platform: string
  // app 版本
  context_appVersion: string
  // 设备版本
  context_osVersion?: string
  // 设备机型
  context_deviceModel: string
  // 设备 ID
  context_deviceId: string
  // 区分 app，小红书 com.xingyin.discovery, 商家版 com.xingyin.eva
  context_package?: string
}

type IDeviceBuilderV2 = (res?: IDeviceBuilderRes, otherDeviceData?: IOtherDeviceData) => IDeviceBuilderV2Return

interface IGetDeviceInfoReturn {
  app: {
    type: string
    value: {
      nameTracker: {
        type: string
        value: string
      }
      platform: {
        type: string
        value: string
      }
      appVersion: string
      appMarket?: string
      buildId?: string
      coldStartId?: number
      package?: string
    }
  }
  mob: {
    type: string
    value: {
      osVersion?: string
      dvceModel?: string
      dvceManufacture?: string
      timezone?: string
      dvceScreenWidth?: number
      dvceScreenHeight?: number
    }
  }
  dvc: {
    type: string
    value: {
      idfa?: string
      idfv?: string
      dvceId?: string
      gid: string
    }
  }
}
// type IGetDeviceInfo = (options: IEagletOps) => Promise<IGetDeviceInfoReturn>

// type IGetDeviceInfoV2 = (options: IEagletOps) => Promise<IDeviceBuilderV2Return>

type IDeviceBuilder = (res?: IDeviceBuilderRes, otherDeviceData?: IOtherDeviceData | Promise<Record<string, unknown>>) => IGetDeviceInfoReturn

const judgePlatform = () => {
  if (commonChecks.isIOS) {
    if (commonChecks.isXHS) {
      return 'iOS'
    }
    return 'iOSBrowser'
  }

  if (commonChecks.isAndroid) {
    if (commonChecks.isXHS) {
      return 'Android'
    }
    return 'AndroidBrowser'
  }

  return 'PC'
}

const placeholder: IDeviceBuilderRes = {
  appVersion: '0.0.0',
  systemVersion: 'unknow',
  uniqueId: '',
  appMarket: '',
  buildId: '',
  bundleId: '',
}

const deviceBuilder: IDeviceBuilder = (res = placeholder, otherDeviceData) => {
  const deviceInfo = {
    app: {
      type: 'App',
      value: {
        nameTracker: {
          type: 'NameTracker',
          value: 'wapT',
        },
        platform: {
          type: 'Platform',
          value: judgePlatform(),
        },
        appVersion: `discovery-${res.appVersion}`,
        appMarket: res.appMarket,
        buildId: res.buildNumber,
        coldStartId: res.cold_start_id,
        package: res.bundleId,
      },
    },
    mob: {
      type: 'Mobile',
      value: {
        osVersion: res.systemVersion,
        dvceModel: res.deviceModel,
        dvceManufacture: res.manufacturer,
        timezone: res.timezone,
        dvceScreenWidth: res.deviceScreenWidth,
        dvceScreenHeight: res.deviceScreenHeight,
      },
    },
    dvc: {
      type: 'Device',
      value: {
        idfa: res.idfa,
        idfv: res.idfv,
        dvceId: res.uniqueId || (otherDeviceData as IOtherDeviceData)?.deviceId || '',
        gid: (otherDeviceData as IOtherDeviceData)?.gid || '',
      },
    },
  }

  return deviceInfo
}

const deviceBuilderV2: IDeviceBuilderV2 = (res = placeholder, otherDeviceData) => {
  const deviceInfo = {
    // 项目类型
    context_nameTracker: 'wapT',
    // 平台
    context_platform: judgePlatform(),
    // app 版本
    context_appVersion: `discovery-${res.appVersion}`,
    // 设备版本
    context_osVersion: res.systemVersion,
    // 设备机型
    context_deviceModel: res.deviceModel || '',
    // 设备 ID
    context_deviceId: res.uniqueId || otherDeviceData?.deviceId || '',
    // 区分 app，小红书 com.xingyin.discovery, 商家版 com.xingyin.eva
    context_package: res.bundleId,
  }

  return deviceInfo
}

export const getDeviceInfoV2 = (options: IEagletOps) => new Promise(resolve => {
  // 端内
  if (commonChecks.isXHS) {
    invoke('getDeviceInfo')
      .then(data => {
        if (!data.value) {
          resolve(deviceBuilderV2())
        } else {
          resolve(deviceBuilderV2(data.value))
        }
      })
      .catch(() => {
        resolve(deviceBuilderV2())
      })
  } else if (options.getDeviceInfo) { // 端外
    options.getDeviceInfo()
      .then(value => {
        resolve(deviceBuilderV2({}, value))
      })
      .catch(() => {
        resolve(deviceBuilderV2())
      })
  } else {
    resolve(deviceBuilderV2())
  }
})

const getDeviceInfo: (options: IEagletOps) => Promise<IGetDeviceInfoReturn> = (options: IEagletOps) => new Promise(resolve => {
// 端内,支持小红书和商家
  if (commonChecks.isXHS || commonChecks.isXhsMerchant) {
    invoke('getDeviceInfo')
      .then((data: GetDeviceInfoRes) => {
        if (!data.value) {
          resolve(deviceBuilder())
        } else {
          resolve(deviceBuilder(data.value))
        }
      })
      .catch(() => {
        resolve(deviceBuilder())
      })
  } else if (options.getDeviceInfo) { // 端外
    options.getDeviceInfo()
      .then(value => {
        resolve(deviceBuilder({}, value))
      })
      .catch(() => {
        resolve(deviceBuilder())
      })
  } else {
    resolve(deviceBuilder())
  }
})

export default getDeviceInfo
