/* eslint-disable no-console, no-underscore-dangle */
import { logger } from '@xhs/launcher'
import { showTrack } from '@xhs/ozone-bridge'
import EmitterBase from '@xhs/eaglet-emitter-base'
import { checkTrackEnv } from '../helpers/get-track-env'
import { sendByDefault } from './client'

import type { IHTTPFromAxiosTrackerResponseData, AxiosResponse } from '../types/emitter/client'

import type { IOptions, IJspb, ISerializeBinary } from '../types/emitter/default-emitter'

import type { ITrackerExtendDataPayloadHttpReturn } from '../types/plugin'

let isNativeDebugOn: boolean

const showTrackerMessage = (title: string, payload: AxiosResponse<IHTTPFromAxiosTrackerResponseData> | unknown) => {
  const content = JSON.stringify(payload)

  showTrack({
    title,
    content,
    isNewTrack: true,
  }).catch(() => { /* No-op */ })
}

export default class DefaultEmitter extends EmitterBase {
  isLite?: boolean

  localDebug?: boolean

  preferNative?: boolean | ((params: (flagName: string | string[]) => Promise<unknown>) => Promise<boolean>) | Promise<boolean>

  constructor(options: IOptions) {
    super(options)

    this.isLite = options.isLite
    this.localDebug = options.debug
    this.preferNative = options.preferNative
  }

  shouldFlushBuffer() {
    return true
  }

  serializeBinary(message: ISerializeBinary, BinaryWriter: IJspb['BinaryWriter']) {
    const writer = new BinaryWriter()

    const messageArr = message.serializeBinary()

    writer.encoder_.writeSignedVarint64(messageArr.byteLength + 4)
    writer.encoder_.writeInt32(this.versionHash)
    writer.encoder_.writeBytes(messageArr)
    const ret = writer.getResultBase64String()

    writer.reset()

    return ret
  }

  sendData(dataString: string) {
    const remoteUrl = (isNativeDebugOn || this.localDebug)
      ? this.endpoint.development
      : this.endpoint.production

    sendByDefault(remoteUrl, dataString, {
      preferNative: this.preferNative,
    })
      .then(res => {
        if (!res) {
          return
        }

        if (isNativeDebugOn) {
          showTrackerMessage('success', res)
        }

        if (this.localDebug) {
          const { tracker, pointId } = res.data as ITrackerExtendDataPayloadHttpReturn
          const groupPath = [
            tracker.event.action,
            tracker.event.target_type,
            tracker.page.page_instance,
          ].filter(k => !!k).join('.')

          console.group(`[eaglet] ${groupPath}`)
          console.log(`%c${pointId}`, 'padding:0 2px;background:green;color:white', tracker)
          console.groupEnd()
        }
      })
      .catch((err: unknown) => {
        if (isNativeDebugOn) {
          showTrackerMessage('error', err)
        }

        if (this.localDebug) {
          console.group('%c[eaglet error] 错误的字段类型或枚举值不存在', 'color:red')
          // @ts-ignore reason: err 数据类型未知
          logger.error(err.detail || err)
          console.groupEnd()
        }
      })
  }

  flush() {
    if (!this.buffer.length) {
      return
    }
    const tasks: Promise<boolean | IJspb| unknown >[] = [checkTrackEnv]

    if (!this.isLite) {
      // @ts-ignore reason: 动态导入 google-protobuf 文件不需要该依赖声明文件
      tasks.push(import('google-protobuf'))
    }

    Promise.all(tasks).then(([nativeDebugOn, jspb]) => {
      isNativeDebugOn = nativeDebugOn as boolean

      if (!this.isLite) {
        while (this.buffer.length) {
          const message = this.buffer.shift() as ISerializeBinary
          this.sendData(this.serializeBinary(message, (jspb as IJspb).BinaryWriter))
        }
      } else {
        while (this.buffer.length) {
          const message = this.buffer.shift() as string

          this.sendData(message)
        }
      }
    })
  }
}
