// @ts-nocheck reason: 后续流量来源代码需要下掉，暂不进行 TS 校验
// eslint-disable-next-line import/no-unresolved
import { invoke, InvokeArgs, SpvType } from '@xhs/ozone-schema'
import { commonChecks } from '@xhs/ozone-detector'

import type { ITrackerExtendDataPayload, ITrackerContent } from '../types/plugin'

const APPVERSION = {
  android: '7617000',
  ios: '7700000',
}
const actions = ['pageview', 'page_end', 'page_info', 'impression', 'modal_show', 'modal_hide', 'popup_show', 'popup_hide']
// 0、是未知   1、default  2、back  3、foreground
const appearListCases = ['undefined', 'default', 'back', 'foreground']

let referPath = ''
const spvType = {
  value: 'default' as SpvType,
}

const judgePlatform = () => {
  if (commonChecks.isIOS) {
    if (commonChecks.isXHS) {
      return 'iOS'
    }
    return 'iOSBrowser'
  }
  if (commonChecks.isAndroid) {
    if (commonChecks.isXHS) {
      return 'Android'
    }
    return 'AndroidBrowser'
  }
  return 'PC'
}
const getPointInfo = (data: ITrackerExtendDataPayload, trackerBuilder: ITrackerContent['builder']) => {
  let pageInstanceStr = ''
  let referKey = ''
  let pointId = 0
  let pageInstance = 0
  let action = 0
  let actionInteractionType = 0
  let targetType = 0
  let targetDisplayType = 0

  if (data.event?.value?.pointId) {
    pointId = data.event.value.pointId
  }
  if (data.page?.value?.pageInstance?.value) {
    pageInstanceStr = data.page.value.pageInstance.value
    const value = data.page.value.pageInstance.value.toUpperCase()
    pageInstance = trackerBuilder?.PageInstance[`${value}`] || 0
  }
  if (data.event?.value?.action?.value) {
    const value = data.event.value.action.value.toUpperCase()
    action = trackerBuilder?.NormalizedAction[`${value}`] || 0
  }
  if (data.event?.value?.actionInteractionType?.value) {
    const value = data.event.value.actionInteractionType.value.toUpperCase()
    actionInteractionType = trackerBuilder?.ActionInteractionType[`${value}`] || 0
  }
  if (data.event?.value?.targetType?.value) {
    const value = data.event.value.targetType.value.toUpperCase()
    targetType = trackerBuilder?.RichTargetType[`${value}`] || 0
  }
  if (data.event?.value?.targetDisplayType?.value) {
    const value = data.event.value.targetDisplayType.value.toUpperCase()
    targetDisplayType = trackerBuilder?.TargetDisplayType[`${value}`] || 0
  }
  referKey = `${pageInstance}^${action}^${actionInteractionType}^${targetType}^${targetDisplayType}`
  return {
    pointId, pageInstanceStr, pageInstance, action, actionInteractionType, targetType, targetDisplayType, referKey,
  }
}

async function flowSourcePointProcess(data: ITrackerExtendDataPayload, trackerBuilder: ITrackerContent['builder']): Promise<ITrackerExtendDataPayload> {
  const appVersion = data?.app?.value?.buildId as string
  // platform
  const platform = judgePlatform()
  if (platform === 'PC' || platform === 'iOSBrowser' || platform === 'AndroidBrowser') {
    return data
  }
  // android device
  if (platform === 'Android' && appVersion < APPVERSION.android) {
    return data
  }
  // ios device
  if (platform === 'iOS' && appVersion < APPVERSION.ios) {
    return data
  }
  if (data.event?.value?.action?.value === 'pageview') {
    const { pointId, pageInstanceStr, referKey } = getPointInfo(data, trackerBuilder)
    const addValue: InvokeArgs['addApmReferV2'] = {
      type: 2,
      spv_type: spvType.value,
      page_instance: pageInstanceStr,
    }
    if (pointId) {
      addValue.point_id = pointId
    } else {
      addValue.point_id = 0
      addValue.refer_key = referKey
    }
    try {
      await invoke('addApmReferV2', addValue)
    } catch (error) {
      const errorMessagePattern = /^(addApmReferV2).*-12002_.*/i
      if (error.message && errorMessagePattern.test(error.message)) {
        const errorCodeType = error.message.split(':')[1].trim()
        referPath = `[{"t":"E","m":"${errorCodeType}"}]`
      } else {
        referPath = '[{"t":"E","m":"-1010"}]'
      }
      data.event.value.rp = referPath
      if (data.page?.value) {
        data.page.value.spvType = addValue.spv_type
      }
      spvType.value = appearListCases[1]
      return data
    }
    try {
      const referPathValue = await invoke('getApmReferPathV2', {})
      referPath = referPathValue?.value
    } catch (error) {
      referPath = '[{"t":"E","m":"-1010"}]'
      data.event.value.rp = referPath
      if (data.page?.value) {
        data.page.value.spvType = addValue.spv_type
      }
      spvType.value = appearListCases[1]
      return data
    }
    data.event.value.rp = referPath
    if (data.page?.value) {
      data.page.value.spvType = addValue.spv_type
    }
    spvType.value = appearListCases[1]
  }
  if (actions.indexOf(data.event?.value?.action?.value) === -1) {
    const { pointId, pageInstanceStr, referKey } = getPointInfo(data, trackerBuilder)
    const addValue: InvokeArgs['addApmReferV2'] = {
      type: 1,
      page_instance: pageInstanceStr,
    }
    if (pointId) {
      addValue.point_id = pointId
    } else {
      addValue.point_id = 0
      addValue.refer_key = referKey
    }
    try {
      await invoke('addApmReferV2', addValue)
    } catch (error) {
      const errorMessagePattern = /^(addApmReferV2).*-12002_.*/i
      if (error.message && errorMessagePattern.test(error.message)) {
        const errorCodeType = error.message.split(':')[1].trim()
        referPath = `[{"t":"E","m":"${errorCodeType}"}]`
      } else {
        referPath = '[{"t":"E","m":"-1010"}]'
      }
      if (data.event?.value) {
        data.event.value.rp = referPath
      }
      return data
    }
  }
  if (data.event?.value) {
    data.event.value.rp = referPath
  }
  return data
}

/* eslint-enable */
function flowSourceConfig() {
  window.addEventListener('customPopState', event => {
    spvType.value = event.detail.spvType
  })
}

export {
  flowSourcePointProcess, flowSourceConfig, appearListCases, spvType,
}
