export function basicSendClientRequest(config) {
  return adapter('sendClientRequest', config)
}

export function sendClientRequest(data) {
  console.warn(
    '[OzoneBridge] sendClientRequest was deprecated, please use [Launcher http](https://code.devops.xiaohongshu.com/formula/launcher/tree/master/src/http)',
  ) // eslint-disable-line no-console

  const spec = {
    argsT: T.shape({
      url: T.string,
      type: T.oneOf(['GET', 'POST', 'PUT', 'DELETE']).isRequired,
      data: T.oneOfType([T.object]),
      transform: T.oneOfType([T.bool, T.shape({ separateNumber: T.bool }), T.func]),
    }),
    resT: T.shape({
      result: resultType,
      response: T.oneOfType([T.object]),
      status: T.number,
    }),
  }

  checkArgs(data, spec.argsT, 'sendClientRequest')

  const cloneData = { ...data }
  const transformOpts = cloneData.transform

  if (transformOpts && typeof cloneData.data === 'object') {
    cloneData.data = toPythonStyle(cloneData.data, transformOpts)
    delete cloneData.transform
  }

  return basicSendClientRequest(cloneData)
    .then(res => {
      checkRes(res, spec.resT)

      if (transformOpts) {
        return toJsStyle(res, transformOpts)
      }

      return res
    })
    .then(resp => {
      // iOS的业务报错形式为{ result: -1, status: xxx }
      if (resp.result === -1) {
        const error = new Error()
        error.status = resp.status
        throw error
      }

      const { response } = resp

      // 安卓的业务错误会放在这里
      if (
        (typeof response.success === 'undefined' && response.result !== 0)
        || response.success === false
      ) {
        const error = new Error()
        error.status = response.result
        throw error
      }

      return response.data || response
    })
}

export function sendClientRequestV2(config) {
  const newConfig = {
    ...config,
    url: completeUrl(config.url),
  }

  // FIXME: iOS 处理了业务错误信息（response.body.result|success)，并且没有返回给我们，只能回退到 sendClientRequest
  if (isSupport('sendClientRequestV2') && commonChecks.isAndroid) {
    return adapter('sendClientRequestV2', newConfig)
  }

  return basicSendClientRequest(newConfig).then(res => {
    const { result, status, response } = res

    /**
     * Android 的 status 一般为状态码，为 -1 时表示网络错误
     */
    if (commonChecks.isAndroid) {
      if (result === -1) {
        if (status !== -1) {
          return {
            result: 0,
            response: {
              status,
            },
          }
        }

        return {
          result,
        }
      }

      return {
        result,
        response: {
          status: 200,
          body: response,
        },
      }
    }

    /**
     * iOS 的 res.status === res.response.status，没有使用价值
     */
    if (result === -1) {
      // result 返回了 -1，但是不能确定具体的状态码
      if (response) {
        return {
          result: 0,
          response: {
            status: 200,
            body: response,
          },
        }
      }

      return {
        result,
      }
    }

    return {
      result,
      response: {
        status: 200,
        body: response,
      },
    }
  })
}

export function sendClientRequestV3(config) {
  let headers = { ...config.headers }
  const requestMethod = config.method.toUpperCase()
  const { 'Content-Type': contentType, ...restHeaders } = headers
  /**
   * sendClientRequestV3 边界case:
   * 1、requestMethod 是 post, put, delete 的场景，需要有 Content-Type
   * 2、contentType 没有指定或者不是 'application/x-www-form-urlencoded' | 'application/json' 二者之一，则默认指定为 'application/json'
   */
  if (
    requestMethod !== 'GET'
    && !['application/x-www-form-urlencoded', 'application/json'].includes(contentType)
  ) {
    headers['Content-Type'] = 'application/json'
  } else if (requestMethod === 'GET' && contentType) {
    // iOS 平台 get 请求不能有 Content-Type
    headers = restHeaders
  }

  // headers 域需要前端转换为 string
  Object.keys(headers).forEach(key => {
    if (typeof headers[key] !== 'string') {
      headers[key] = String(headers[key])
    }
  })

  const newConfig = {
    ...config,
    url: completeUrl(config.url),
    headers,
  }

  return invoke('sendClientRequestV3', newConfig)
}

