export async function shortVibrate() {
  if (commonChecks.isIOS) {
    // iOS 通过 bridge 实现短震动
    return invoke('shortVibrate', { id: iOSShortVibrateSourceID })
  }
  if (window?.navigator?.vibrate) {
    // 安卓平台用浏览器的实现
    window.navigator.vibrate(50)
    // 安卓的返回值和 iOS 兼容一致
    return Promise.resolve({
      message: 'success',
      result: 0,
    })
  }
  throw new BridgeError('ERR_NOT_SUPPORT', 'not support shortVibrate')
}

export async function findCalendarEvent(args) {
  try {
    const res = await invoke('authorizeCalendarStatus')
    const { value } = res || {}
    if (value === OzoneCalendarStatus.Authorized) {
      return invoke('findCalendarEvent', args)
    }
    throw new BridgeError(`calendar permission: ${value}`)
  } catch (error) {
    return Promise.reject(error)
  }
}

