export function getABFlag(args) {
  return invoke('getABFlag', args).catch(() => getUserInfo({ extractValue: true }).then(res => {
    const { key } = args
    const { flags } = res || {}
    const {
      fulishe, shequ, ios, android,
    } = flags || {}
    const platformAB = commonChecks.isIOS ? ios : android
    let value = null
    if (shequ?.[key]) {
      value = shequ[key]
    } else if (fulishe?.[key]) {
      value = fulishe[key]
    } else if (platformAB?.[key]) {
      value = platformAB[key]
    }
    return {
      result: 0,
      message: 'success',
      value,
    }
  }))
}
