const TWO_TO_32 = 4294967296
const ENCODED_VALS = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/='
const CHARMAP: {
  [props: number]: number | string
} = {}

for (let i = 0; i < ENCODED_VALS.length; i++) {
  CHARMAP[i] = ENCODED_VALS.charAt(i)
}

function splitInt64(input: number) {
  // Convert to sign-magnitude representation.
  const sign = (input < 0)
  const value = Math.abs(input)

  // Extract low 32 bits and high 32 bits as unsigned integers.
  let lowBits = value >>> 0 // eslint-disable-line
  let highBits = Math.floor((value - lowBits) / TWO_TO_32)
  highBits >>>= 0 // eslint-disable-line

  // Perform two's complement conversion if the sign bit was set.
  if (sign) {
    highBits = ~highBits >>> 0 // eslint-disable-line
    lowBits = ~lowBits >>> 0 // eslint-disable-line
    lowBits += 1
    if (lowBits > 0xFFFFFFFF) {
      lowBits = 0
      highBits += 1
      if (highBits > 0xFFFFFFFF) highBits = 0
    }
  }

  return {
    lowBits,
    highBits,
  }
}

export default {
  writeString(value: string) {
    const buffer: number[] = []

    for (let i = 0; i < value.length; i++) {
      let c = value.charCodeAt(i)

      if (c < 128) {
        buffer.push(c)
      } else if (c < 2048) {
        buffer.push((c >> 6) | 192) // eslint-disable-line
        buffer.push((c & 63) | 128) // eslint-disable-line
      } else if (c < 65536) {
        // Look for surrogates
        if (c >= 0xD800 && c <= 0xDBFF && i + 1 < value.length) {
          const second = value.charCodeAt(i + 1)
          if (second >= 0xDC00 && second <= 0xDFFF) { // low surrogate
            // http://mathiasbynens.be/notes/javascript-encoding#surrogate-formulae
            c = (c - 0xD800) * 0x400 + second - 0xDC00 + 0x10000

            buffer.push((c >> 18) | 240) // eslint-disable-line
            buffer.push(((c >> 12) & 63) | 128) // eslint-disable-line
            buffer.push(((c >> 6) & 63) | 128) // eslint-disable-line
            buffer.push((c & 63) | 128) // eslint-disable-line
            i += 1
          }
        } else {
          buffer.push((c >> 12) | 224) // eslint-disable-line
          buffer.push(((c >> 6) & 63) | 128) // eslint-disable-line
          buffer.push((c & 63) | 128) // eslint-disable-line
        }
      }
    }

    return buffer
  },
  writeInt64(value: number) {
    let { lowBits, highBits } = splitInt64(value)
    const buffer = []

    while (highBits > 0 || lowBits > 127) {
      buffer.push((lowBits & 0x7f) | 0x80) // eslint-disable-line
      lowBits = ((lowBits >>> 7) | (highBits << 25)) >>> 0 // eslint-disable-line
      highBits >>>= 7 // eslint-disable-line
    }
    buffer.push(lowBits)

    return buffer
  },
  writeInt32(value: number) {
    const buffer = []

    buffer.push((value >>> 0) & 0xFF) // eslint-disable-line
    buffer.push((value >>> 8) & 0xFF) // eslint-disable-line
    buffer.push((value >>> 16) & 0xFF) // eslint-disable-line
    buffer.push((value >>> 24) & 0xFF) // eslint-disable-line
    return buffer
  },
  writeSignedVarint32(value: number) {
    if (value >= 0) {
      return this.writeUnsignedVarint32(value)
    }
    const buffer = []
    let num = value

    for (let i = 0; i < 9; i++) {
      buffer.push((num & 0x7f) | 0x80) // eslint-disable-line
      num >>= 7 // eslint-disable-line
    }

    buffer.push(1)
    return buffer
  },
  writeUnsignedVarint32(input: number) {
    const buffer = []
    let value = input

    while (value > 127) {
      buffer.push((value & 0x7f) | 0x80) // eslint-disable-line
      value >>>= 7 // eslint-disable-line
    }
    buffer.push(value)
    return buffer
  },
  writeBoolean(value: boolean) {
    return [value ? 1 : 0]
  },
  encodeByteArray(input: Uint8Array) {
    const output = []

    for (let i = 0; i < input.length; i += 3) {
      const byte1 = input[i]
      const haveByte2 = i + 1 < input.length
      const byte2 = haveByte2 ? input[i + 1] : 0
      const haveByte3 = i + 2 < input.length
      const byte3 = haveByte3 ? input[i + 2] : 0

      const outByte1 = byte1 >> 2 // eslint-disable-line
      const outByte2 = ((byte1 & 0x03) << 4) | (byte2 >> 4) // eslint-disable-line
      let outByte3 = ((byte2 & 0x0F) << 2) | (byte3 >> 6) // eslint-disable-line
      let outByte4 = byte3 & 0x3F // eslint-disable-line

      if (!haveByte3) {
        outByte4 = 64

        if (!haveByte2) {
          outByte3 = 64
        }
      }

      output.push(
        CHARMAP[outByte1], CHARMAP[outByte2],
        CHARMAP[outByte3] || '', CHARMAP[outByte4] || '',
      )
    }

    return output.join('')
  },
}
