import Permission from './permission'

const hasImplemented = service => (
  typeof service === 'function' && !service.needImplement
)

function noop() {} // eslint-disable-line

function forEach(obj, handle) {
  for (const key in obj) {
    if (obj.hasOwnProperty(key)) {
      handle(obj[key], key)
    }
  }
}

/* ------------------------------------------------- */

function createDefaultPermission(authContext, store) {
  const authServices = authContext.services

  const isLogin = () => {
    if (store.getters.userInfo) {
      return Promise.resolve(store.getters.userInfo)
    }
    if (hasImplemented(authServices.fetchUserInfo)) {
      return authServices.fetchUserInfo()
    }
    return Promise.reject('[Launcher Auth Error] failed to check isLogin(), getUserInfo() service is not defined')
  }

  return {
    isLogin,
  }
}

function mergePermission(authContext, store, options) {
  const defaultPermission = createDefaultPermission(authContext, store)
  const userDefinedPermission = options.permission || {}

  return { ...defaultPermission, ...userDefinedPermission }
}

function createPermissionFactoryMap(authContext, store, options) {
  return options.permissionFactory || {}
}

/* ------------------------------------------------- */

export default function permissionModule(Launcher, options) {
  const authContext = this.vm.config.globalProperties.$auth
  const appContext = this

  const store = appContext.store
  const router = appContext.router

  const permissionFactory = createPermissionFactoryMap(authContext, store, options)

  // Define permission object
  const permission = new Permission({
    store,
    permission: mergePermission(authContext, store, options),
    permissionFactory,
  })

  // Define checkAuth
  function checkAuth(arg) {
    const permissionNames = Array.isArray(arg) ? arg : [arg]

    // make the payload which will pass to the actual checkAuth method
    const checkAuthPayload = {
      checkAuth,
      userInfo: store.getters.userInfo,
    }

    const authCheck = permissionNames.map(
      permissionName => permission.check(permissionName, checkAuthPayload),
    )

    const promise = Promise.all(authCheck)

    return promise.then(promiseResults => {
      if (promiseResults.length === 1) {
        return promiseResults[0]
      }

      return promiseResults
    }, rejectResOrError => {
      // if exception happened and not come from http module, then throw to global,
      //   (http module will add statusCode into error object)
      if (rejectResOrError instanceof Error && !rejectResOrError.statusCode) {
        setTimeout(() => {
          throw rejectResOrError
        }, 0)
      }

      // otherwise is a normaul permission rejection
      return Promise.reject(rejectResOrError)
    })
  }

  function makePermissionFun() {
    const stateFunc = {}

    forEach(permission.getList(), (permissionFn, permissionName) => {
      // give a fn alias, so user can call $auth.permission.${permission}()
      stateFunc[permissionName] = () => checkAuth(permissionName)
    })

    forEach(permission.getFactory(), (permissionFactoryFn, permissionFactoryName) => {
      // give a factory fn alias, so user can call $auth.permission.${permission}(arg1, arg2)
      stateFunc[permissionFactoryName] = (...args) => {
        const permissionName = permission.makePermissionNameUnderFactory(
          permissionFactoryName, args,
        )

        if (!permission.has(permissionName)) {
          // once create, it will emit permission "push", and define to getter
          permission.create(permissionName)
        }

        return checkAuth(permissionName)
      }
    })

    return stateFunc
  }

  /*
  function makePermissionGetter() {
    return new Proxy({}, {
      get(target, permissionName) {
        checkAuth(permissionName).then(noop, noop)
        target[permissionName] = store.getters.authPermissionState[permissionName]
        return target[permissionName]
      }
    })
  }
  */

  // Bind $auth
  authContext.checkPermission = checkAuth
  authContext.permission = makePermissionFun() // usage: this.$auth.permission.isLogin()

  /* since permission factory may create any permission in runtime
      , so it is impossible to make the getter here
      , for avoid confusing just deprecat it
      , use v-auth directive */

  // authContext.permissionState = makePermissionGetter()
  authContext.permissionState = {
    deprecated: true,
  }

  Object.defineProperty(authContext, 'userInfo', {
    get: () => store.getters.userInfo,
  })

  // Bind Router
  router.beforeEach((to, from, next) => {
    // Reset permission
    permission.reset()

    let check = []
    let fallback = ''

    to.matched.forEach(match => {
      if (match.meta?.auth?.check) {
        if (typeof match.meta.auth.check === 'string') {
          check.push(match.meta.auth.check)
          fallback = match.meta.auth.fallback || fallback
        } else {
          check = check.concat(match.meta.auth.check)
          fallback = match.meta.auth.fallback || fallback
        }
      }
    })

    if (check.length > 0) {
      const promise = checkAuth(check)

      promise.then(() => {
        next()
      }).catch(() => {
        if (typeof fallback === 'function') {
          fallback()
        } else {
          const route = typeof fallback === 'object' ? fallback : { name: fallback }
          next(route)
        }
      })
    } else {
      next()
    }
  })

  // Bind v-auth
  this.vm.directive('auth', (el, { value }) => {
    const permissionName = value

    el.style.visibility = 'hidden'

    checkAuth(permissionName).then(() => {
      el.style.visibility = ''
    }, () => {
      el.remove()
    })
  })
}
