function needImplement(funcName) {
  throw Error(`[Eaglet Emitter Exception] __proto__.${funcName} should be implemented`)
}

function argsCheck(args) {
  if (!args.versionHash) {
    throw Error(`[Eaglet Emitter Exception] args.versionHash is required`)
  }

  if (!args.endpoint) {
    throw Error(`[Eaglet Emitter Exception] args.endpoint is required`)
  }
}

export default class EmitterBase {
  constructor(args) {
    argsCheck(args)

    // versionHash  {number}  pb versionHash
    // endpoint     {string}  report endpoint
    this.versionHash = args.versionHash
    this.endpoint = args.endpoint
  }

  buffer = []

  shouldFlushBuffer() {
    return true
  }

  flush() {
    needImplement('flush')()
  }

  push(tracker) {
    this.buffer.push(tracker)

    if (this.shouldFlushBuffer(tracker)) {
      this.flush()
    }
  }
}
