import type { ITrackerName, ITrackerMap } from '../types/plugin'

export default function checkTrackerInstalled(trackerMap: ITrackerMap, trackerName: ITrackerName) {
  if (!trackerMap[trackerName]) {
    if (process.env.BUILD_ENV !== 'production') {
      console.error(`[eagletPlugin] Should install tracker which named ${trackerName}, please reference the README`) // eslint-disable-line
    }

    return false
  }

  return true
}
