export const getTargetDom = (container:Element | null, targetInfo:string):Element[] => {
  let res:Element[] = []
  if (!container) return res
  if (container.getAttribute(targetInfo)) {
    res.push(container)
  }

  if (!container.children || container.children.length === 0) {
    return res
  }

  // 层序遍历每一个子项
  let nextDomRes:Element[]
  Object.values(container.children).forEach(childNode => {
    nextDomRes = getTargetDom(childNode, targetInfo)
    if (nextDomRes.length !== 0) {
      res = res.concat(nextDomRes)
    }
  })

  return res
}
