import type { ITrackerExtendDataPayload, ITrackerContent } from '../types/plugin'

// app.eventSeqIdInSession: 自增 ID，客户端使用，保证应用内的主进程、子进程、前端等业务点位的 eventSeqIdInSession 都是按序的、连续的
// 在 Android 端通过 bridge 上报业务点位会被端侧修改，在整个 app 生命周期内自增。iOS 暂未使用
let eventSeqIdInSession = 0

// event.sdkSeqId: 自增 ID，前端使用，用于建立前端业务埋点准确性指标
let sdkSeqId = 0

export const addEventSeqIdInSession = (payload: ITrackerExtendDataPayload, builder: ITrackerContent['builder']) => {
  if (!payload || !builder) {
    return
  }
  // 业务埋点跨端串联,更新上报时序
  if (payload?.event?.value) {
    payload.event.value.seqId = Date.now()
  }
  // 业务埋点上报顺序校验，更新业务埋点上报顺序
  if (payload?.app?.value) {
    eventSeqIdInSession += 1
    payload.app.value.eventSeqIdInSession = eventSeqIdInSession
  }
  const isSurpportColdStartId = builder?.App?.prototype?.getColdStartId && builder?.App?.prototype?.setColdStartId
  const isSurpportEventSeqIdInSession = builder?.App?.prototype?.getEventSeqIdInSession && builder?.App?.prototype?.setEventSeqIdInSession
  const isSurpportSeqId = builder?.Event?.prototype?.getSeqId && builder?.Event?.prototype?.setSeqId
  if (!isSurpportColdStartId) {
    delete payload.app?.value?.coldStartId
  }
  if (!isSurpportEventSeqIdInSession) {
    delete payload.app?.value?.eventSeqIdInSession
  }
  if (!isSurpportSeqId) {
    delete payload.event?.value?.seqId
  }

  // 防御性写入 event.sdkSeqId
  const isSurpportSdkSeqId = builder?.Event?.prototype?.getSdkSeqId && builder?.Event?.prototype?.setSdkSeqId
  if (isSurpportSdkSeqId) {
    sdkSeqId += 1
    // @ts-ignore  代码逻辑走到这里一定存在 payload.event.value
    payload.event.value.sdkSeqId = sdkSeqId
  }
}
