const DEFAULT_AUTH_TOKEN_KEY = 'auth-token'

function silenceFail() { // eslint-disable-line

}

function createServicesForNonBrowser() {
  return {
    getAuthToken: silenceFail,
    setAuthToken: silenceFail,
    resetAuthToken: silenceFail,
  }
}

function createServices(storedKey = DEFAULT_AUTH_TOKEN_KEY) {
  if (!localStorage) {
    return createServicesForNonBrowser()
  }

  function getAuthToken() {
    if (localStorage.getItem(storedKey)) {
      return localStorage.getItem(storedKey)
    }
    return ''
  }

  function setAuthToken(token) {
    localStorage.setItem(storedKey, token)
  }

  function resetAuthToken() {
    localStorage.removeItem(storedKey)
  }

  return {
    getAuthToken,
    setAuthToken,
    resetAuthToken,
  }
}

function handleHttpModule(http, authServices) {
  const methodsMap = ['get', 'del', 'head', 'post', 'put', 'patch']

  methodsMap.forEach(method => {
    if (http[method]) {
      const httpFn = http[method]
      const configIdxFromArgs = httpFn.length - 1 // assume the last arguments is config

      httpFn.noAuth = function xhrNoAuth(...args) {
        const config = args[configIdxFromArgs] || {}

        args[configIdxFromArgs] = { ...config, launcherNoAuth: true }

        return httpFn.apply(http, args)
      }
    }
  })

  http.interceptors.request.use(config => {
    if (!config.launcherNoAuth && !config.noAuth) {
      const token = authServices.getAuthToken()

      config.headers.Authorization = token
    }

    return config
  })
}

export default function localStorageModule(Launcher, options, scheme) {
  const authContext = this.vm.config.globalProperties.$auth
  const authServices = authContext.services

  const localStorageServices = createServices(scheme.storedKey)

  // Bind $auth
  Object.assign(authServices, localStorageServices)

  // Bind noAuth method to http
  handleHttpModule(Launcher.http, authServices)
}
