import Launcher from '@xhs/launcher'
import { v4 as uuidv4 } from 'uuid'
import { RouteLocationNormalizedLoaded, RouteLocationMatched } from 'vue-router'

export default function getBrowserInfo(route: RouteLocationNormalizedLoaded | {
  matched?: RouteLocationMatched[]
  fullPath: string
} = {
  fullPath: '',
}) {
  if (!Launcher.isBrowser) {
    return {}
  }

  const { matched = [] } = route
  const lastMatchedRoute = matched.length > 0 ? matched.slice(-1)[0] : null
  const matchedPath = lastMatchedRoute?.path ?? ''

  return {
    browser: {
      type: 'Browser',
      value: {
        matchedPath,
        route: window.location.origin + route?.fullPath,
        userAgent: window.navigator.userAgent,
        sessionId: uuidv4(),
        fixedReferer: document.referrer,
      },
    },
  }
}

export const getBrowserInfoV2 = (route: RouteLocationNormalizedLoaded | {
  matched?: RouteLocationMatched[]
} = {}) => {
  if (!Launcher.isBrowser) {
    return {}
  }

  const { matched = [] } = route
  const lastMatchedRoute = matched.length > 0 ? matched.slice(-1)[0] : null
  const matchedPath = lastMatchedRoute?.path ?? ''

  return {
    context_matchedPath: matchedPath,
    context_route: window.location.href,
    context_userAgent: window.navigator.userAgent,
  }
}
