/* eslint-disable no-param-reassign */
import { toDict } from '../../helpers'

export function serializeParams(url = '', params = {}) {
  return Object.keys(params).reduce((acc, cur) => {
    const val = params[cur]

    if (!val) {
      return acc
    }

    const pair = `${encodeURIComponent(cur)}=${encodeURIComponent(val)}`

    if (acc.indexOf('?') === -1) {
      acc += `?${pair}`
    } else {
      acc += `&${pair}`
    }

    return acc
  }, url)
}

class HttpTracker {
  constructor() {
    this.entryHash = {}
  }

  resetEntryHash() {
    this.entryHash = {}
  }

  track(name, payload) {
    if (!name) {
      throw new Error('[apm-metrics] mark name is required')
    }

    const requestPayload = this.entryHash[name]

    if (typeof requestPayload !== 'undefined') {
      performance.mark(`${name}_end`)

      this.entryHash[name] = {
        ...requestPayload,
        ...payload,
      }
    } else {
      performance.mark(`${name}_start`)

      this.entryHash[name] = payload
    }
  }

  measure(name) {
    const meta = this.entryHash[name]

    if (!meta) {
      return {}
    }

    try { // 兼容 android instance 复用
      performance.measure(name, `${name}_start`, `${name}_end`)
    } catch (ex) {
      return {}
    }

    const [measure] = performance.getEntriesByName(name, 'measure')

    return {
      ...meta,
      measure: toDict(measure),
    }
  }
}

export function createHttpTracker() {
  return new HttpTracker()
}
