/** 判断一个多层对象内部是否包含一个key
* params: deepObject <object>,
*   deepObject: {
*     country: {
*       china:{
*         city: shanghai,
*       },
*     },
*   }
* params: keyPath <string>,
*   keyPath: 'country.franch.paris'
* return: boolean
* */

export type IDeepObject = Partial<Record<string, object | number| string>>
function objectHasKey(deepObject: IDeepObject, keyPath: string): boolean {
  if (!keyPath || !deepObject) {
    return false
  }
  const keys = keyPath.split('.')

  if (keys.length === 1) {
    if (deepObject[keys[0]] !== undefined && deepObject[keys[0]] !== '' && deepObject[keys[0]] !== null) {
      return true
    }
    return false
  }
  if ((typeof deepObject[keys[0]] === 'object' && deepObject[keys[0]] !== null) || typeof deepObject[keys[0]] === 'function') {
    const headKey = keys.shift() as string
    const newKeyPath = keys.join('.')
    return objectHasKey(deepObject[headKey] as IDeepObject, newKeyPath)
  }
  return false
}
export { objectHasKey }
